// /home/coffee/my-keymap-viewer/src/hooks/useSokuonKomojiPractice.ts
import { useState, useCallback, useMemo, useEffect, useRef } from 'react';
import {
    PracticeHookProps,
    PracticeInputInfo,
    PracticeInputResult,
    PracticeHookResult,
    sokuonKomojiData, // 更新された sokuonKomojiData を使用
    hid2GyouHRight_Kana,
    hid2GyouHLeft_Kana,
    hid2DanHRight_Kana,
    hid2DanHLeft_Kana,
    hid2GyouVRight_Kana,
    hid2GyouVLeft_Kana,
    hid2DanVRight_Kana,
    hid2DanVLeft_Kana,
    CharInfoSokuonKomoji, // これは usePracticeCommons から来る想定
    allSokuonKomojiCharInfos, // ランダムモード用
    PracticeHighlightResult,
} from './usePracticeCommons';

type SokuonKomojiStage = 'tsuInput' | 'gyouInput' | 'middleInput' | 'danInput';

export default function useSokuonKomojiPractice({ gIdx, dIdx, isActive, side, kb, isRandomMode }: PracticeHookProps): PracticeHookResult {
    const [stage, setStage] = useState<SokuonKomojiStage>('gyouInput');
    const [currentErrorHighlightLayer, setCurrentErrorHighlightLayer] = useState<number | null>(null);

    const { hid2Gyou, hid2Dan } = useMemo(() => {
        if (kb === 'tw-20v') {
            return side === 'left'
                ? { hid2Gyou: hid2GyouVLeft_Kana, hid2Dan: hid2DanVLeft_Kana }
                : { hid2Gyou: hid2GyouVRight_Kana, hid2Dan: hid2DanVRight_Kana };
        } else { // TW-20H
            return side === 'left'
                ? { hid2Gyou: hid2GyouHLeft_Kana, hid2Dan: hid2DanHLeft_Kana }
                : { hid2Gyou: hid2GyouHRight_Kana, hid2Dan: hid2DanHRight_Kana };
        }
    }, [side, kb]);

    const tsuKeyCode = useMemo(() => {
        const gyouMap = kb === 'tw-20v'
            ? (side === 'left' ? hid2GyouVLeft_Kana : hid2GyouVRight_Kana)
            : (side === 'left' ? hid2GyouHLeft_Kana : hid2GyouHRight_Kana);
        const entry = Object.entries(gyouMap).find(([_, name]) => name === 'っ');
        return entry ? parseInt(entry[0]) : null;
    }, [kb, side]);

    const dakuonKeyCode = useMemo(() => {
        const gyouMap = kb === 'tw-20v'
            ? (side === 'left' ? hid2GyouVLeft_Kana : hid2GyouVRight_Kana)
            : (side === 'left' ? hid2GyouHLeft_Kana : hid2GyouHRight_Kana);
        const entry = Object.entries(gyouMap).find(([_, name]) => name === '” ° 小');
        return entry ? parseInt(entry[0]) : null;
    }, [kb, side]);


    const [randomTarget, setRandomTarget] = useState<CharInfoSokuonKomoji | null>(null);
    const prevGIdxRef = useRef(gIdx);
    const prevDIdxRef = useRef(dIdx);
    const isInitialMount = useRef(true);
    const prevIsActiveRef = useRef(isActive);
    const prevIsRandomModeRef = useRef(isRandomMode);

    const currentSet = useMemo(() => {
        if (!isRandomMode && isActive && gIdx >= 0 && gIdx < sokuonKomojiData.length) {
            return sokuonKomojiData[gIdx];
        }
        return null;
    }, [gIdx, isActive, isRandomMode]);

    const currentInputDef = useMemo((): CharInfoSokuonKomoji | null => {
        if (isRandomMode) return randomTarget;
        if (!currentSet || dIdx < 0 || dIdx >= currentSet.inputs.length) return null;

        const inputData = currentSet.inputs[dIdx];
        if (!inputData) return null;

        return {
            type: 'sokuonKomoji',
            char: inputData.char,
            isTsu: inputData.isTsu,
            gyouKey: inputData.gyouKey,
            middleKey: inputData.middleKey,
            danKey: inputData.danKey,
        };
    }, [currentSet, dIdx, isRandomMode, randomTarget]);

    useEffect(() => {
        // この useEffect は、練習ターゲット (currentInputDef) が変更されたとき、
        // または練習がアクティブになったときに、ステージを適切に初期化する。
        // currentInputDef が変更された (新しい文字がターゲットになった) 場合、
        // ステージをその文字の初期入力ステージに設定する。
        if (isActive && currentInputDef) {
            const newInitialStage = currentInputDef.isTsu ? 'tsuInput' : 'gyouInput';
            setStage(newInitialStage);
        }
        // isActiveがfalseの場合やcurrentInputDefがnullの場合は、他のuseEffectやreset関数が処理する。
    }, [isActive, currentInputDef]);

    const selectNextRandomTarget = useCallback(() => {
        if (allSokuonKomojiCharInfos.length > 0) {
            const randomIndex = Math.floor(Math.random() * allSokuonKomojiCharInfos.length);
            const nextTarget = allSokuonKomojiCharInfos[randomIndex];
            setRandomTarget(nextTarget);
        } else {
            setRandomTarget(null);
        }
    }, [setRandomTarget]);

    const reset = useCallback(() => {
        setRandomTarget(null);
        prevGIdxRef.current = -1;
        prevDIdxRef.current = -1;
        isInitialMount.current = true;
        prevIsRandomModeRef.current = false;
        setCurrentErrorHighlightLayer(null);
        // ステージは currentInputDef の useEffect で初期化される
    }, [setRandomTarget]);

    useEffect(() => {
        if (!isActive && prevIsActiveRef.current) {
            reset();
        }

        if (isActive) {
            if (isActive && !prevIsActiveRef.current) {
                isInitialMount.current = true;
            }

            const indicesChanged = gIdx !== prevGIdxRef.current || dIdx !== prevDIdxRef.current;
            const randomModeChangedToTrue = isRandomMode && !prevIsRandomModeRef.current;
            const randomModeChangedToFalse = !isRandomMode && prevIsRandomModeRef.current;

            if (randomModeChangedToFalse || (!isRandomMode && (isInitialMount.current || indicesChanged))) {
                setCurrentErrorHighlightLayer(null);
                setRandomTarget(null); // 通常モードではランダムターゲットをクリア
                // ステージは currentInputDef の useEffect で設定される
                prevGIdxRef.current = gIdx;
                prevDIdxRef.current = dIdx;
                isInitialMount.current = false;
            } else if (isRandomMode && (randomModeChangedToTrue || isInitialMount.current || !randomTarget)) {
                 selectNextRandomTarget();
                 setCurrentErrorHighlightLayer(null);
                 isInitialMount.current = false;
                 prevGIdxRef.current = gIdx;
                 prevDIdxRef.current = dIdx;
            }
        }
        prevIsActiveRef.current = isActive;
        prevIsRandomModeRef.current = isRandomMode;
    }, [isActive, isRandomMode, gIdx, dIdx, randomTarget, reset, selectNextRandomTarget, setCurrentErrorHighlightLayer]);


    const targetChar = useMemo(() => {
        return currentInputDef?.char ?? null;
    }, [currentInputDef]);

    const isMiddleKeyRequired = useMemo(() => {
        return !!currentInputDef?.middleKey;
    }, [currentInputDef]);

    const headingChars = useMemo(() => {
        if (!isActive) return [];
        if (isRandomMode) {
            return targetChar ? [targetChar] : [];
        } else {
            return currentSet?.chars ?? [];
        }
    }, [isActive, isRandomMode, targetChar, currentSet]);

    const expectedGyouKey = useMemo(() => {
        return currentInputDef?.gyouKey ?? null;
    }, [currentInputDef]);

    const expectedDanKey = useMemo(() => {
        return currentInputDef?.danKey ?? null;
    }, [currentInputDef]);

    const handleInput = useCallback((input: PracticeInputInfo): PracticeInputResult => {
        if (!isActive || !currentInputDef) {
            setCurrentErrorHighlightLayer(null);
            return { isExpected: false, shouldGoToNext: undefined, errorLayer: null };
        }
        if (currentInputDef.isTsu && tsuKeyCode === null) {
            console.error("Sokuon/Komoji Input Error: tsuKeyCode is null for 'っ'.");
            setCurrentErrorHighlightLayer(null);
            return { isExpected: false, shouldGoToNext: undefined, errorLayer: null };
        }
        if (isMiddleKeyRequired && dakuonKeyCode === null) {
            console.error("Sokuon/Komoji Input Error: dakuonKeyCode is null when middleKey is required.");
            setCurrentErrorHighlightLayer(null);
            return { isExpected: false, shouldGoToNext: undefined, errorLayer: null };
        }

        if (input.type !== 'release') {
            // エラー状態は変更しない
            return { isExpected: false, shouldGoToNext: undefined, errorLayer: currentErrorHighlightLayer };
        }

        let isExpected = false;
        let shouldGoToNext_final: boolean | undefined = undefined;
        let nextHookStage: SokuonKomojiStage = stage;
        let errorLayer: number | null = null;

        if (stage === 'tsuInput') {
            // input.pressCode は 0-based, tsuKeyCode は 1-based
            if ((input.pressCode + 1) === tsuKeyCode) {
                isExpected = true;
                setCurrentErrorHighlightLayer(null);
                if (isRandomMode) {
                    selectNextRandomTarget();
                    shouldGoToNext_final = false;
                } else {
                    const currentGroupData = sokuonKomojiData[gIdx];
                    const isLastInGroup = currentGroupData ? dIdx === currentGroupData.inputs.length - 1 : false;
                    shouldGoToNext_final = isLastInGroup;
                }
                // 正解時はステージ変更なし (次の文字がまた「っ」の可能性もあるため、useEffectで再評価)
            } else {
                isExpected = false;
                setCurrentErrorHighlightLayer(2); // 「っ」キーはレイヤー2
                errorLayer = 2;
                // 不正解時もステージ変更なし (再度「っ」入力を待つ)
            }
        } else if (stage === 'gyouInput') {
            if (!expectedGyouKey) {
                setCurrentErrorHighlightLayer(null);
                return { isExpected: false, shouldGoToNext: undefined, errorLayer: null };
            }
            const expectedGyouKeyCodes = Object.entries(hid2Gyou)
                .filter(([_, name]) => name === expectedGyouKey)
                .map(([codeStr, _]) => parseInt(codeStr));
            if (expectedGyouKeyCodes.includes(input.pressCode + 1)) { // 0-based to 1-based
                isExpected = true;
                setCurrentErrorHighlightLayer(null);
                nextHookStage = isMiddleKeyRequired ? 'middleInput' : 'danInput';
            } else {
                isExpected = false;
                setCurrentErrorHighlightLayer(2); // 行キーはレイヤー2
                errorLayer = 2;
                // 不正解時はステージ変更なし (再度行入力を待つ)
            }
        } else if (stage === 'middleInput') {
            if ((input.pressCode + 1) === dakuonKeyCode) { // 0-based to 1-based
                isExpected = true;
                setCurrentErrorHighlightLayer(null);
                nextHookStage = 'danInput';
            } else {
                isExpected = false;
                setCurrentErrorHighlightLayer(2); // 濁音キーはレイヤー2
                errorLayer = 2;
                nextHookStage = 'gyouInput'; // 不正解なら行入力からやり直し
            }
        } else if (stage === 'danInput') {
            if (!expectedDanKey) {
                setCurrentErrorHighlightLayer(null);
                return { isExpected: false, shouldGoToNext: undefined, errorLayer: null };
            }
            const expectedDanKeyCodes = Object.entries(hid2Dan)
                .filter(([_, name]) => name === expectedDanKey)
                .map(([codeStr, _]) => parseInt(codeStr));
            if (expectedDanKeyCodes.includes(input.pressCode + 1)) { // 0-based to 1-based
                isExpected = true;
                setCurrentErrorHighlightLayer(null);
                if (isRandomMode) {
                    selectNextRandomTarget();
                    shouldGoToNext_final = false;
                } else {
                    const currentGroupData = sokuonKomojiData[gIdx];
                    const isLastInGroup = currentGroupData ? dIdx === currentGroupData.inputs.length - 1 : false;
                    shouldGoToNext_final = isLastInGroup;
                }
                // 正解時は次の文字のステージを useEffect で設定するため、ここでは nextHookStage を変更しない
                // (または、明示的に 'gyouInput' or 'tsuInput' に設定しても良い)
                // nextHookStage = currentInputDef?.isTsu ? 'tsuInput' : 'gyouInput'; // 次の文字の初期ステージ
            } else {
                isExpected = false;
                setCurrentErrorHighlightLayer(3); // 段キーはレイヤー3
                errorLayer = 3;
                nextHookStage = 'gyouInput'; // 不正解なら行入力からやり直し
            }
        }

        // ステージ遷移は、正解した場合、または不正解で明示的にステージをリセットする場合に行う
        if ((isExpected && nextHookStage !== stage) || (!isExpected && nextHookStage !== stage && (stage === 'middleInput' || stage === 'danInput'))) {
            setStage(nextHookStage);
        } else if (!isExpected && stage !== 'tsuInput' && stage !== 'gyouInput') {
            // 上記の条件でカバーされない不正解時 (例: tsuInput や gyouInput での不正解でステージ変更がない場合)
            // middleInput や danInput で不正解の場合は nextHookStage が 'gyouInput' になるため、上の条件で処理される
            setStage('gyouInput');
        }

        return { isExpected, shouldGoToNext: shouldGoToNext_final, errorLayer };
    }, [
        isActive, stage, expectedGyouKey, expectedDanKey, currentInputDef,
        tsuKeyCode, dakuonKeyCode, hid2Gyou, hid2Dan, isRandomMode, selectNextRandomTarget, setCurrentErrorHighlightLayer,
        setStage, isMiddleKeyRequired, gIdx, dIdx
    ]);

    const getHighlightClassName = useCallback((key: string, layoutIndex: number): PracticeHighlightResult => {
        const noHighlight: PracticeHighlightResult = { className: null, overrideKey: null };
        if (!isActive || !currentInputDef) {
            return noHighlight;
        }

        // エラーハイライトがアクティブな場合、通常の期待キーハイライトは表示しない
        if (currentErrorHighlightLayer === layoutIndex) {
            return noHighlight;
        }

        // 常に現在の stage ステートを直接使用する
        const currentStageForHighlight = stage;
        let expectedKeyName: string | null = null;
        let targetLayoutIndex: number | null = null;

        if (currentStageForHighlight === 'tsuInput') {
            expectedKeyName = 'っ';
            targetLayoutIndex = 2;
        } else if (currentStageForHighlight === 'gyouInput') {
            expectedKeyName = expectedGyouKey;
            targetLayoutIndex = 2;
        } else if (currentStageForHighlight === 'middleInput') {
            expectedKeyName = '” ° 小';
            targetLayoutIndex = 2;
        } else if (currentStageForHighlight === 'danInput') {
            expectedKeyName = expectedDanKey;
            targetLayoutIndex = 3;
        }

        if (expectedKeyName !== null && layoutIndex === targetLayoutIndex && key === expectedKeyName) {
            return { className: 'bg-blue-100', overrideKey: null };
        }
        return noHighlight;
    }, [isActive, stage, expectedGyouKey, expectedDanKey, currentInputDef, currentErrorHighlightLayer]);

    const isInvalidInputTarget = useCallback((pressCode: number, layoutIndex: number, keyIndex: number): boolean => {
        if (!isActive) return false;

        if (currentErrorHighlightLayer === layoutIndex && keyIndex === pressCode) {
            return true;
        }
        return false;
    }, [isActive, currentErrorHighlightLayer]);

    const clearErrorState = useCallback(() => {
        setCurrentErrorHighlightLayer(null);
    }, [setCurrentErrorHighlightLayer]);

    return {
        headingChars,
        handleInput,
        getHighlightClassName,
        reset,
        isInvalidInputTarget,
        targetChar: targetChar ?? undefined,
        errorHighlightLayer: currentErrorHighlightLayer,
        clearErrorState,
        targetLayerIndices: useMemo(() => {
            let determinedTargetLayerIndices = [2, 3]; // Default
            if (currentErrorHighlightLayer !== null) {
                const layersToShow = new Set([2, 3]);
                layersToShow.add(currentErrorHighlightLayer);
                determinedTargetLayerIndices = Array.from(layersToShow);
            }
            return determinedTargetLayerIndices;
        }, [currentErrorHighlightLayer]),
        stage, // Add stage to the return object
    };
}
