import React, { useEffect, useMemo, useCallback } from 'react';
import { PracticeMode, PracticeStatus, PracticeHookResult, CharInfoGairaigo, HeadingCharDisplayInfo } from '../hooks/usePracticeCommons';
// import { YouonKakuchoHeadingCharInfo } from '../hooks/useYouonKakuchoPractice'; // No longer needed as HeadingCharDisplayInfo is used
import { gairaigoPracticeData, alphaSymbolPractice1Data } from '../data/keymapData'; // gairaigoPracticeData, alphaSymbolPractice1Data をインポート

interface PracticeHeadingProps {
  practice: PracticeMode;
  headingChars: string[] | HeadingCharDisplayInfo[];
  gIdx: number;
  dIdx: number;
  isRandomMode?: boolean;
  isFinished?: boolean;
  typedEndIndex?: number;
  status?: PracticeStatus;
  currentTargetCharForHighlight?: string | null;
  activePractice?: PracticeHookResult | null;
  typedChar?: string | null;
}
// Define an interface for the structure of data in alphaSymbolPractice1Data
// This should match the structure of the objects in your alphaSymbolPractice1Data array
interface AlphaSymbolPracticeGroup {
  // Add other properties if they exist, e.g., name: string;
  chars: string[];
}

// Helper function to shuffle an array (moved outside the component for clarity)
const shuffleArray = <T extends any>(array: T[]): T[] => {
  const newArray = [...array];
  for (let i = newArray.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [newArray[i], newArray[j]] = [newArray[j], newArray[i]];
  }
  return newArray;
};

const PracticeHeading: React.FC<PracticeHeadingProps> = ({
  practice,
  headingChars: propsHeadingChars, // Renamed to avoid conflict in useMemo
  gIdx,
  dIdx,
  isRandomMode = false,
  isFinished = false,
  typedEndIndex = 0,
  status = 'idle',
  currentTargetCharForHighlight,
  activePractice,
  typedChar,
}) => {
  console.log(`[PracticeHeading RENDER] Received propsHeadingChars:`, propsHeadingChars, `practice: ${practice}, isRandomMode: ${isRandomMode}`);
  const isGairaigo = practice === '拗音拡張２';

  const currentSentence = useMemo(() => {
    if ((practice === '短文入力３分間トレーニング' || practice === '英数短文入力３分間トレーニング') && propsHeadingChars.length > 0 && typeof propsHeadingChars[0] === 'string') {
      return propsHeadingChars[0];
    }
    return null;
  }, [practice, propsHeadingChars]);

  const currentIndex = useMemo(() => {
    if (practice === '短文入力３分間トレーニング' || practice === '英数短文入力３分間トレーニング') {
      return typedEndIndex ?? 0;
    }
    return 0;
  }, [practice, typedEndIndex]);

  const currentTargetForComparison = useMemo(() => {
    if (isRandomMode && (practice === '拗音の基本練習' || practice === '拗濁音の練習' || practice === '拗半濁音の練習')) {
      if (activePractice?.targetChar && typeof activePractice.targetChar === 'string' && activePractice.targetChar.length === 2) {
        return activePractice.targetChar;
      }
      if (activePractice?.currentTarget && typeof activePractice.currentTarget === 'object' && activePractice.currentTarget !== null && 'char' in activePractice.currentTarget) {
        const ct = activePractice.currentTarget as { char?: string };
        if (typeof ct.char === 'string' && ct.char.length === 2) return ct.char;
      }
      return null;
    }

    if (practice === '英字入力１分間トレーニング' || practice === 'かな入力１分間トレーニング' || practice === '記号入力１分間トレーニング') {
        if (activePractice?.headingChars && activePractice.headingChars.length > 0 && typeof activePractice.headingChars[0] === 'string') {
            return activePractice.headingChars[0];
        }
    }
    if ((practice === '短文入力３分間トレーニング' || practice === '英数短文入力３分間トレーニング') && currentSentence) {
        return currentSentence[currentIndex];
    }
    if (practice === '拗音拡張２' && currentTargetCharForHighlight) {
        return currentTargetCharForHighlight;
    }
    let targetCharForComparison: string | undefined | null = undefined;
    if (activePractice?.currentTarget) {
        if (typeof activePractice.currentTarget === 'string') {
            targetCharForComparison = activePractice.currentTarget;
        } else if (typeof activePractice.currentTarget === 'object' && activePractice.currentTarget !== null && 'char' in activePractice.currentTarget) {
            const ct = activePractice.currentTarget as { char?: string };
            if (typeof ct.char === 'string') {
                targetCharForComparison = ct.char;
            }
        }
    }
    if (targetCharForComparison === undefined && activePractice?.targetChar) {
        if (typeof activePractice.targetChar === 'string') {
            targetCharForComparison = activePractice.targetChar;
        } else if (typeof activePractice.targetChar === 'object' && activePractice.targetChar !== null && 'char' in activePractice.targetChar) {
            const tc = activePractice.targetChar as { char?: string };
            if (typeof tc.char === 'string') {
                targetCharForComparison = tc.char;
            }
        }
    }
    if (targetCharForComparison === undefined || targetCharForComparison === null) {
        targetCharForComparison = typedChar;
    }
    return targetCharForComparison;
  }, [practice, activePractice, typedChar, currentSentence, currentIndex, currentTargetCharForHighlight, isRandomMode]);

  const capitalizedCharIndices = useMemo(() => {
    return activePractice?.capitalizedCharIndices ?? new Set<number>();
  }, [activePractice?.capitalizedCharIndices]);

  const currentGairaigoPracticeTargets = useMemo(() => {
    if (practice === '拗音拡張２' && gairaigoPracticeData[gIdx]) {
      return new Set(gairaigoPracticeData[gIdx].targets.map(t => t.char));
    }
    return new Set<string>();
  }, [practice, gIdx]);

  // Calculate the full set of unique symbols for 英字記号の練習１ (used in random mode)
  const allAlphaSymbol1Chars = useMemo(() => {
      const chars = new Set<string>();
      // Ensure alphaSymbolPractice1Data is correctly typed or cast if necessary,
      // and that it's properly imported and available.
      // Assuming alphaSymbolPractice1Data is an array of AlphaSymbolPracticeGroup
      (alphaSymbolPractice1Data as AlphaSymbolPracticeGroup[]).forEach((group: AlphaSymbolPracticeGroup) => group.chars.forEach((char: string) => chars.add(char)));
      return Array.from(chars);
  }, []); // Dependency array is empty as the data is static


  const displayItems = useMemo((): (string | HeadingCharDisplayInfo)[] => {
    console.log(`[PracticeHeading displayItems CALC START] practice: ${practice}, isRandomMode: ${isRandomMode}, propsHeadingChars:`, propsHeadingChars);
    if (isFinished && practice !== '短文入力３分間トレーニング' && practice !== '英数短文入力３分間トレーニング') {
      return ['終了！'];
    }
    const isHeadingCharDisplayInfo = (item: any): item is HeadingCharDisplayInfo => {
      return typeof item === 'object' && item !== null && 'char' in item && typeof (item as any).char === 'string';
    };
    const isHeadingCharDisplayInfoArray = (arr: any[]): arr is HeadingCharDisplayInfo[] => {
      return Array.isArray(arr) && arr.length > 0 && isHeadingCharDisplayInfo(arr[0]);
    };

    let result: (string | HeadingCharDisplayInfo)[];
    // Priority 1: Specific random mode handling for certain practices
    if (isRandomMode) {
      if (practice === '拗音拡張１') {
        if (activePractice?.targetChar && typeof activePractice.targetChar === 'string') {
          return [activePractice.targetChar]; // Display only the target Youon as a single string item
        }
        console.warn(`[PracticeHeading] displayItems: Missing/invalid targetChar for 拗音拡張１ (Random).`);
        return ['?']; // Fallback
      }
      // 「拗音拡張２」ランダムモード (Fallback if not caught by Priority 1)
      if (practice === '拗音拡張２') {
        if (activePractice?.targetChar && typeof activePractice.targetChar === 'string') {
          const targetWord = activePractice.targetChar;
          const currentStage = activePractice.stage;
          return targetWord.split('').map((c, idx) => {
            let isTarget = false;
            if (currentStage === 'key1' && idx === 0) isTarget = true;
            else if (currentStage === 'key2' && idx === 1) isTarget = true;
            else if (currentStage === 'key2' && targetWord.length === 1 && idx === 0) isTarget = true;
            else if (currentStage === 'key3' && idx === 2) isTarget = true;
            else if (currentStage === 'key3' && targetWord.length === 1 && idx === 0) isTarget = true;
            else if (currentStage === 'key3' && targetWord.length === 2 && idx === 1) isTarget = true;
            return { char: c, isPracticeTarget: isTarget };
          });
        }
        console.warn(`[PracticeHeading] displayItems: Missing/invalid targetChar for 拗音拡張２ (Random).`);
        const fallbackWord = (isHeadingCharDisplayInfoArray(propsHeadingChars) && propsHeadingChars.length > 0) ? propsHeadingChars[0].char : (Array.isArray(propsHeadingChars) && propsHeadingChars.length > 0 && typeof propsHeadingChars[0] === 'string' ? propsHeadingChars[0] : '');
        return fallbackWord.split('').map(c => ({ char: c, isPracticeTarget: false }));
      }

      if (practice === '拗半濁音の練習') {
        if (activePractice?.targetChar && typeof activePractice.targetChar === 'string') {
          return [activePractice.targetChar];
        }
        console.warn(`[PracticeHeading] displayItems: Missing/invalid targetChar for 拗半濁音の練習 (Random).`);
        return (isHeadingCharDisplayInfoArray(propsHeadingChars) && propsHeadingChars.length > 0) ? [propsHeadingChars[0].char] : (Array.isArray(propsHeadingChars) && propsHeadingChars.length > 0 && typeof propsHeadingChars[0] === 'string' ? [propsHeadingChars[0]] : ['?']);
      }

      if (practice === '拗音の基本練習' || practice === '拗濁音の練習') {
        if (activePractice?.targetChar && typeof activePractice.targetChar === 'string' && activePractice.targetChar.length === 2) {
          return activePractice.targetChar.split('');
        }
        console.warn(`[PracticeHeading] displayItems: Missing/invalid targetChar for ${practice} (Random).`);
        const fallbackChar = (isHeadingCharDisplayInfoArray(propsHeadingChars) && propsHeadingChars.length > 0) ? propsHeadingChars[0].char : (Array.isArray(propsHeadingChars) && propsHeadingChars.length > 0 && typeof propsHeadingChars[0] === 'string' ? propsHeadingChars[0] : '??');
        return fallbackChar.length === 2 ? fallbackChar.split('') : [fallbackChar[0] || '?', fallbackChar[1] || '?'];
      }

      if (practice === '英字の基本練習') {
        if (activePractice?.headingChars && activePractice.headingChars.length > 0 && typeof activePractice.headingChars[0] === 'string') {
          return activePractice.headingChars; // フックから渡された1文字の配列をそのまま使用
        }
        console.warn(`[PracticeHeading] displayItems: Missing/invalid headingChars for 英字の基本練習 (Random).`);
        return ['?']; // フォールバック
      }

      if (practice === '英字記号の練習１') {
        // グローバルランダムONの時、ターゲットの1文字だけを表示
        if (activePractice?.targetChar && typeof activePractice.targetChar === 'string') {
          return [{ char: activePractice.targetChar, isPracticeTarget: true }];
        }
        // Fallback if targetChar is not available
        console.warn(`[PracticeHeading] displayItems: Missing targetChar for 英字記号の練習１ (Random ON). Displaying fallback.`);
        return [{ char: '?', isPracticeTarget: true }];
      }
      if (
        (practice === 'Caps Lock操作' || practice === '短文入力３分間トレーニング' || practice === '英数短文入力３分間トレーニング') &&
        Array.isArray(propsHeadingChars) && propsHeadingChars.length > 0 && typeof propsHeadingChars[0] === 'string'
      ) {
        return propsHeadingChars[0].split('');
      }
      result = propsHeadingChars; // Default for other random modes
      console.log(`[PracticeHeading displayItems CALC] Random mode, default. Result:`, result);
      return result;
    }
    
    // Priority 2: If activePractice.headingChars is already HeadingCharDisplayInfo[] (typically for normal mode)
    if ((practice === '拗音拡張１' || practice === '拗音拡張２' || practice === '英字記号の練習１') && activePractice?.headingChars && isHeadingCharDisplayInfoArray(activePractice.headingChars)) {
      return activePractice.headingChars;
    }
    
    // Priority 3: Normal mode processing
    // For 拗音拡張１, if not caught by Priority 1 or 2 (e.g. propsHeadingChars was string[] from older data)
    if (practice === '拗音拡張１' && Array.isArray(propsHeadingChars) && propsHeadingChars.length > 0 && typeof propsHeadingChars[0] === 'string') {
        result = propsHeadingChars.flatMap(group => group.split(''));
        console.log(`[PracticeHeading displayItems CALC] 拗音拡張１ normal. Result:`, result);
        return result;
    }

    let stringArray: string[];
    if (isHeadingCharDisplayInfoArray(propsHeadingChars)) {
        stringArray = propsHeadingChars.map(info => info.char);
    } else {
        // Ensure it's an array of strings, even if it's empty or contains non-strings (though TS should prevent non-strings)
        stringArray = Array.isArray(propsHeadingChars) ? propsHeadingChars.filter(item => typeof item === 'string') as string[] : [];
    }

    console.log(`[PracticeHeading displayItems CALC] stringArray for switch:`, stringArray);
    switch (practice) {
      case '短文入力３分間トレーニング':
      case '英数短文入力３分間トレーニング':
        return stringArray.length > 0 && typeof stringArray[0] === 'string' ? stringArray[0].split('') : [];
      case '拗音の基本練習':
      case '拗濁音の練習':
      case '拗半濁音の練習':
      // case '拗音拡張１': // Already handled
        return stringArray.flatMap(group => typeof group === 'string' ? group.split('') : []);
      case '清音の基本練習':
      // case '拗音拡張２': // Already handled by Priority 1 or random mode logic
      case '濁音の基本練習':
      case '半濁音の基本練習':
      case '小文字の基本練習':
      case '記号の基本練習１':
      case '記号の基本練習２':
      case '記号の基本練習３':
      case '数字の基本練習':
      case '英字記号の練習１': // Normal mode (global random OFF)
      case '記号の基本練習３_英数': // Add this case
      case '英字記号の練習２':
      case '英字の基本練習':
      case '大文字変換操作':
        return stringArray;
      case '拗音拡張２': // Fallback for normal mode if not HeadingCharDisplayInfo[]
        result = stringArray.map((char, idx) => ({
            char,
            isPracticeTarget: idx === dIdx // Simplified: assumes dIdx points to the target char in the list
        }));
        console.log(`[PracticeHeading displayItems CALC] 拗音拡張２ fallback. Result:`, result);
        return result;
      case 'Caps Lock操作':
        if (stringArray && stringArray.length > 0 && typeof stringArray[0] === 'string') {
          result = stringArray[0].split('');
          console.log(`[PracticeHeading displayItems CALC] Caps Lock操作. Result:`, result);
          return result;
        }
        result = [];
        console.log(`[PracticeHeading displayItems CALC] Caps Lock操作 (empty). Result:`, result);
        return result;
      default:
        if (stringArray && stringArray.length > 0 && typeof stringArray[0] === 'string') {
          // For Kigo3 and other similar practices, stringArray should be the full array.
          console.log(`[PracticeHeading displayItems CALC] Default. stringArray:`, stringArray);
          return stringArray;
        }
        result = [];
        console.log(`[PracticeHeading displayItems CALC] Default (empty). Result:`, result);
        return result;
    }
  }, [practice, propsHeadingChars, gIdx, dIdx, isFinished, typedEndIndex, activePractice, isRandomMode, allAlphaSymbol1Chars]);

  useEffect(() => {
    if (practice === '拗音拡張２') {
        // console.log(`[PracticeHeading] displayItems for 拗音拡張２:`, displayItems);
        // console.log(`[PracticeHeading] activePractice.headingChars for 拗音拡張２:`, activePractice?.headingChars);
    }
  }, [displayItems, practice, activePractice?.headingChars]);

  const getHighlightClass = useCallback((char: string, index: number, localCurrentTargetForComparison: string | null | undefined): string => {
    const currentPracticeStatus = activePractice?.status ?? status;
    // console.log(`[PracticeHeading getHighlightClass] char: ${char}, index: ${index}, target: ${localCurrentTargetForComparison}, status: ${currentPracticeStatus}, practice: ${practice}`);
    
    if (currentPracticeStatus === 'countdown' || isFinished) {
      return '';
    }

    // New rule: For "かな入力１分間トレーニング", always highlight.
    // The displayItems for this mode is expected to be the single target character.
    if (practice === 'かな入力１分間トレーニング') {
      return 'text-blue-500 font-bold';
    }

    if (practice === '拗半濁音の練習' && isRandomMode) {
      if (currentPracticeStatus !== 'finished' && localCurrentTargetForComparison && char === localCurrentTargetForComparison) {
        return 'text-blue-500 font-bold';
      }
      return 'text-gray-700 dark:text-gray-300';
    }

    if (isRandomMode && (practice === '拗音の基本練習' || practice === '拗濁音の練習')) {
        if (currentPracticeStatus !== 'finished' && localCurrentTargetForComparison && localCurrentTargetForComparison.length === 2) {
            const firstCharOfTarget = localCurrentTargetForComparison[0];
            const secondCharOfTarget = localCurrentTargetForComparison[1];
            if (index === 0 && char === firstCharOfTarget) return `text-blue-500 font-bold`;
            if (index === 1 && char === secondCharOfTarget) return `text-blue-500 font-bold`;
        }
        return 'text-gray-700 dark:text-gray-300';
    }

    // For "記号入力１分間トレーニング", always highlight.
    if (practice === '記号入力１分間トレーニング') {
        return 'text-blue-500 font-bold';
    }
    if (practice === '英字入力１分間トレーニング') { // Keep existing logic for 英字入力１分間トレーニング
        return 'text-blue-500 font-bold';
    }
    // The specific conditions for 'かな入力１分間トレーニング' that previously prevented highlighting (e.g., for 'っ', seion, dakuon)
    // are now superseded by the general rule above for this practice mode.
    if (practice === '英字の基本練習') {
      if (isRandomMode) {
        // ランダムモードでは displayItems は1要素の配列 ["R"] などになる。index は常に 0。
        // char はその1文字。localCurrentTargetForComparison もその1文字のはず。
        if (char === localCurrentTargetForComparison && currentPracticeStatus !== 'finished') {
          return 'text-blue-500 font-bold'; // 常にハイライト
        }
      } else {
        const currentTargetIndex = dIdx; // 通常モードでは dIdx を使用
        if (index === currentTargetIndex && currentPracticeStatus !== 'finished') {
          return 'text-blue-400 font-bold';
        }
      }
      return 'text-gray-700 dark:text-gray-300'; // 上記以外はグレー
    } else if (practice === '英字記号の練習１') {
        // displayItems は HeadingCharDisplayInfo の配列になっているはず (Random ON/OFF どちらでも)
        const currentDisplayItem = displayItems[index] as HeadingCharDisplayInfo;
        if (currentDisplayItem?.isPracticeTarget && currentPracticeStatus !== 'finished') {
            return 'text-blue-500 font-bold'; // Highlight the target in the list
        }
        return 'text-gray-700 dark:text-gray-300';
    } else if (practice === 'Caps Lock操作') {
      const targetCharFromHook = typeof activePractice?.targetChar === 'string' ? activePractice.targetChar : 
                                 (typeof activePractice?.targetChar === 'object' && activePractice.targetChar && 'char' in activePractice.targetChar ? (activePractice.targetChar as {char?: string}).char : '');
      if (index === dIdx && targetCharFromHook && char.toLowerCase() === targetCharFromHook.toLowerCase() && currentPracticeStatus !== 'finished') {
        return 'text-blue-400 font-bold';
      } else {
        return 'text-gray-700 dark:text-gray-300';
      }
    } else if (practice === '大文字変換操作') {
      if (currentPracticeStatus === 'incorrect' && index === dIdx) { /* Handled in JSX */ }
      else if (index === dIdx && currentPracticeStatus !== 'finished') return 'text-gray-700 dark:text-gray-300';
      else return 'text-gray-700 dark:text-gray-300';
    }
    
    if (practice === '拗音拡張１') {
        if (isRandomMode) {
            // displayItems is [targetYouonString], char is targetYouonString, index is 0
            if (char === localCurrentTargetForComparison && currentPracticeStatus !== 'finished') {
                return 'text-blue-500 font-bold';
            }
            return 'text-gray-700 dark:text-gray-300'; 
        } else { // Normal mode for 拗音拡張１
            const currentDisplayItem = displayItems[index] as HeadingCharDisplayInfo; // HeadingCharDisplayInfo now includes isValidInGroup
            if (currentDisplayItem?.isPracticeTarget) {
                return 'text-blue-500 font-bold';
            } else if (currentDisplayItem?.isValidInGroup) { 
                return ''; // Valid in group but not current target, default color
            }
            return 'text-gray-400'; // Not a valid target in this group, gray out
        }
    } 
    
    if (isGairaigo) { 
        const currentDisplayItem = displayItems[index] as HeadingCharDisplayInfo; // Type assertion
        if (currentDisplayItem?.isPracticeTarget) {
            return 'text-blue-500 font-bold';
        }
        // If NOT the currently active target (isPracticeTarget is false)
        if (!isRandomMode) {
            const currentGroupData = gairaigoPracticeData[gIdx];
            if (currentGroupData?.targets?.some(target => target.char === currentDisplayItem.char)) {
                return ''; 
            }
            return 'text-gray-400'; 
        } else { 
            return ''; 
        }
    }

    if (practice === '短文入力３分間トレーニング' || practice === '英数短文入力３分間トレーニング') {
      if (typedEndIndex === undefined || typedEndIndex === null) return 'text-gray-700 dark:text-gray-300';
      if (activePractice && 'errorCharIndex' in activePractice && activePractice.errorCharIndex === index) return 'text-red-500 dark:text-red-400 bg-red-200 dark:bg-red-700 px-1 rounded';
      
      const baseYouonChars = ['き', 'し', 'ち', 'に', 'ひ', 'み', 'り', 'ぎ', 'じ', 'ぢ', 'び', 'ぴ'];
      const smallYouonChars = ['ゃ', 'ゅ', 'ょ', 'ぁ', 'ぃ', 'ぅ', 'ぇ', 'ぉ'];
      const isSecondCharOfTargetYouon =
        index === typedEndIndex + 1 &&
        typedEndIndex >= 0 &&
        displayItems[typedEndIndex] !== undefined &&
        baseYouonChars.includes(displayItems[typedEndIndex] as string) &&
        smallYouonChars.includes(displayItems[index] as string);
      
      if (index < typedEndIndex) {
        if (practice === '英数短文入力３分間トレーニング' && char === ' ') return 'text-gray-400 bg-slate-100 dark:bg-slate-700 rounded';
        return 'text-gray-400';
      } else if (index === typedEndIndex || isSecondCharOfTargetYouon) {
        if (practice === '英数短文入力３分間トレーニング' && char === ' ') return 'bg-sky-200 dark:bg-sky-700 rounded';
        if (practice === '英数短文入力３分間トレーニング' && capitalizedCharIndices.has(index) && activePractice && 'correctionStage' in activePractice && activePractice.correctionStage === 'waitingForCorrectionKey') return 'text-orange-500 font-bold';
        return 'text-blue-500 font-bold';
      } else {
        if (practice === '英数短文入力３分間トレーニング' && char === ' ') return 'text-gray-700 dark:text-gray-300 bg-slate-100 dark:bg-slate-700 rounded';
        return 'text-gray-700 dark:text-gray-300';
      }
    }

    if (!isRandomMode && (practice === '拗音の基本練習' || practice === '拗濁音の練習' || practice === '拗半濁音の練習')) {
      const targetPairStartIndex = dIdx * 2;
      if (index === targetPairStartIndex || index === targetPairStartIndex + 1) return 'text-blue-500 font-bold';
      return 'text-gray-700 dark:text-gray-300';
    }

    let highlightClass = 'text-gray-700 dark:text-gray-300';
    let pulseClass = '';

    let isCurrentTarget = false;
    if (practice === '記号の基本練習３') {
        // char は表示文字 (例: "。")
        // localCurrentTargetForComparison は内部的なターゲット文字 (例: ",")
        if (localCurrentTargetForComparison === ',' && char === '。') {
            isCurrentTarget = true;
        } else if (localCurrentTargetForComparison === '.' && char === '、') {
            isCurrentTarget = true;
        } else if (typeof localCurrentTargetForComparison === 'string' && char === localCurrentTargetForComparison) {
            isCurrentTarget = true;
        }
    } else {
        if (localCurrentTargetForComparison !== undefined && localCurrentTargetForComparison !== null && typeof localCurrentTargetForComparison === 'string' && char === localCurrentTargetForComparison) {
            isCurrentTarget = true;
        }
    }

    if (isCurrentTarget) {
        highlightClass = 'text-blue-400 font-bold';
        const isYouonPracticeHandledAbove = practice === '拗音の基本練習' || practice === '拗濁音の練習' || practice === '拗半濁音の練習';
        if (isRandomMode && currentPracticeStatus === 'waiting' && !isYouonPracticeHandledAbove && practice !== '数字の基本練習') {
             pulseClass = ' animate-pulse';
        }
    } else if (currentPracticeStatus === 'correct') {
        let isCorrectlyTypedTarget = false;
        if (practice === '記号の基本練習３') {
            if (localCurrentTargetForComparison === ',' && char === '。') {
                isCorrectlyTypedTarget = true;
            } else if (localCurrentTargetForComparison === '.' && char === '、') {
                isCorrectlyTypedTarget = true;
            } else if (typeof localCurrentTargetForComparison === 'string' && char === localCurrentTargetForComparison) {
                isCorrectlyTypedTarget = true;
            }
        } else {
            if (localCurrentTargetForComparison !== undefined && localCurrentTargetForComparison !== null && typeof localCurrentTargetForComparison === 'string' && char === localCurrentTargetForComparison) {
                isCorrectlyTypedTarget = true;
            }
        }
        if (isCorrectlyTypedTarget) {
            highlightClass = 'text-green-500 font-bold';
        }
    }
    return `${highlightClass}${pulseClass}`;
  }, [practice, isRandomMode, activePractice, typedChar, capitalizedCharIndices, status, isFinished, dIdx, typedEndIndex, displayItems, isGairaigo, currentTargetForComparison, propsHeadingChars, currentGairaigoPracticeTargets, allAlphaSymbol1Chars, gIdx]); // Added gIdx

  const isTanbunChallenge = practice === '短文入力３分間トレーニング' || practice === '英数短文入力３分間トレーニング';

  return (
      <div className={`mb-4 flex items-center text-4xl ${
        isFinished ? 'font-bold justify-center'
        : (isTanbunChallenge ? 'font-semibold justify-start'
          : (practice === '拗音の基本練習' || practice === '拗濁音の練習' || practice === '拗半濁音の練習' || practice === '拗音拡張１' ? 'font-bold justify-center'
            : 'font-bold justify-center')
          )
       }`}> {/* Removed console.log from here */}
      {displayItems.map((item, charIdx) => {
        const charToDisplay = (typeof item === 'string') ? item : (item as HeadingCharDisplayInfo).char; // Ensure char access
        
        let dynamicCharClasses = "";
        // For YouonKakucho and Gairaigo, highlighting is now primarily driven by getHighlightClass
        // based on isPracticeTarget from displayItems[charIdx]
        if (practice === '拗音拡張１' || isGairaigo || practice === '英字記号の練習１') { // Add 英字記号の練習１ here
            dynamicCharClasses = getHighlightClass(charToDisplay, charIdx, currentTargetForComparison);
        } else { 
            let progressClass = "";
            // Gairaigo doesn't use typed-char/current-char in the same way for this specific progress logic
            // if (practice !== '拗音拡張２') { // This condition might be too broad now
            if (practice === '短文入力３分間トレーニング' || practice === '英数短文入力３分間トレーニング') {
                if (typedEndIndex !== undefined && charIdx < typedEndIndex) {
                    progressClass = " typed-char";
                }
                if (charIdx === typedEndIndex && !isFinished && activePractice?.status !== 'countdown' && activePractice?.status !== 'idle') {
                    progressClass = " current-char";
                }
            } else if (isRandomMode && activePractice?.status !== 'countdown' && activePractice?.status !== 'idle' && !isFinished ) {
                // For non-tanbun random modes, highlight based on dIdx if it's the target
                // However, for Kigo3, the target is determined by currentTargetForComparison
                if (practice === '記号の基本練習３' || practice === '記号の基本練習１' || practice === '記号の基本練習２') {
                     // Highlighting for these is handled by getHighlightClass directly
                } else if (charIdx === dIdx) { // Fallback for other random modes if needed
                    progressClass = " current-char";
                }

            } else if (charIdx === dIdx && activePractice?.status !== 'countdown' && activePractice?.status !== 'idle' && !isFinished) {
                progressClass = " current-char";
            } else if (charIdx < dIdx && activePractice?.status !== 'countdown' && activePractice?.status !== 'idle' && !isFinished) {
                progressClass = " typed-char";
            }
            // }
            dynamicCharClasses = `${progressClass} ${getHighlightClass(charToDisplay, charIdx, currentTargetForComparison)}`;
        }

        const isYouonLikePractice = practice === '拗音の基本練習' || practice === '拗濁音の練習' || practice === '拗半濁音の練習' || practice === '拗音拡張１' || practice === 'かな入力１分間トレーニング';
        let charSpecificClass = '';
        let paddingClass = '';
        
        if (isTanbunChallenge) {
          if (practice === '英数短文入力３分間トレーニング') {
            if (charToDisplay === ' ') charSpecificClass = 'mx-0.5';
            else paddingClass = 'px-0';
          } else {
            if (charIdx === 0) paddingClass = 'pl-1';
            else if (charIdx === displayItems.length - 1) paddingClass = 'pr-1';
            else paddingClass = 'px-0';
          }
        } else if (isYouonLikePractice || isGairaigo || practice === '英字記号の練習１') { // Include 英字記号の練習１ for consistent padding
          // For Gairaigo, displayItems are individual characters of the target word 
          // For Youon-like, displayItems can be pairs or single characters in random mode
          // For 英字記号の練習１ random, displayItems is shuffled list of chars
          const isSingleCharDisplayInHeader = displayItems.length === 1; // Covers 拗半濁音 random AND 拗音拡張１ random AND 英字記号の練習１ random (when fallback)
          
          if (isSingleCharDisplayInHeader) {
            paddingClass = 'px-1'; // Apply for any practice mode where only one item is in the header
          } else if (isGairaigo) { // Gairaigo characters are displayed one by one
            if (isRandomMode) {
              paddingClass = 'px-0'; // ランダムモード時は文字間パディングをなくす
            } else {
              paddingClass = 'px-0.5'; // 通常モード時はヘッダーリスト項目間にわずかなパディングを保持
            }
          } else if (practice === '英字記号の練習１' && isRandomMode) { // 英字記号の練習１ランダムは文字間にわずかな間隔
             paddingClass = 'px-0.5'; // 英字記号の練習１ランダムは文字間にわずかなパディング
          }
          else { // Youon pairs
            const isFirstCharOfYouonPair = charIdx % 2 === 0;
            const isSecondCharOfYouonPair = charIdx % 2 !== 0;
            if (isFirstCharOfYouonPair) paddingClass = 'pl-1';
            else if (isSecondCharOfYouonPair) paddingClass = 'pr-1';
          }
        }
        

        if (!isTanbunChallenge && !isYouonLikePractice && !isGairaigo && practice !== '英字記号の練習１') paddingClass = 'px-1'; // Exclude 英字記号の練習１ from default padding

        if (practice === '大文字変換操作' && charIdx === dIdx && activePractice?.targetChar) {
          const parts = charToDisplay.split('-');
          const targetCharFromHook = typeof activePractice?.targetChar === 'string' ? activePractice.targetChar : 
                                     (typeof activePractice?.targetChar === 'object' && activePractice.targetChar && 'char' in activePractice.targetChar ? (activePractice.targetChar as {char?: string}).char : '');
          if (parts.length === 2 && targetCharFromHook) {
            return (
              <span key={charIdx} className={`${paddingClass} ${charSpecificClass} py-1 rounded transition-colors duration-150`}>
                <span className={`${parts[0] === targetCharFromHook ? 'text-blue-400 font-bold' : 'text-gray-700 dark:text-gray-300'}`}>
                  {parts[0]}
                </span>
                <span className="text-gray-500 dark:text-gray-400">-</span>
                <span className={parts[1] === targetCharFromHook ? 'text-blue-400 font-bold' : 'text-gray-700 dark:text-gray-300'}>
                  {parts[1]}
                </span>
              </span>
            );
          }
        }
          return (
            <span
              key={charIdx}
              className={`${paddingClass} ${charSpecificClass} py-1 rounded transition-colors duration-150 ${
                "char"} ${dynamicCharClasses.trim()
              }`}
            >
              {charToDisplay === ' ' && practice === '英数短文入力３分間トレーニング' ? '\u00A0' : charToDisplay}
            </span>
          );
      })}
    </div>
  );
};

export default PracticeHeading;
