// /home/coffee/my-keymap-viewer/src/hooks/useAlphabetPractice.ts
import { useState, useCallback, useMemo, useEffect, useRef } from 'react';
import {
    PracticeHookProps,
    PracticeHookResult,
    PracticeInputInfo,
    PracticeInputResult as HandleInputResult,
    PracticeStatus,
    PracticeHighlightResult,
} from './usePracticeCommons';
import { alphabetPracticeGroups } from '../data/keymapData'; // グループ情報をインポート

// Define the type for internalStage explicitly
type AlphabetPracticeStage = 'waitingForGroupKey' | 'waitingForCharKey' | 'finished';

const useAlphabetPractice = ({
    gIdx,
    dIdx,
    isActive,
    onAdvance,
    layers,
    isRandomMode, // isRandomMode を props から受け取る
}: PracticeHookProps): PracticeHookResult => {
    const [internalStage, setInternalStage] = useState<AlphabetPracticeStage>('waitingForGroupKey');
    const [currentTargetGroup, setCurrentTargetGroup] = useState<string>('');
    const [status, setStatus] = useState<PracticeStatus>('waiting');
    const [errorLayer, setErrorLayer] = useState<number | null>(null); // エラーレイヤーを管理するstate
    const [randomTargetChar, setRandomTargetChar] = useState<string | null>(null); // ランダムモード用のターゲット文字

    const prevIsActiveRef = useRef(isActive);
    const prevGIdxRef = useRef(gIdx);
    const prevDIdxRef = useRef(dIdx);
    const prevIsRandomModeRef = useRef(isRandomMode); // isRandomMode の前回の値を保持
    const isInitialMount = useRef(true); // 初期マウントフラグ

    // console.log(`[AlphabetPractice] Hook initialized/rendered. isActive: ${isActive}, gIdx: ${gIdx}, dIdx: ${dIdx}, internalStage: ${internalStage}, isRandomMode: ${isRandomMode}`);

    const selectNextRandomChar = useCallback(() => {
        const allChars = alphabetPracticeGroups.join('');
        if (allChars.length > 0) {
            const randomIndex = Math.floor(Math.random() * allChars.length);
            const char = allChars[randomIndex];
            setRandomTargetChar(char);
            // ランダムモードでは、ターゲット文字に対応するグループを見つける必要がある
            const group = alphabetPracticeGroups.find(g => g.includes(char.toLowerCase()));
            setCurrentTargetGroup(group || ''); // グループが見つからない場合は空文字列
            setInternalStage('waitingForGroupKey'); // 常にグループキー入力から
            setStatus('waiting');
            setErrorLayer(null);
            console.log(`[AlphabetPractice selectNextRandomChar] New random target: ${char}, Group: ${group}`);
        }
    }, []);

    const reset = useCallback(() => {
        // console.log('[AlphabetPractice reset]');
        setInternalStage('waitingForGroupKey');
        setCurrentTargetGroup('');
        setStatus('waiting');
        setErrorLayer(null); // エラーレイヤーをリセット
        setRandomTargetChar(null); // ランダムターゲットもリセット
        prevGIdxRef.current = -1;
        prevDIdxRef.current = -1;
        isInitialMount.current = true; // リセット時に初期マウントフラグもリセット
    }, [setInternalStage, setCurrentTargetGroup, setStatus, setErrorLayer, setRandomTargetChar]);

    useEffect(() => {
        console.log(`[AlphabetPractice useEffect] START. isActive: ${isActive}, gIdx: ${gIdx}, dIdx: ${dIdx}, prevGIdx: ${prevGIdxRef.current}, prevDIdx: ${prevDIdxRef.current}, internalStage: ${internalStage}, currentTargetGroup: "${currentTargetGroup}", isRandomMode: ${isRandomMode}`);
        const justActivated = isActive && !prevIsActiveRef.current;
        const randomModeChanged = isRandomMode !== prevIsRandomModeRef.current;

        if (!isActive && prevIsActiveRef.current) {
            console.log('[AlphabetPractice useEffect] Practice became inactive. Calling reset().');
            reset();
        } else if (isActive) {
            if (randomModeChanged) {
                console.log(`[AlphabetPractice useEffect] Random mode changed to ${isRandomMode}. Resetting and selecting target if random.`);
                reset(); // モード変更時はリセット
                if (isRandomMode) {
                    selectNextRandomChar();
                } else {
                    // 通常モードに戻った場合、gIdx/dIdx に基づいてターゲットグループを設定
                    if (gIdx < alphabetPracticeGroups.length) {
                        setCurrentTargetGroup(alphabetPracticeGroups[gIdx]);
                    } else {
                        setCurrentTargetGroup('');
                        setInternalStage('finished');
                        setStatus('finished');
                    }
                }
            } else if (isRandomMode) {
                if (justActivated || !randomTargetChar) { // アクティブ化直後またはターゲットがない場合
                    console.log('[AlphabetPractice useEffect] Random mode, just activated or no target. Selecting next random char.');
                    selectNextRandomChar();
                }
            } else { // 通常モード
                if (gIdx < alphabetPracticeGroups.length) {
                    const group = alphabetPracticeGroups[gIdx];
                    if (group !== currentTargetGroup) {
                        setCurrentTargetGroup(group);
                    }
                    if (justActivated || gIdx !== prevGIdxRef.current || dIdx !== prevDIdxRef.current) {
                        console.log(`[AlphabetPractice useEffect] Normal mode, resetting to 'waitingForGroupKey'. Reason: justActivated=${justActivated}, gIdx changed=${gIdx !== prevGIdxRef.current}, dIdx changed=${dIdx !== prevDIdxRef.current}`);
                        setInternalStage('waitingForGroupKey');
                        setStatus('waiting');
                        setErrorLayer(null);
                    }
                } else {
                    if (internalStage !== 'finished') {
                        console.log('[AlphabetPractice useEffect] Normal mode, all groups completed. Setting stage to finished.');
                        setInternalStage('finished');
                        setStatus('finished');
                        setErrorLayer(null);
                        setCurrentTargetGroup('');
                    }
                }
            }
        }
        prevIsActiveRef.current = isActive;
        prevGIdxRef.current = gIdx;
        prevDIdxRef.current = dIdx;
        prevIsRandomModeRef.current = isRandomMode;
        if (isActive) isInitialMount.current = false; // アクティブになったら初期マウントではない
        console.log(`[AlphabetPractice useEffect] END. Updated refs. currentTargetGroup: "${currentTargetGroup}", internalStage: ${internalStage}, status: ${status}, prevGIdx: ${prevGIdxRef.current}, prevDIdx: ${prevDIdxRef.current}, randomTargetChar: ${randomTargetChar}`);
    }, [isActive, gIdx, dIdx, isRandomMode, alphabetPracticeGroups, reset, internalStage, currentTargetGroup, randomTargetChar, selectNextRandomChar, setInternalStage, setStatus, setErrorLayer]);


    const handleInput = useCallback((input: PracticeInputInfo): HandleInputResult => {
        console.log(`[AlphabetPractice handleInput] CALLED. isActive: ${isActive}, internalStage: ${internalStage}, currentTargetGroup: "${currentTargetGroup}", gIdx: ${gIdx}, dIdx: ${dIdx}, randomTargetChar: "${randomTargetChar}"`);
        console.log(`[AlphabetPractice handleInput] Input details: type=${input.type}, pressCode=0x${input.pressCode.toString(16)}`);

        const currentEffectiveTargetGroup = isRandomMode
            ? alphabetPracticeGroups.find(g => g.includes(randomTargetChar?.toLowerCase() ?? '')) || ''
            : currentTargetGroup;

        const currentEffectiveDIdx = isRandomMode
            ? currentEffectiveTargetGroup.indexOf(randomTargetChar?.toLowerCase() ?? '')
            : dIdx;

        console.log(`[AlphabetPractice handleInput] Effective Target Group: "${currentEffectiveTargetGroup}", Effective dIdx: ${currentEffectiveDIdx}`);


        if (!isActive || internalStage === 'finished' || !currentEffectiveTargetGroup || !layers) {
            console.log(`[AlphabetPractice handleInput] PRE-CONDITION FAILED. isActive: ${isActive}, internalStage: ${internalStage}, currentEffectiveTargetGroup: "${currentEffectiveTargetGroup}", layers: ${!!layers}`);
            return { isExpected: false, shouldGoToNext: false, errorLayer: null };
        }

        if (input.type !== 'release') {
            console.log(`[AlphabetPractice handleInput] Not a 'release' event. Type: ${input.type}. Skipping.`);
            return { isExpected: false, shouldGoToNext: false, errorLayer: null };
        }

        let pressedKeyNameOnLayer4: string | null = null;
        if (layers[4] && input.pressCode >= 0 && input.pressCode < layers[4].length) {
            pressedKeyNameOnLayer4 = layers[4][input.pressCode]?.trim();
            console.log(`[AlphabetPractice handleInput] Key Lookup Layer 4: input.pressCode=${input.pressCode}, layers[4][${input.pressCode}]="${layers[4][input.pressCode]}" -> pressedKeyNameOnLayer4="${pressedKeyNameOnLayer4}"`);
        }

        let pressedKeyNameOnLayer5: string | null = null;
        if (layers[5] && input.pressCode >= 0 && input.pressCode < layers[5].length) {
            pressedKeyNameOnLayer5 = layers[5][input.pressCode]?.trim();
            console.log(`[AlphabetPractice handleInput] Key Lookup Layer 5: input.pressCode=${input.pressCode}, layers[5][${input.pressCode}]="${layers[5][input.pressCode]}" -> pressedKeyNameOnLayer5="${pressedKeyNameOnLayer5}"`);
        }

        const charInputKeyNames = ["①", "②", "③", "④", "⑤", "⑥", "⑦", "⑧", "⑨", "⑩", "⑪", "⑫", "⑬", "⑭", "⑮", "⑯", "⑰", "⑱", "⑲", "⑳"];
        const expectedCharKeyName = (currentEffectiveDIdx !== -1 && currentEffectiveDIdx < charInputKeyNames.length && currentEffectiveDIdx < currentEffectiveTargetGroup.length)
            ? charInputKeyNames[currentEffectiveDIdx]
            : null;

        let isCorrectInput = false;
        let nextInternalStage: AlphabetPracticeStage = internalStage;
        let currentErrorLayer: number | null = null; // この関数スコープでのエラーレイヤー

        if (internalStage === 'waitingForGroupKey') {
            console.log(`[AlphabetPractice handleInput] Stage: waitingForGroupKey. Pressed Layer 4: "${pressedKeyNameOnLayer4}", Expected Group: "${currentEffectiveTargetGroup}"`);
            if (pressedKeyNameOnLayer4 && pressedKeyNameOnLayer4.toLowerCase() === currentEffectiveTargetGroup.toLowerCase()) {
                console.log('[AlphabetPractice handleInput] Group key MATCH. Transitioning to waitingForCharKey.');
                isCorrectInput = true;
                nextInternalStage = 'waitingForCharKey';
                setStatus('waiting');
                currentErrorLayer = null;
            } else {
                 console.log('[AlphabetPractice handleInput] Group key NO MATCH.');
                 isCorrectInput = false;
                 currentErrorLayer = 4;
            }
        } else if (internalStage === 'waitingForCharKey') {
            console.log(`[AlphabetPractice handleInput] Stage: waitingForCharKey. Pressed Layer 5: "${pressedKeyNameOnLayer5}", Expected Char Key: "${expectedCharKeyName}" (for char: ${currentEffectiveTargetGroup[currentEffectiveDIdx]})`);
            if (expectedCharKeyName && pressedKeyNameOnLayer5 && pressedKeyNameOnLayer5 === expectedCharKeyName) {
                console.log('[AlphabetPractice handleInput] Char key MATCH.');
                isCorrectInput = true;
                nextInternalStage = 'waitingForGroupKey';
                currentErrorLayer = null;
                if (isRandomMode) {
                    selectNextRandomChar(); // ランダムモードなら次の文字へ
                    return { isExpected: isCorrectInput, shouldGoToNext: false, errorLayer: currentErrorLayer };
                } else {
                    // 通常モードなら App.tsx に進行を任せる
                    return { isExpected: isCorrectInput, shouldGoToNext: true, errorLayer: currentErrorLayer };
                }
            } else {
                console.log('[AlphabetPractice handleInput] Char key NO MATCH.');
                isCorrectInput = false;
                currentErrorLayer = 5;
            }
        }

        setStatus(isCorrectInput ? 'correct' : 'incorrect');
        setErrorLayer(currentErrorLayer);

        if (nextInternalStage !== internalStage) {
            console.log(`[AlphabetPractice handleInput] Stage changing from ${internalStage} to ${nextInternalStage}`);
            setInternalStage(nextInternalStage);
        }

        return { isExpected: isCorrectInput, shouldGoToNext: false, errorLayer: currentErrorLayer };

    }, [isActive, internalStage, currentTargetGroup, gIdx, dIdx, onAdvance, layers, setInternalStage, setStatus, setErrorLayer, isRandomMode, randomTargetChar, selectNextRandomChar]);


    const getHighlightClassName = useCallback((keyName: string, layoutIndex: number): PracticeHighlightResult => {
        if (!isActive || internalStage === 'finished' || status === 'incorrect') {
            return { className: null, overrideKey: null };
        }

        const currentEffectiveTargetGroup = isRandomMode
            ? alphabetPracticeGroups.find(g => g.includes(randomTargetChar?.toLowerCase() ?? '')) || ''
            : currentTargetGroup;

        const currentEffectiveDIdx = isRandomMode
            ? currentEffectiveTargetGroup.indexOf(randomTargetChar?.toLowerCase() ?? '')
            : dIdx;

        if (!currentEffectiveTargetGroup) {
            return { className: null, overrideKey: null };
        }

        const charInputKeyNames = ["①", "②", "③", "④", "⑤", "⑥", "⑦", "⑧", "⑨", "⑩", "⑪", "⑫", "⑬", "⑭", "⑮", "⑯", "⑰", "⑱", "⑲", "⑳"];
        const expectedCharKeyName = (currentEffectiveDIdx !== -1 && currentEffectiveDIdx < charInputKeyNames.length && currentEffectiveDIdx < currentEffectiveTargetGroup.length)
            ? charInputKeyNames[currentEffectiveDIdx]
            : null;

        if (internalStage === 'waitingForGroupKey') {
            if (layoutIndex === 4 && keyName.trim().toLowerCase() === currentEffectiveTargetGroup.toLowerCase()) {
                return { className: 'bg-blue-100 dark:bg-blue-800', overrideKey: null };
            }
        } else if (internalStage === 'waitingForCharKey') {
            if (layoutIndex === 5 && expectedCharKeyName && keyName.trim() === expectedCharKeyName) {
                return { className: 'bg-blue-100 dark:bg-blue-800', overrideKey: null };
            }
        }
        return { className: null, overrideKey: null };
    }, [isActive, internalStage, currentTargetGroup, dIdx, status, isRandomMode, randomTargetChar]);


    const headingChars = useMemo(() => {
        if (!isActive) return [];
        if (isRandomMode) {
            // ランダムモード時は、ターゲットの1文字だけをヘッダー文字として返す
            return randomTargetChar ? [randomTargetChar] : [];
        }
        // 通常モード時は、現在のグループの文字リストを返す
        if (gIdx < alphabetPracticeGroups.length) {
            return alphabetPracticeGroups[gIdx].split('');
        }
        return [];
    }, [isActive, isRandomMode, randomTargetChar, gIdx, alphabetPracticeGroups]); // alphabetPracticeGroups を依存配列に追加

    const isInvalidInputTarget = useCallback((
        invalidlyPressedKeyCode: number,
        keyBeingRenderedLayoutIndex: number,
        keyBeingRenderedIndex: number
    ): boolean => {
        if (!isActive || status !== 'incorrect' || errorLayer === null) {
            return false;
        }
        return keyBeingRenderedLayoutIndex === errorLayer && keyBeingRenderedIndex === invalidlyPressedKeyCode;
    }, [isActive, status, errorLayer]);

    const clearErrorState = useCallback(() => {
        console.log('[AlphabetPractice clearErrorState] CALLED. Clearing error layer and setting status to waiting.');
        setErrorLayer(null);
        setStatus('waiting');
    }, [setErrorLayer, setStatus]);

    const currentTargetCharForHook = useMemo(() => {
        if (isRandomMode) {
            return randomTargetChar;
        }
        if (currentTargetGroup && dIdx < currentTargetGroup.length) {
            return currentTargetGroup[dIdx];
        }
        return undefined;
    }, [isRandomMode, randomTargetChar, currentTargetGroup, dIdx]);

    return {
        status,
        handleInput,
        getHighlightClassName,
        targetChar: currentTargetCharForHook,
        reset,
        targetLayerIndex: null,
        headingChars,
        targetLayerIndices: [4, 5],
        isInvalidInputTarget,
        errorLayer,
        clearErrorState,
    };
};

export default useAlphabetPractice;
