// /home/coffee/my-keymap-viewer/src/hooks/useHandakuonPractice.ts
import { useState, useCallback, useMemo, useEffect, useRef } from 'react';
import {
  PracticeHookProps,
  PracticeHookResult,
  PracticeInputInfo,
  PracticeInputResult,
  hid2GyouHRight_Kana,
  hid2GyouHLeft_Kana,
  hid2DanHRight_Kana,
  hid2DanHLeft_Kana,
  hid2GyouVRight_Kana,
  hid2GyouVLeft_Kana,
  hid2DanVRight_Kana,
  hid2DanVLeft_Kana,
  CharInfoHandakuon,
  allHandakuonCharInfos,
  PracticeHighlightResult,
} from './usePracticeCommons';
import { handakuonGyouList, handakuonGyouChars, handakuonDanMapping } from '../data/keymapData';

type HandakuonStage = 'gyouInput' | 'handakuonInput' | 'danInput';

export default function useHandakuonPractice({
  isActive,
  gIdx, // 半濁音は gIdx を使わないが、props としては受け取る
  dIdx,
  side,
  kb,
  isRandomMode
}: PracticeHookProps): PracticeHookResult {
  const [stage, setStage] = useState<HandakuonStage>('gyouInput');
  const [isBlinking, setIsBlinking] = useState(false);
  const inputCount = useRef<number>(0);
  const [isErrorInDanInput, setIsErrorInDanInput] = useState(false);
  const [currentErrorHighlightLayer, setCurrentErrorHighlightLayer] = useState<number | null>(null);

  const blinkTimeoutRef = useRef<number | null>(null);
  const { hid2Gyou, hid2Dan } = useMemo(() => {
      if (kb === 'tw-20v') {
          return side === 'left'
              ? { hid2Gyou: hid2GyouVLeft_Kana, hid2Dan: hid2DanVLeft_Kana }
              : { hid2Gyou: hid2GyouVRight_Kana, hid2Dan: hid2DanVRight_Kana };
      } else { // TW-20H
          return side === 'left'
              ? { hid2Gyou: hid2GyouHLeft_Kana, hid2Dan: hid2DanHLeft_Kana }
              : { hid2Gyou: hid2GyouHRight_Kana, hid2Dan: hid2DanHRight_Kana };
      }
  }, [side, kb]);

  const hGyouKeyCode = useMemo(() => {
      const entry = Object.entries(hid2Gyou).find(([_, name]) => name === 'は行');
      return entry ? parseInt(entry[0]) : null;
  }, [hid2Gyou]);

  const dakuonKeyCode = useMemo(() => {
      const gyouMap = kb === 'tw-20v'
          ? (side === 'left' ? hid2GyouVLeft_Kana : hid2GyouVRight_Kana)
          : (side === 'left' ? hid2GyouHLeft_Kana : hid2GyouHRight_Kana);
      const entry = Object.entries(gyouMap).find(([_, name]) => name === '” ° 小');
      return entry ? parseInt(entry[0]) : null;
  }, [side, kb]);

  const [randomTarget, setRandomTarget] = useState<CharInfoHandakuon | null>(null);
  const prevDIdxRef = useRef(dIdx); // gIdx は常に0なので prevGIdxRef は不要
  const isInitialMount = useRef(true);
  const prevIsActiveRef = useRef(isActive);
  const [currentTargetCharInternal, setCurrentTargetCharInternal] = useState<string | null>(null); // ヘッダーハイライト用
  const prevIsRandomModeRef = useRef(isRandomMode);

  const resetBlinkingState = useCallback(() => {
    setIsBlinking(false);
    inputCount.current = 0;
    if (blinkTimeoutRef.current !== null) {
      clearTimeout(blinkTimeoutRef.current);
      blinkTimeoutRef.current = null;
    }
  }, []);

  const selectNextRandomTarget = useCallback(() => {
      if (allHandakuonCharInfos.length > 0) {
          const randomIndex = Math.floor(Math.random() * allHandakuonCharInfos.length);
          setRandomTarget(allHandakuonCharInfos[randomIndex]);
          setCurrentTargetCharInternal(allHandakuonCharInfos[randomIndex].char); // ランダムターゲットの文字を設定
          setStage('gyouInput');
          resetBlinkingState();
          setIsErrorInDanInput(false);
          setCurrentErrorHighlightLayer(null);
      } else {
          setRandomTarget(null);
          setCurrentTargetCharInternal(null);
      }
  }, [setRandomTarget, setStage, resetBlinkingState, setCurrentTargetCharInternal]);

  const reset = useCallback(() => {
    setStage('gyouInput');
    resetBlinkingState();
    setRandomTarget(null);
    prevDIdxRef.current = -1; // dIdx の前回値をリセット
    isInitialMount.current = true;
    prevIsRandomModeRef.current = false;
    setCurrentTargetCharInternal(null); // リセット時にクリア
    setIsErrorInDanInput(false);
    setCurrentErrorHighlightLayer(null);
  }, [resetBlinkingState, setStage, setRandomTarget, setCurrentTargetCharInternal]);

  useEffect(() => {
    if (!isActive && prevIsActiveRef.current) {
        reset();
    }

    if (isActive) {
        const justActivated = isActive && !prevIsActiveRef.current;
        const indicesChangedInNormalMode = !isRandomMode && (dIdx !== prevDIdxRef.current);
        const switchedToNormalMode = !isRandomMode && prevIsRandomModeRef.current;
        const switchedToRandomMode = isRandomMode && !prevIsRandomModeRef.current;

        if (justActivated) {
            isInitialMount.current = true;
        }

        if (switchedToNormalMode || (!isRandomMode && (isInitialMount.current || indicesChangedInNormalMode))) {
            setStage('gyouInput');
            resetBlinkingState();
            setRandomTarget(null);
            // 通常モード時のターゲット文字を設定
            if (dIdx >= 0) {
                const char = handakuonGyouChars['は行']?.[dIdx];
                setCurrentTargetCharInternal(char ?? null);
            } else {
                setCurrentTargetCharInternal(null);
            }
            setIsErrorInDanInput(false);
            setCurrentErrorHighlightLayer(null);
            prevDIdxRef.current = dIdx; // dIdx の変更を追跡
            isInitialMount.current = false;
        } else if (switchedToNormalMode) { // ランダムモードから通常モードに切り替わった場合
            setStage('gyouInput');
            resetBlinkingState();
            setRandomTarget(null);
            setCurrentErrorHighlightLayer(null); // エラー状態をクリア
            prevDIdxRef.current = dIdx;
            isInitialMount.current = false;
        } else if (isRandomMode && (switchedToRandomMode || isInitialMount.current || !randomTarget)) {
            // selectNextRandomTarget の中でエラーステートはリセットされる
            selectNextRandomTarget();
            isInitialMount.current = false;
        }
        // App.tsx のエラークリア後に再レンダリングが走ることを期待し、
        // dIdx が変更されていなくても、エラー状態をリセットする
        if (!isRandomMode && isActive && currentErrorHighlightLayer !== null && !indicesChangedInNormalMode && !justActivated && !switchedToNormalMode && !switchedToRandomMode) {
            // console.log("[HandakuonPractice useEffect] Clearing error highlight due to potential App.tsx error clear");
            // setCurrentErrorHighlightLayer(null); // ここでクリアすると、エラー表示直後に消えてしまう可能性
        }
    }
    prevIsActiveRef.current = isActive;
    prevIsRandomModeRef.current = isRandomMode;

    // エラー状態のクリーンアップ (練習非アクティブ時)
    if (!isActive && currentErrorHighlightLayer !== null) {
        setCurrentErrorHighlightLayer(null);
        setIsErrorInDanInput(false);
    }

    return () => {
        if (blinkTimeoutRef.current !== null) {
            clearTimeout(blinkTimeoutRef.current);
            blinkTimeoutRef.current = null; // Explicitly nullify the ref in cleanup
        }
    };
  }, [isActive, isRandomMode, dIdx, reset, selectNextRandomTarget, resetBlinkingState, randomTarget, setStage, setCurrentTargetCharInternal, currentErrorHighlightLayer]);


  const currentDan = useMemo(() => {
      if (isRandomMode) return randomTarget?.danKey ?? null;
      if (!isActive) return null;
      const danList = handakuonDanMapping['は行'];
      if (!danList || dIdx < 0 || dIdx >= danList.length) return null;
      return danList[dIdx];
  }, [isActive, isRandomMode, randomTarget, dIdx]);

  const headingChars = useMemo(() => {
    if (!isActive) return [];
    const charsForGyou = handakuonGyouChars['は行'];
    if (!charsForGyou || charsForGyou.length === 0) return [];

    if (isRandomMode) {
        return randomTarget ? [randomTarget.char] : [];
    } else {
        return charsForGyou;
    }
  }, [isActive, isRandomMode, randomTarget]);

  const handleInput = useCallback(
    (input: PracticeInputInfo): PracticeInputResult => {
      if (!isActive || !currentDan || hGyouKeyCode === null || dakuonKeyCode === null) {
          return { isExpected: false, shouldGoToNext: undefined };
      }
      if (input.type !== 'release') {
          return { isExpected: false, shouldGoToNext: undefined };
      }

      const pressCode = input.pressCode;
      let isExpected = false;
      let shouldGoToNext_final: boolean | undefined = undefined;
      let nextStage: HandakuonStage = stage;

      // 入力があったら、まず前回のエラーハイライトをクリアする
      // これにより、App.tsxのエラー表示タイムアウト後に残っている可能性のあるフック内部のエラー状態をリセットする
      // ただし、エラー発生直後の入力ではエラーレイヤーを維持したいので、ここではクリアしない。
      // エラークリアは、正解時、または不正解でステージがリセットされる際に行う。
      // if (currentErrorHighlightLayer !== null) {
      //     setCurrentErrorHighlightLayer(null);
      // }


      switch (stage) {
        case 'gyouInput':
          if ((pressCode + 1) === hGyouKeyCode) { // pressCode を 1-based にして比較
            nextStage = 'handakuonInput';
            inputCount.current = 0;
            isExpected = true;
            shouldGoToNext_final = undefined;
            setIsErrorInDanInput(false); // gyouInput が正解なら danInput のエラーは解消
            setCurrentErrorHighlightLayer(null); // 正解なのでエラークリア
          } else {
            isExpected = false;
            shouldGoToNext_final = undefined;
            if (stage === 'gyouInput') { // gyouInput でのエラー
                setCurrentErrorHighlightLayer(2);
            }
          }
          break;
        case 'handakuonInput':
          if ((pressCode + 1) === dakuonKeyCode) { // pressCode を 1-based にして比較
            if (inputCount.current === 0) {
              inputCount.current = 1;
              setIsBlinking(true);
              if (blinkTimeoutRef.current !== null) clearTimeout(blinkTimeoutRef.current);
              blinkTimeoutRef.current = window.setTimeout(() => {
                setIsBlinking(false);
                blinkTimeoutRef.current = null;
              }, 300);
              isExpected = true;
              shouldGoToNext_final = undefined;
            } else if (inputCount.current === 1) {
              inputCount.current = 0;
              setIsBlinking(false);
              if (blinkTimeoutRef.current !== null) {
                clearTimeout(blinkTimeoutRef.current);
                blinkTimeoutRef.current = null;
              }
              nextStage = 'danInput';
              isExpected = true;
              shouldGoToNext_final = undefined;
            }
            setIsErrorInDanInput(false); // handakuonInput が正解なら danInput のエラーは解消
            setCurrentErrorHighlightLayer(null); // 正解なのでエラークリア
          } else {
              isExpected = false;
              shouldGoToNext_final = undefined;
              if (stage === 'handakuonInput') { // handakuonInput でのエラー
                  setCurrentErrorHighlightLayer(2);
              }
          }
          break;
        case 'danInput':
          const expectedDanKeyCodes = Object.entries(hid2Dan)
              .filter(([_, name]) => name === currentDan)
              .map(([codeStr, _]) => parseInt(codeStr));
          nextStage = 'gyouInput'; // Reset stage for the next character/target
          resetBlinkingState();    // Also reset blinking state
          if (expectedDanKeyCodes.includes(pressCode + 1)) { // pressCode を 1-based にして比較
            isExpected = true;
            console.log(`[HandakuonPractice handleInput DAN_INPUT_CORRECT] isExpected: ${isExpected}, dIdx: ${dIdx}, currentDan: ${currentDan}`);
            setCurrentErrorHighlightLayer(null); // 正解なのでエラークリア
            setIsErrorInDanInput(false);
            if (isRandomMode) {
                setCurrentErrorHighlightLayer(null); // Ensure error highlight is cleared
                setIsErrorInDanInput(false);
                selectNextRandomTarget(); // This will reset stage to gyouInput
                shouldGoToNext_final = false;
            } else {
                const charsInCurrentGyou = handakuonGyouChars['は行'];
                if (dIdx >= charsInCurrentGyou.length - 1) {
                    shouldGoToNext_final = true;
                } else {
                    shouldGoToNext_final = false;
                }
            }
            console.log(`[HandakuonPractice handleInput DAN_INPUT_CORRECT] shouldGoToNext_final: ${shouldGoToNext_final}`);
          } else {
              isExpected = false;
              console.log(`[HandakuonPractice handleInput DAN_INPUT_INCORRECT] isExpected: ${isExpected}`);
              setIsErrorInDanInput(true); // エラー状態をtrueに
              setCurrentErrorHighlightLayer(3); // danInput はレイヤー3
              shouldGoToNext_final = undefined;
          }
          break;
      }

      if (isExpected) {
        // 正解の場合、エラーハイライトは既にクリアされているか、
        // もともとエラーでなかったはずなので、ここでは何もしないか、念のためクリアする。
        setCurrentErrorHighlightLayer(null);
        if (stage === 'danInput') setIsErrorInDanInput(false); // danInput で正解ならエラー状態解除
        // danInput で正解した場合、nextStage は 'gyouInput' になっているはず
        if (stage === 'danInput' && nextStage === 'gyouInput') {
            resetBlinkingState(); // gyouInput に戻るのでブリンク状態もリセット
        }
        if (nextStage !== stage) {
          console.log(`[HandakuonPractice handleInput] Setting stage from ${stage} to ${nextStage}`);
          setStage(nextStage);
        }
      } else { // Not expected
        // 不正解だった場合、エラーレイヤーを設定する
        if (stage === 'gyouInput') {
            setCurrentErrorHighlightLayer(2);
        } else if (stage === 'handakuonInput') {
            setCurrentErrorHighlightLayer(2);
        } else if (stage === 'danInput') {
            setCurrentErrorHighlightLayer(3);
            // danInput でエラーの場合、ステージは gyouInput に戻す
            nextStage = 'gyouInput';
        }
        // 不正解の場合、ステージを gyouInput に戻す
        if (stage === 'danInput') { // danInput で不正解だった場合
          setStage('gyouInput');
          resetBlinkingState(); // Ensure blinking is reset on error if not already gyouInput
        } else if (stage !== 'gyouInput') {
          setStage('gyouInput');
          resetBlinkingState();
          console.log(`[HandakuonPractice handleInput] Incorrect input, stage was ${stage}, resetting to gyouInput`);
        }
      }


      console.log(`[HandakuonPractice handleInput END] Returning: isExpected=${isExpected}, shouldGoToNext=${shouldGoToNext_final}, nextStageToSet=${nextStage}, currentStageAfterSet=${stage}`);
      return { isExpected, shouldGoToNext: shouldGoToNext_final };
    }, [
        isActive, stage, currentDan, hGyouKeyCode, dakuonKeyCode, hid2Dan, resetBlinkingState,
        isRandomMode, selectNextRandomTarget, setStage, dIdx, setIsBlinking,
        currentErrorHighlightLayer, // currentErrorHighlightLayer を依存配列に追加
        setIsErrorInDanInput // setIsErrorInDanInput を依存配列に追加
    ]);

  const getHighlightClassName = useCallback((key: string, layoutIndex: number, keyIndex: number): PracticeHighlightResult => { // Added keyIndex
      const noHighlight: PracticeHighlightResult = { className: null, overrideKey: null };
      if (!isActive || !currentDan) {
          return noHighlight;
      }


      const indicesJustChanged = !isRandomMode && dIdx !== prevDIdxRef.current;
      const currentStageForHighlight = indicesJustChanged ? 'gyouInput' : stage;

      // エラーハイライトがアクティブな場合
      if (currentErrorHighlightLayer !== null) {
          // エラーが発生しているレイヤー以外のキーはハイライトしない
          if (layoutIndex !== currentErrorHighlightLayer) {
              return noHighlight; // エラーレイヤー以外はハイライトしない
          }
      }      let expectedKeyName: string | null = null;      let targetLayoutIndex: number | null = null;

      switch (currentStageForHighlight) {
        case 'gyouInput':
          expectedKeyName = 'は行';
          targetLayoutIndex = 2;
          break;
        case 'handakuonInput':
          expectedKeyName = '” ° 小';
          targetLayoutIndex = 2;
          break;
        case 'danInput':
          expectedKeyName = currentDan;
          targetLayoutIndex = 3;
          break;
      }

      if (expectedKeyName !== null && layoutIndex === targetLayoutIndex && key === expectedKeyName) {
          if (currentStageForHighlight === 'handakuonInput' && isBlinking) {
            return noHighlight;
          }
          return { className: 'bg-blue-100', overrideKey: null };
      }
      return noHighlight; // keyIndex is not used in logic, so not a dependency
    }, [isActive, currentDan, stage, isBlinking, isRandomMode, dIdx, currentErrorHighlightLayer]);

    const isInvalidInputTarget = useCallback((targetPressCode: number, currentLayoutIndex: number, currentKeyIndex: number, pressedLayerIndex?: number): boolean => {
        if (!isActive) return false;

        // エラーが発生していて、そのエラーレイヤー上の、実際に押されたキーであれば true
        if (currentErrorHighlightLayer !== null && currentLayoutIndex === currentErrorHighlightLayer) {
            const pressedKeyIndex = targetPressCode; // App.tsx から渡される targetPressCode は 0-based のはず
            return currentKeyIndex === pressedKeyIndex;
        }
        return false; // エラーでない場合、またはエラーレイヤーと一致しない場合は不正入力ターゲットではない
    }, [isActive, currentErrorHighlightLayer]);

    const clearErrorState = useCallback(() => {
        console.log(`[HandakuonPractice clearErrorState] CALLED. Current errorHighlightLayer BEFORE: ${currentErrorHighlightLayer}`);
        setCurrentErrorHighlightLayer(null);
        setIsErrorInDanInput(false);
        console.log(`[HandakuonPractice clearErrorState] FINISHED. Current errorHighlightLayer AFTER: null (expected)`);
    }, [setCurrentErrorHighlightLayer, setIsErrorInDanInput]);

    // 練習フックが返すオブジェクト
    return useMemo(() => {
        let determinedtargetLayerIndex: number | undefined = undefined;
        let determinedErrorHighlightLayer: number | null = currentErrorHighlightLayer;
        let determinedTargetLayerIndices: number[] = [2, 3]; // Default for Handakuon

        if (stage === 'gyouInput') {
            determinedtargetLayerIndex = 2;
            if (currentErrorHighlightLayer !== null) {
                determinedTargetLayerIndices = Array.from(new Set([2, currentErrorHighlightLayer]));
            }
        } else if (stage === 'handakuonInput') {
            determinedtargetLayerIndex = 2;
            determinedTargetLayerIndices = [2,3];
            if (currentErrorHighlightLayer !== null) {
                 determinedTargetLayerIndices = Array.from(new Set([2, 3, currentErrorHighlightLayer]));
            }
        } else if (stage === 'danInput') {
            determinedtargetLayerIndex = 3;
            determinedTargetLayerIndices = [2,3];
            if (currentErrorHighlightLayer !== null) {
                 determinedTargetLayerIndices = Array.from(new Set([2, 3, currentErrorHighlightLayer]));
            }
        }

        // Ensure [2,3] are always included if no specific error layer dictates otherwise, or if error layer is one of them.
        if (currentErrorHighlightLayer === null || [2,3].includes(currentErrorHighlightLayer)) {
            determinedTargetLayerIndices = [2,3];
        }

        if (determinedErrorHighlightLayer !== null) {
            console.log(`[HandakuonPractice RETURN ERROR STATE] targetLayerIndex: ${determinedtargetLayerIndex}, targetLayerIndices: ${JSON.stringify(determinedTargetLayerIndices)}, errorHighlightLayer: ${determinedErrorHighlightLayer}`);
        } else if (stage === 'gyouInput') {
            console.log(`[HandakuonPractice RETURN GYOU INPUT] targetLayerIndex: ${determinedtargetLayerIndex}, targetLayerIndices: ${JSON.stringify(determinedTargetLayerIndices)}, errorHighlightLayer: null`);
        } else if (stage === 'danInput') {
            console.log(`[HandakuonPractice RETURN DAN INPUT (NO ERROR)] targetLayerIndex: ${determinedtargetLayerIndex}, targetLayerIndices: ${JSON.stringify(determinedTargetLayerIndices)}, errorHighlightLayer: null`);
        } else {
            console.log(`[HandakuonPractice RETURN OTHER STAGE: ${stage}] targetLayerIndex: ${determinedtargetLayerIndex}, targetLayerIndices: ${JSON.stringify(determinedTargetLayerIndices)}, errorHighlightLayer: null`);
        }

        return {
            stage: stage,
            targetChar: currentTargetCharInternal ?? undefined,
            headingChars: headingChars,
            handleInput,
            reset,
            targetLayerIndex: determinedtargetLayerIndex,
            targetLayerIndices: determinedTargetLayerIndices,
            errorHighlightLayer: determinedErrorHighlightLayer,
            getHighlightClassName,
            isInvalidInputTarget,
            clearErrorState,
            typedChar: '', // typedChar はこの練習では使わない
            status: 'running', // この練習では status は常に 'running' (または 'idle')
        };
    }, [
        stage, currentTargetCharInternal, headingChars, handleInput, reset,
        currentErrorHighlightLayer, getHighlightClassName, isInvalidInputTarget, clearErrorState, // 内部ステートも依存配列に追加
    ]);
}
