import { useState, useEffect } from 'react';
import { KeyboardModel, KeyboardSide } from './usePracticeCommons';
import { sampleJson } from '../data/keymapData';

interface KeyboardInitializerResult {
    layers: string[][];
    title: string;
    cols: number;
    kb: KeyboardModel;
    side: KeyboardSide;
    fw: string | null;
    sn: string | null;
    showTrainingButton: boolean;
}

export const useKeyboardInitializer = (): KeyboardInitializerResult => {
    const [layers, setLayers] = useState<string[][]>([]);
    const [title, setTitle] = useState('レイアウト読み込み中...');
    const [cols, setCols] = useState(5);
    const [kb, setKb] = useState<KeyboardModel>('tw-20v');
    const [side, setSide] = useState<KeyboardSide>('right');
    const [fw, setFw] = useState<string | null>(null);
    const [sn, setSn] = useState<string | null>(null);
    const [showTrainingButton, setShowTrainingButton] = useState(false);

    useEffect(() => {
        const params = new URLSearchParams(window.location.search);
        const kbRaw = params.get('kb') ?? 'tw-20v';
        const sideRaw = params.get('side') ?? 'right';

        const currentKb: KeyboardModel = kbRaw === 'tw-20h' ? 'tw-20h' : 'tw-20v';
        setKb(currentKb);

        const currentSide: KeyboardSide = sideRaw === 'left' ? 'left' : 'right';
        setSide(currentSide);

        // firmware と serial の宣言を console.log より前に移動
        const firmware = params.get('version');
        const serial = params.get('serial');

        const kbKey = currentKb as keyof typeof sampleJson;
        const sideKey = currentSide as keyof typeof sampleJson[typeof kbKey];
        if (sampleJson[kbKey] && sampleJson[kbKey][sideKey]) {
            const sel = sampleJson[kbKey][sideKey];
            setLayers(sel.layers);
            setTitle(`${currentKb.toUpperCase()} レイアウト (${currentSide})`);
            setCols(currentKb === 'tw-20v' ? 4 : 5);
            // sel が有効なこのブロック内でログを出力する
            console.log('[useKeyboardInitializer] Initialized values (data found):', { layers: sel.layers, title: `${currentKb.toUpperCase()} レイアウト (${currentSide})`, cols: currentKb === 'tw-20v' ? 4 : 5, kb: currentKb, side: currentSide, fw: firmware, sn: serial, showTrainingButton: !!(firmware || serial) });
        } else {
            console.error(`キーマップデータが見つかりません: ${currentKb}, ${currentSide}`);
            setLayers([]); setTitle('レイアウト不明'); setCols(5);
            console.log('[useKeyboardInitializer] Initialized values (data NOT found):', { layers: [], title: 'レイアウト不明', cols: 5, kb: currentKb, side: currentSide, fw: firmware, sn: serial, showTrainingButton: !!(firmware || serial) });
        }

        setFw(firmware); setSn(serial);
        setShowTrainingButton(!!(firmware || serial));
    }, []);

    return { layers, title, cols, kb, side, fw, sn, showTrainingButton };
};