// /home/coffee/my-keymap-viewer/src/hooks/useAlphaSymbolPractice2.ts
import { useMemo, useState, useEffect, useCallback, useRef } from 'react';
import { alphaSymbolPractice2Data } from '../data/keymapData'; // 練習データをインポート
import {
    PracticeHookProps,
    PracticeInputInfo,
    PracticeInputResult,
    PracticeHookResult,
    CharInfoAlphaSymbol2, // 型を AlphaSymbol2 に変更
    allAlphaSymbol2CharInfos, // データ配列を AlphaSymbol2 に変更
    PracticeHighlightResult,
    PracticeStatus,
} from './usePracticeCommons';

// Define stages for the practice mode
type AlphaSymbolPractice2Stage = 'initialSymbolKey' | 'targetSymbolChar';

const useAlphaSymbolPractice2 = ({ // フック名を変更
    isActive,
    gIdx, // 通常モード用に gIdx を受け取る
    dIdx, // 通常モード用に dIdx を受け取る
    layers,
    isRandomMode, // isRandomMode を受け取る
}: PracticeHookProps): PracticeHookResult => {
    const [status, setStatus] = useState<PracticeStatus>('idle');
    const [stage, setStage] = useState<AlphaSymbolPractice2Stage>('initialSymbolKey');
    const prevIsActiveRef = useRef(isActive);
    const prevGIdxRef = useRef(gIdx);
    const prevDIdxRef = useRef(dIdx);
    const prevIsRandomModeRef = useRef(isRandomMode);
    const [randomTargetInfo, setRandomTargetInfo] = useState<CharInfoAlphaSymbol2 | null>(null); // ランダムモード用

    // 通常モード用のターゲット情報を計算
    const normalModeTargetInfo = useMemo((): CharInfoAlphaSymbol2 | null => {
        console.log(`[AlphaSymbolPractice2 normalModeTargetInfo] Calculating. isActive: ${isActive}, isRandomMode: ${isRandomMode}, gIdx: ${gIdx}, dIdx: ${dIdx}`);
        if (isRandomMode || !isActive || gIdx === undefined || dIdx === undefined || gIdx < 0 || gIdx >= alphaSymbolPractice2Data.length) return null;
        const group = alphaSymbolPractice2Data[gIdx];
        if (!group || dIdx < 0 || dIdx >= group.chars.length) {
            console.log(`[AlphaSymbolPractice2 normalModeTargetInfo] Group not found or dIdx out of bounds. group: ${JSON.stringify(group)}, dIdx: ${dIdx}, group.chars.length: ${group?.chars?.length}`);
            return null;
        }
        const char = group.chars[dIdx];
        console.log(`[AlphaSymbolPractice2 normalModeTargetInfo] Selected char: "${char}" from group "${group.groupName}" at dIdx ${dIdx}`);
        return { type: 'alphaSymbol2', char, layerIndex: 10 }; // layerIndex は固定
    }, [isActive, isRandomMode, gIdx, dIdx]);

    // 現在の練習ターゲット情報を決定 (ランダムモードか通常モードかによる)
    const currentTargetInfo = useMemo(() => {
        return isRandomMode ? randomTargetInfo : normalModeTargetInfo;
    }, [isRandomMode, randomTargetInfo, normalModeTargetInfo]);

    // The actual symbol to be typed (e.g., '’', '"', ':', ';')
    const actualSymbolTarget = useMemo(() => {
        const char = currentTargetInfo?.char ?? null;
        console.log(`[AlphaSymbolPractice2 actualSymbolTarget] currentTargetInfo: ${JSON.stringify(currentTargetInfo)}, char: ${char}`);
        return char;
    }, [currentTargetInfo]);

    // ヘッダー文字 (固定)
    const headingChars = useMemo(() => {
        if (!isActive) return [];
        if (isRandomMode) {
            return currentTargetInfo ? [{ char: currentTargetInfo.char, isPracticeTarget: true }] : [];
        } else {
            // 通常モード時は現在のグループの全文字を表示し、ターゲットをマーク
            if (gIdx === undefined || gIdx < 0 || gIdx >= alphaSymbolPractice2Data.length) return [];
            const groupChars = alphaSymbolPractice2Data[gIdx].chars;
            const charsToDisplay = groupChars.map((char, index) => ({
                char,
                isPracticeTarget: index === dIdx
            }));

            // 0文字または1文字の場合はスペースを挿入する必要なし
            if (charsToDisplay.length <= 1) {
                return charsToDisplay;
            }

            // 各文字オブジェクトの間にスペースオブジェクトを挿入
            return charsToDisplay.flatMap((item, index) =>
                index < charsToDisplay.length - 1 ? [item, { char: '\u00A0', isPracticeTarget: false }] : [item]
            );
        }
    }, [isActive, isRandomMode, currentTargetInfo, gIdx, dIdx]);

    // Advance to the next symbol in the sequence
    const advanceToNextRandomSymbol = useCallback(() => {
        if (allAlphaSymbol2CharInfos.length > 0) {
            const randomIndex = Math.floor(Math.random() * allAlphaSymbol2CharInfos.length);
            const nextTarget = allAlphaSymbol2CharInfos[randomIndex];
            setRandomTargetInfo(nextTarget);
            console.log(`[AlphaSymbolPractice2 advanceToNextRandomSymbol] New random target selected: ${nextTarget.char}`);
        }
        setStage('initialSymbolKey'); // Always expect "記号" key first for the new symbol
        setStatus('waiting');
    }, [setRandomTargetInfo, setStage, setStatus]);

    // リセット関数
    const reset = useCallback(() => {
        setStage('initialSymbolKey');
        setStatus('idle');
        setRandomTargetInfo(null);
    }, [setStage, setStatus, setRandomTargetInfo]);

    // 初期化とアクティブ状態の監視
    useEffect(() => {
        console.log(`[AlphaSymbolPractice2 useEffect] START. isActive: ${isActive}, isRandomMode: ${isRandomMode}, gIdx: ${gIdx}, dIdx: ${dIdx}, prevIsActive: ${prevIsActiveRef.current}, prevGIdx: ${prevGIdxRef.current}, prevDIdx: ${prevDIdxRef.current}, prevIsRandomMode: ${prevIsRandomModeRef.current}, current status: ${status}`);
        if (!prevIsActiveRef.current && isActive) { // Just became active
            console.log(`[AlphaSymbolPractice2 useEffect] Just became active.`);
            reset(); // Reset first to clear any lingering state
            setStatus('waiting');
            setStage('initialSymbolKey');
            if (isRandomMode) {
                advanceToNextRandomSymbol();
            }
            // For normal mode, the target is derived from gIdx/dIdx, which should be correct on first activation.
        } else if (isActive) { // Already active, check for changes
            if (isRandomMode !== prevIsRandomModeRef.current) { // Mode changed
                console.log(`[AlphaSymbolPractice2 useEffect] Random mode changed from ${prevIsRandomModeRef.current} to ${isRandomMode}.`);
                setStage('initialSymbolKey');
                setStatus('waiting');
                if (isRandomMode) {
                    advanceToNextRandomSymbol();
                }
                // If switching from random to normal, normalModeTargetInfo will provide the target based on current gIdx/dIdx
            } else if (isRandomMode) { // Staying in random mode
                if (!randomTargetInfo && status !== 'running') { // If no random target and not already running a sequence for one
                    console.log(`[AlphaSymbolPractice2 useEffect] In random mode, no random target, advancing.`);
                    advanceToNextRandomSymbol(); // This also sets stage and status
                }
            } else { // Staying in normal mode (not random mode)
                if (gIdx !== prevGIdxRef.current || dIdx !== prevDIdxRef.current) {
                    console.log(`[AlphaSymbolPractice2 useEffect] Normal mode, gIdx/dIdx changed. Prev dIdx: ${prevDIdxRef.current}, New dIdx: ${dIdx}`);
                    setStage('initialSymbolKey');
                    setStatus('waiting');
                } else if (status === 'correct') {
                    // This case handles if status was 'correct' from previous input,
                    // and gIdx/dIdx didn't change (e.g. end of practice group but not advancing to next group yet)
                    // For AlphaSymbolPractice, after a correct sequence, it should wait for the next gIdx/dIdx.
                    // If gIdx/dIdx *has* changed, the above block handles it.
                    // If they haven't, and status is 'correct', it implies the sequence for the current char is done.
                    // The stage should have been reset by handleInput already if it was a successful sequence.
                    // If status is 'correct' but stage is 'targetSymbolChar', it means handleInput didn't reset stage (e.g. random mode).
                    // For normal mode, after 'correct' and shouldAdvance=true, handleInput resets stage.
                    // So, if status is 'correct' here, it means the UI might need to reflect 'waiting' for the *same* target if gIdx/dIdx didn't change.
                    // However, the primary driver for new targets in normal mode is gIdx/dIdx change.
                }
            }
        } else if (prevIsActiveRef.current && !isActive) { // Just became inactive
            console.log(`[AlphaSymbolPractice2 useEffect] Just became inactive.`);
            reset(); // Full reset including status to idle
        }

        prevIsActiveRef.current = isActive;
        prevGIdxRef.current = gIdx;
        prevDIdxRef.current = dIdx;
        prevIsRandomModeRef.current = isRandomMode;
        console.log(`[AlphaSymbolPractice2 useEffect] END. Current status: ${status}, stage: ${stage}`);
    }, [isActive, isRandomMode, gIdx, dIdx, reset, setStatus, advanceToNextRandomSymbol, randomTargetInfo, setStage, status]); // Added status to deps

    // 入力処理
    const handleInput = useCallback((inputInfo: PracticeInputInfo): PracticeInputResult => {
        if (!isActive || !actualSymbolTarget) {
            console.log(`[AlphaSymbolPractice2 handleInput] Bailing: isActive: ${isActive}, status: ${status}, actualSymbolTarget: ${actualSymbolTarget}`);
            return { isExpected: false, shouldGoToNext: false, errorLayer: null };

        }
        if (inputInfo.type !== 'release') {
            return { isExpected: false, shouldGoToNext: false, errorLayer: null };
        }

        const pressCode = inputInfo.pressCode; // 0-based
        const layer10 = layers[10] ?? []; // レイヤー10を参照
        const pressedKeyOnLayer10 = layer10[pressCode]?.trim();

        let isExpected = false;
        let nextStatus: PracticeStatus = status;
        let errorLayer: number | null = null;
        let isCorrectAndFinalInputForSymbol = false;

        if (stage === 'initialSymbolKey') {
            console.log(`[AlphaSymbolPractice2 handleInput initialSymbolKey] pressedKeyOnLayer10: "${pressedKeyOnLayer10}"`);
            if (pressedKeyOnLayer10 === '記号') { // レイヤー10の「記号」キー
                isExpected = true;
                setStage('targetSymbolChar');
                nextStatus = 'waiting';
            } else {
                isExpected = false;
                nextStatus = 'incorrect';
                errorLayer = 10; // エラーレイヤーは10
            }
        } else if (stage === 'targetSymbolChar') {
            console.log(`[AlphaSymbolPractice2 handleInput targetSymbolChar] pressedKeyOnLayer10: "${pressedKeyOnLayer10}" (type: ${typeof pressedKeyOnLayer10}), actualSymbolTarget: "${actualSymbolTarget}" (type: ${typeof actualSymbolTarget}), isRandomMode: ${isRandomMode}`);

            if (pressedKeyOnLayer10 && pressedKeyOnLayer10 === actualSymbolTarget) {
                isExpected = true;
                nextStatus = 'correct';
                isCorrectAndFinalInputForSymbol = true;
                console.log(`[AlphaSymbolPractice2 handleInput] Correct target symbol: ${actualSymbolTarget}.`);
                if (isRandomMode) {
                    console.log(`  [AlphaSymbolPractice2 handleInput] Random mode: Advancing to next random symbol.`);
                    advanceToNextRandomSymbol();
                } else {
                    console.log(`  [AlphaSymbolPractice2 handleInput] Normal mode: Signaling to advance (shouldGoToNext will be true).`);
                }
            } else if (pressedKeyOnLayer10 !== undefined) { // Pressed something, but it wasn't the target
                console.log(`[AlphaSymbolPractice2 handleInput targetSymbolChar] MISMATCH. pressedKeyOnLayer10: "${pressedKeyOnLayer10}" (type: ${typeof pressedKeyOnLayer10}), actualSymbolTarget: "${actualSymbolTarget}" (type: ${typeof actualSymbolTarget})`);
                isExpected = false; // Ensure isExpected is false for mismatch
                nextStatus = 'incorrect';
                errorLayer = 10; // エラーレイヤーは10
                setStage('initialSymbolKey'); // 不正解時は「記号」キー入力に戻す
            } else { // Nothing was pressed on layer 10 (e.g., key not in layer10 map)
                isExpected = false;
                nextStatus = 'incorrect';
                errorLayer = 10; // エラーレイヤーは10
                setStage('initialSymbolKey'); // 不正解時は「記号」キー入力に戻す
            }
        }

        setStatus(nextStatus);

        // This is the crucial part for normal mode advancement
        const shouldAdvance = !isRandomMode && isCorrectAndFinalInputForSymbol;
        console.log(`[AlphaSymbolPractice2 handleInput DEBUG] Final check: isRandomMode: ${isRandomMode}, isCorrectAndFinalInputForSymbol: ${isCorrectAndFinalInputForSymbol}, calculated shouldAdvance: ${shouldAdvance}`);
        if (shouldAdvance) {
            console.log(`[AlphaSymbolPractice2 handleInput] Setting stage to initialSymbolKey for next char.`);
            setStage('initialSymbolKey'); // Reset stage for the next symbol in normal mode
        }

        return { isExpected, shouldGoToNext: shouldAdvance, errorLayer };
    }, [
        isActive,
        layers,
        actualSymbolTarget,
        stage,
        setStatus,
        advanceToNextRandomSymbol,
        status,
        isRandomMode, setStage
    ]);

    // ハイライト処理
    const getHighlightClassName = useCallback((keyName: string, layoutIndex: number): PracticeHighlightResult => {
        const noHighlight: PracticeHighlightResult = { className: null, overrideKey: null };
        if (!isActive) {
            return noHighlight;
        }

        if (layoutIndex === 10) { // レイヤー10を対象
            const trimmedKeyName = keyName.trim();
            if (stage === 'initialSymbolKey' && trimmedKeyName === '記号') {
                return { className: 'bg-blue-100', overrideKey: null };
            }
            if (stage === 'targetSymbolChar' && actualSymbolTarget && trimmedKeyName === actualSymbolTarget) {
                return { className: 'bg-blue-100', overrideKey: null };
            }
        }
        return noHighlight;
    }, [isActive, stage, actualSymbolTarget]);

    const isInvalidInputTarget = useCallback((pressCode: number, layoutIndex: number, keyIndex: number): boolean => {
        if (!isActive || status !== 'incorrect') return false;
        return layoutIndex === 10 && keyIndex === pressCode; // レイヤー10で判定
    }, [isActive, status]);

    const clearErrorState = useCallback(() => {
        setStatus('waiting');
    }, [setStatus]);

    return {
        headingChars,
        handleInput,
        getHighlightClassName,
        reset,
        status, // status を返す
        targetChar: actualSymbolTarget ?? undefined, // ヘッダーハイライト用には常に実際のターゲット記号を渡す
        errorHighlightLayer: status === 'incorrect' ? 10 : null,
        isInvalidInputTarget,
        clearErrorState,
        targetLayerIndex: 10, // 常にレイヤー10をターゲットとする
        targetLayerIndices: [10], // 表示するレイヤーもレイヤー10のみ
    };
};

export default useAlphaSymbolPractice2;
