// /home/coffee/my-keymap-viewer/src/hooks/useYouhandakuonPractice.ts
import { useCallback, useEffect, useMemo, useRef, useState } from 'react';
import {
    PracticeHookProps,
    PracticeMode, PracticeHookResult, PracticeStatus, PracticeInputInfo,
    PracticeHighlightResult, PracticeInputResult
} from './usePracticeCommons';
import {
    youhandakuonPracticeData, youhandakuonGyouChars, youhandakuonDanMapping,
    youonGyouList,
    danOrder,
} from '../data/keymapData';
import { getKeyFromKeymap } from '../utils/getKeyFromKeymap';
import {
    CharInfoYouhandakuon,
    allYouhandakuonCharInfos,
    HeadingCharDisplayInfo,
} from './usePracticeCommons';

type YouhandakuonStage = 'initial' | 'waitingForYouonKey' | 'waitingForFirstDakuonKey' | 'waitingForSecondDakuonKey' | 'waitingForDanKey' | 'stageCompleted';

const defaultTargetLayerIndex = 2;

interface YouhandakuonPracticeProps extends PracticeHookProps {
    practice: PracticeMode;
}

export const useYouhandakuonPractice = ({
    practice,
    gIdx,
    dIdx,
    isActive,
    isRandomMode,
    onAdvance,
    layers: keymap,
}: YouhandakuonPracticeProps): PracticeHookResult => {
    const [status, setStatus] = useState<PracticeStatus>('waiting');
    const [normalModeTargetChar, setNormalModeTargetChar] = useState<string | null>(null);
    const [targetLayerIndex, setTargetLayerIndex] = useState<number>(defaultTargetLayerIndex);
    const [targetLayerIndices, setTargetLayerIndices] = useState<number[]>([defaultTargetLayerIndex, defaultTargetLayerIndex + 1]);
    const [errorHighlightLayer, setErrorHighlightLayer] = useState<number | null>(null);
    const [internalStage, setInternalStage] = useState<YouhandakuonStage>('initial');
    const selectedGyouKeyRef = useRef<string | null>(null);
    const selectedYouonKeyRef = useRef<string | null>(null);
    const selectedDanKeyRef = useRef<string | null>(null);
    // const currentExpectedKeysRef = useRef<string[]>([]); // Not actively used for logic
    const [isBlinkingDakuon, setIsBlinkingDakuon] = useState(false); // For blinking dakuon key highlight
    const blinkTimeoutRef = useRef<number | null>(null);

    const prevIsActiveRef = useRef(isActive);
    const prevIsRandomModeRef = useRef(isRandomMode);
    const prevGIdxRef = useRef(gIdx);
    const prevDIdxRef = useRef(dIdx);
    const isInitialMountRef = useRef(true);

    const [randomYouhandakuonTarget, setRandomYouhandakuonTarget] = useState<CharInfoYouhandakuon | null>(null);

    const getTargetInfoNormalMode = useCallback(() => {
        const practiceGroup = youhandakuonPracticeData[0];
        if (!practiceGroup || dIdx < 0 || dIdx >= practiceGroup.inputs.length) {
            return null;
        }
        const inputDef = practiceGroup.inputs[dIdx];
        const char = practiceGroup.chars[dIdx];
        return { char, inputDef, currentGyou: 'ぱ行' as const };
    }, [dIdx]);

    const isGyouKey = (pressCode: number, layerIndex: number): boolean => {
        const keyLabel = getKeyFromKeymap({ layers: keymap }, layerIndex, pressCode);
        return youonGyouList.includes(keyLabel ?? '');
    };
    const isYouonKey = (pressCode: number, layerIndex: number): boolean => {
        const keyLabel = getKeyFromKeymap({ layers: keymap }, layerIndex, pressCode);
        return keyLabel === 'ゃゅょ';
    };
    const isDakuonKey = (pressCode: number, layerIndex: number): boolean => {
        const keyLabel = getKeyFromKeymap({ layers: keymap }, layerIndex, pressCode);
        return keyLabel === '” ° 小';
    };
    const isDanKey = (pressCode: number, layerIndex: number, expectedDan: string): boolean => {
        const keyLabel = getKeyFromKeymap({ layers: keymap }, layerIndex, pressCode);
        return keyLabel === expectedDan;
    };
    const isCancelKey = (pressCode: number, layerIndex: number): boolean => {
        const keyLabel = getKeyFromKeymap({ layers: keymap }, layerIndex, pressCode);
        return keyLabel === 'BS' || keyLabel === 'ENT';
    };

    const determineErrorLayer = (inputLayerIndex: number): number => {
        if (internalStage === 'waitingForDanKey' && inputLayerIndex !== targetLayerIndices[1]) {
            return targetLayerIndices[1];
        }
        return inputLayerIndex;
    };

    const resetPracticeState = useCallback(() => {
        setInternalStage('initial');
        selectedGyouKeyRef.current = null;
        selectedYouonKeyRef.current = null;
        selectedDanKeyRef.current = null;
        setStatus('waiting');
        setTargetLayerIndex(defaultTargetLayerIndex);
        setTargetLayerIndices([defaultTargetLayerIndex, defaultTargetLayerIndex + 1]);
        setErrorHighlightLayer(null);
        setIsBlinkingDakuon(false); // Reset blinking state
        if (blinkTimeoutRef.current !== null) {
            clearTimeout(blinkTimeoutRef.current);
            blinkTimeoutRef.current = null;
        }
    }, [setIsBlinkingDakuon]);

    const selectNextRandomYouhandakuonTarget = useCallback(() => {
        if (allYouhandakuonCharInfos.length > 0) {
            const randomIndex = Math.floor(Math.random() * allYouhandakuonCharInfos.length);
            const nextTarget = allYouhandakuonCharInfos[randomIndex];
            setRandomYouhandakuonTarget(nextTarget);
            resetPracticeState();
        } else {
            setRandomYouhandakuonTarget(null);
            resetPracticeState();
        }
    }, [setRandomYouhandakuonTarget, resetPracticeState]);

    const currentInputDef = useMemo(() => {
        if (isRandomMode) {
            return randomYouhandakuonTarget?.inputDef ?? null;
        }
        return getTargetInfoNormalMode()?.inputDef ?? null;
    }, [isRandomMode, randomYouhandakuonTarget, getTargetInfoNormalMode]);

    const reset = useCallback(() => {
        resetPracticeState();
        setRandomYouhandakuonTarget(null);
        setNormalModeTargetChar(null);
        prevGIdxRef.current = -1;
        prevDIdxRef.current = -1;
        isInitialMountRef.current = true;
        prevIsRandomModeRef.current = isRandomMode;
    }, [resetPracticeState, setRandomYouhandakuonTarget, isRandomMode]);

    useEffect(() => {
        const dIdxChanged = dIdx !== prevDIdxRef.current;
        const activeChanged = isActive !== prevIsActiveRef.current;
        const randomModeChanged = isRandomMode !== prevIsRandomModeRef.current;

        if (activeChanged && !isActive) {
            reset();
        } else if (isActive) {
            const conditionForReset = isInitialMountRef.current || (activeChanged && isActive) || randomModeChanged || (!isRandomMode && dIdxChanged);
            if (conditionForReset) {
                resetPracticeState();
                if (isRandomMode) {
                    if (!randomYouhandakuonTarget || randomModeChanged) {
                        selectNextRandomYouhandakuonTarget();
                    }
                    setNormalModeTargetChar(null); 
                } else {
                    const currentTargetInfo = getTargetInfoNormalMode();
                    setNormalModeTargetChar(currentTargetInfo?.char ?? null);
                    setRandomYouhandakuonTarget(null);
                }
            }
        }

        prevIsActiveRef.current = isActive;
        prevIsRandomModeRef.current = isRandomMode;
        prevGIdxRef.current = gIdx;
        prevDIdxRef.current = dIdx;
        if (isInitialMountRef.current && isActive) {
            isInitialMountRef.current = false;
        }

        return () => {
            if (blinkTimeoutRef.current !== null) {
                clearTimeout(blinkTimeoutRef.current);
                blinkTimeoutRef.current = null;
            }
        };
    }, [isActive, isRandomMode, gIdx, dIdx, reset, getTargetInfoNormalMode, selectNextRandomYouhandakuonTarget, randomYouhandakuonTarget, resetPracticeState]);

    useEffect(() => {
        if (isRandomMode && randomYouhandakuonTarget) {
            resetPracticeState();
        }
    }, [isRandomMode, randomYouhandakuonTarget, resetPracticeState]);

    const handleInput = useCallback((inputInfo: PracticeInputInfo): PracticeInputResult => {
        if (inputInfo.type !== 'release' || !isActive || internalStage === 'stageCompleted') {
            return { isExpected: false, shouldGoToNext: false, errorLayer: null };
        }
        setErrorHighlightLayer(null);

        if (!currentInputDef) return { isExpected: false, shouldGoToNext: false, errorLayer: null };
        const inputDef = currentInputDef;

        let layerForInputEvaluation = defaultTargetLayerIndex;
        if (internalStage === 'waitingForDanKey') {
            layerForInputEvaluation = defaultTargetLayerIndex + 1;
        }
        // console.log(`[Youhandakuon handleInput] Calling getKeyFromKeymap with layer: ${layerForInputEvaluation}, pressCode: ${inputInfo.pressCode}`);
        const inputKey = getKeyFromKeymap({ layers: keymap }, layerForInputEvaluation, inputInfo.pressCode);

        if (internalStage === 'initial') {
            if (isGyouKey(inputInfo.pressCode, layerForInputEvaluation) && inputKey === inputDef.gyouKey) {
                selectedGyouKeyRef.current = inputKey ?? null;
                setInternalStage('waitingForYouonKey');
                // console.log(`[Youhandakuon handleInput] Stage: initial -> waitingForYouonKey. Target: ゃゅょ.`);
                return { isExpected: true, shouldGoToNext: false };
            } else if (isCancelKey(inputInfo.pressCode, inputInfo.layerIndex)) {
                reset();
                return { isExpected: true, shouldGoToNext: false };
            }
        } else if (internalStage === 'waitingForYouonKey') {
            if (isYouonKey(inputInfo.pressCode, layerForInputEvaluation)) {
                selectedYouonKeyRef.current = inputKey ?? null;
                setInternalStage('waitingForFirstDakuonKey');
                // console.log(`[Youhandakuon handleInput] Stage: waitingForYouonKey -> waitingForFirstDakuonKey. Target: 濁音.`);
                return { isExpected: true, shouldGoToNext: false };
            } else if (isCancelKey(inputInfo.pressCode, inputInfo.layerIndex)) {
                setInternalStage('initial');
                selectedYouonKeyRef.current = null;
                return { isExpected: true, shouldGoToNext: false };
            }
        } else if (internalStage === 'waitingForFirstDakuonKey') {
            if (isDakuonKey(inputInfo.pressCode, layerForInputEvaluation)) {
                setInternalStage('waitingForSecondDakuonKey');
                // console.log(`[Youhandakuon handleInput] Stage: waitingForFirstDakuonKey -> waitingForSecondDakuonKey. Target: 濁音.`);
                setIsBlinkingDakuon(true);
                if (blinkTimeoutRef.current) clearTimeout(blinkTimeoutRef.current);
                blinkTimeoutRef.current = window.setTimeout(() => {
                    setIsBlinkingDakuon(false);
                    blinkTimeoutRef.current = null;
                }, 300);
                return { isExpected: true, shouldGoToNext: false };
            } else if (isCancelKey(inputInfo.pressCode, inputInfo.layerIndex)) {
                setInternalStage('waitingForYouonKey');
                setIsBlinkingDakuon(false);
                if (blinkTimeoutRef.current !== null) {
                    clearTimeout(blinkTimeoutRef.current);
                    blinkTimeoutRef.current = null;
                }
                return { isExpected: true, shouldGoToNext: false };
            }
        } else if (internalStage === 'waitingForSecondDakuonKey') {
            if (isDakuonKey(inputInfo.pressCode, layerForInputEvaluation)) {
                setInternalStage('waitingForDanKey');
                // console.log(`[Youhandakuon handleInput] Stage: waitingForSecondDakuonKey -> waitingForDanKey. Target: ${inputDef.dan}.`);
                setTargetLayerIndex(defaultTargetLayerIndex + 1);
                setIsBlinkingDakuon(false); // Ensure blinking is stopped
                if (blinkTimeoutRef.current) { // Clear any pending timer
                    clearTimeout(blinkTimeoutRef.current);
                    blinkTimeoutRef.current = null;
                }
                return { isExpected: true, shouldGoToNext: false };
            } else if (isCancelKey(inputInfo.pressCode, inputInfo.layerIndex)) {
                setInternalStage('waitingForFirstDakuonKey');
                setTargetLayerIndex(defaultTargetLayerIndex);
                setIsBlinkingDakuon(false); // Blinking should stop if going back
                if (blinkTimeoutRef.current !== null) {
                    clearTimeout(blinkTimeoutRef.current);
                    blinkTimeoutRef.current = null;
                }
                return { isExpected: true, shouldGoToNext: false };
            }
        } else if (internalStage === 'waitingForDanKey') {
            if (isDanKey(inputInfo.pressCode, layerForInputEvaluation, inputDef.dan)) {
                setStatus('correct');
                if (isRandomMode) {
                    selectNextRandomYouhandakuonTarget();
                    return { isExpected: true, shouldGoToNext: false };
                } else {
                    setStatus('correct');
                    setInternalStage('stageCompleted');
                    return { isExpected: true, shouldGoToNext: true };
                }
            } else if (isCancelKey(inputInfo.pressCode, inputInfo.layerIndex)) {
                setInternalStage('waitingForSecondDakuonKey');
                setTargetLayerIndex(defaultTargetLayerIndex);
                setIsBlinkingDakuon(true); // Re-initiate blinking for the second dakuon key
                if (blinkTimeoutRef.current) clearTimeout(blinkTimeoutRef.current);
                blinkTimeoutRef.current = window.setTimeout(() => {
                    setIsBlinkingDakuon(false);
                    blinkTimeoutRef.current = null;
                }, 300);
                return { isExpected: true, shouldGoToNext: false };
            } else if (isDakuonKey(inputInfo.pressCode, layerForInputEvaluation)) {
                return { isExpected: true, shouldGoToNext: false };
            }
        }

        setStatus('incorrect');
        const errLayer = determineErrorLayer(layerForInputEvaluation);
        setErrorHighlightLayer(errLayer);
        setInternalStage('initial');
        selectedGyouKeyRef.current = null;
        selectedYouonKeyRef.current = null;
        setTargetLayerIndex(defaultTargetLayerIndex);
        setTargetLayerIndices([defaultTargetLayerIndex, defaultTargetLayerIndex + 1]);
        setIsBlinkingDakuon(false);
        if (blinkTimeoutRef.current !== null) {
            clearTimeout(blinkTimeoutRef.current);
            blinkTimeoutRef.current = null;
        }
        return {
            isExpected: false,
            errorLayer: errLayer,
            shouldGoToNext: false,
        };
    }, [
        isActive, keymap, resetPracticeState, onAdvance, targetLayerIndices, currentInputDef, isRandomMode, 
        selectNextRandomYouhandakuonTarget, internalStage, setInternalStage, setStatus, setErrorHighlightLayer, 
        setTargetLayerIndex, setTargetLayerIndices, determineErrorLayer, reset, setIsBlinkingDakuon
    ]);

    const finalTargetChar = useMemo(() => {
        return isRandomMode ? (randomYouhandakuonTarget?.char ?? null) : normalModeTargetChar;
    }, [isRandomMode, randomYouhandakuonTarget, normalModeTargetChar]);

    const headingChars = useMemo((): HeadingCharDisplayInfo[] => {
        if (!isActive) return [];
        const practiceGroup = youhandakuonPracticeData[0];
        if (!practiceGroup) return [];
        const displayableChars = practiceGroup.chars;
        return displayableChars.map((char: string) => ({
            char: char,
            isPracticeTarget: false, 
        }));
    }, [isActive]);

    const getHighlightClassName = useCallback((keyName: string, layer: number): PracticeHighlightResult => { // Removed keyIndex from signature
        const noHighlight: PracticeHighlightResult = { className: null, overrideKey: null };
        if (!isActive || !currentInputDef) {
            return noHighlight;
        }
        const inputDef = currentInputDef;
        // console.log(`[Youhandakuon getHighlightClassName EVAL] Key: "${keyName}", Layer: ${layer}, Stage: ${internalStage}, isBlinkingDakuon: ${isBlinkingDakuon}, TargetGyou: "${inputDef?.gyouKey}", TargetDan: "${inputDef?.dan}"`);

        let expectedKeyToHighlight: string | null = null;
        let expectedLayerForHighlight: number | null = null;
        let applyHighlight = true;

        switch (internalStage) {
            case 'initial':
                expectedKeyToHighlight = inputDef.gyouKey;
                expectedLayerForHighlight = defaultTargetLayerIndex;
                break;
            case 'waitingForYouonKey':
                expectedKeyToHighlight = 'ゃゅょ';
                expectedLayerForHighlight = defaultTargetLayerIndex;
                break;
            case 'waitingForFirstDakuonKey':
                expectedKeyToHighlight = '” ° 小';
                expectedLayerForHighlight = defaultTargetLayerIndex;
                break;
            case 'waitingForSecondDakuonKey':
                expectedKeyToHighlight = '” ° 小';
                expectedLayerForHighlight = defaultTargetLayerIndex;
                if (keyName === '” ° 小' && isBlinkingDakuon) applyHighlight = false;
                break;
            case 'waitingForDanKey':
                expectedKeyToHighlight = inputDef.dan;
                expectedLayerForHighlight = defaultTargetLayerIndex + 1;
                break;
            default:
                return noHighlight;
        }

        if (applyHighlight && keyName === expectedKeyToHighlight && layer === expectedLayerForHighlight) {
            return { className: 'bg-blue-100 dark:bg-blue-800', overrideKey: null };
        }
        return noHighlight;
    }, [isActive, currentInputDef, internalStage, isBlinkingDakuon]); // keyIndex is not used in logic, so not a dependency

    const isInvalidInputTarget = useCallback((targetPressCode: number, currentLayoutIndex: number, currentKeyIndex: number): boolean => {
        if (!isActive || errorHighlightLayer === null) return false;
        return currentLayoutIndex === errorHighlightLayer && currentKeyIndex === targetPressCode;
    }, [isActive, errorHighlightLayer]);

    const clearErrorState = useCallback(() => {
        setErrorHighlightLayer(null);
        setStatus('waiting');
    }, [setErrorHighlightLayer, setStatus]);

    return {
        status,
        targetChar: finalTargetChar,
        currentTarget: currentInputDef ? { 
            type: 'youhandakuon', 
            char: finalTargetChar ?? '',
            inputDef: currentInputDef 
        } : undefined,
        headingChars,
        targetLayerIndex,
        targetLayerIndices,
        errorHighlightLayer,
        handleInput,
        getHighlightClassName,
        reset: resetPracticeState, // Use the internal reset function
        isInvalidInputTarget,
        countdownValue: undefined,
        challengeTimer: undefined,
        challengeTotalTime: undefined,
        challengeResults: null,
        capitalizedCharIndices: new Set(),
        typedEndIndex: 0,
        clearErrorState,
        stage: internalStage,
    };
};
