// /home/coffee/my-keymap-viewer/src/hooks/useCapsLockPractice.ts
import { useState, useCallback, useMemo, useEffect, useRef } from 'react';
import {
    PracticeHookProps,
    PracticeHookResult,
    PracticeInputInfo,
    PracticeInputResult as HandleInputResult,
    PracticeStatus,
    PracticeHighlightResult,
} from './usePracticeCommons';
import { alphabetPracticeGroups, CAPS_LOCK_KEY_NAME, CAPS_LOCK_LAYER_INDEX } from '../data/keymapData'; // CapsLockキー名とレイヤーインデックス

// Define the type for internalStage explicitly
type CapsLockPracticeStage =
    'groupInput' | 'charInput' | 'finished';

const useCapsLockPractice = ({
    gIdx,
    dIdx,
    isActive,
    onAdvance,
    layers,
}: PracticeHookProps): PracticeHookResult => {
    const [internalStage, setInternalStage] = useState<CapsLockPracticeStage>('groupInput');
    const [currentTargetGroup, setCurrentTargetGroup] = useState<string>(''); // 例: "abc"
    const [isCapsLockOn, setIsCapsLockOn] = useState(false); // CapsLockの状態
    const [status, setStatus] = useState<PracticeStatus>('waiting');
    const [errorHighlightDetails, setErrorHighlightDetails] = useState<{ layer: number; pressCode: number } | null>(null);

    const prevIsActiveRef = useRef(isActive);
    const prevGIdxRef = useRef(gIdx);
    const prevDIdxRef = useRef(dIdx);

    const reset = useCallback(() => {
        console.log('[CapsLockPractice reset]');
        setInternalStage('groupInput');
        setCurrentTargetGroup('');
        setIsCapsLockOn(false);
        setStatus('waiting');
        setErrorHighlightDetails(null);
        prevGIdxRef.current = -1;
        prevDIdxRef.current = -1;
    }, [setInternalStage, setCurrentTargetGroup, setStatus, setIsCapsLockOn, setErrorHighlightDetails]);

    useEffect(() => {
        console.log(`[CapsLockPractice useEffect] START. isActive: ${isActive}, gIdx: ${gIdx}, dIdx: ${dIdx}, prevGIdx: ${prevGIdxRef.current}, prevDIdx: ${prevDIdxRef.current}, internalStage: ${internalStage}, currentTargetGroup: "${currentTargetGroup}", isCapsLockOn: ${isCapsLockOn}`);
        const justActivated = isActive && !prevIsActiveRef.current;

        if (!isActive && prevIsActiveRef.current) {
            console.log('[CapsLockPractice useEffect] Practice became inactive. Calling reset().');
            reset();
        } else if (isActive) {
            if (gIdx < alphabetPracticeGroups.length) {
                const group = alphabetPracticeGroups[gIdx];
                if (group !== currentTargetGroup) {
                    setCurrentTargetGroup(group);
                }

                // gIdx または dIdx が変更された場合、またはアクティブになった直後にステージをリセット
                if (justActivated || gIdx !== prevGIdxRef.current || dIdx !== prevDIdxRef.current) {
                    console.log(`[CapsLockPractice useEffect] Resetting stage. Reason: justActivated=${justActivated}, gIdx changed from ${prevGIdxRef.current} to ${gIdx}, dIdx changed from ${prevDIdxRef.current} to ${dIdx}.`);
                    setInternalStage('groupInput'); // 常にグループキー入力待ちから開始
                    // isCapsLockOn はここではリセットしない (練習の途中で gIdx/dIdx が変わることは通常ない想定)
                    setStatus('waiting');
                    setErrorHighlightDetails(null); // ステージリセット時にエラーもクリア
                }
            } else {
                setStatus('finished');
                setInternalStage('finished');
                setCurrentTargetGroup('');
            }
        }
        prevIsActiveRef.current = isActive;
        prevGIdxRef.current = gIdx;
        prevDIdxRef.current = dIdx;
        console.log(`[CapsLockPractice useEffect] END. Updated refs. currentTargetGroup: "${currentTargetGroup}", internalStage: ${internalStage}, isCapsLockOn: ${isCapsLockOn}, status: ${status}, errorDetails:`, errorHighlightDetails);
    }, [isActive, gIdx, dIdx, alphabetPracticeGroups, reset, currentTargetGroup, internalStage, errorHighlightDetails]);

    const handleInput = useCallback((input: PracticeInputInfo): HandleInputResult => {
        console.log(`[CapsLockPractice handleInput] CALLED. isActive: ${isActive}, internalStage: ${internalStage}, currentTargetGroup: "${currentTargetGroup}", gIdx: ${gIdx}, dIdx: ${dIdx}, isCapsLockOn: ${isCapsLockOn}`);
        console.log(`[CapsLockPractice handleInput] Input details: type=${input.type}, pressCode=0x${input.pressCode.toString(16)}`);
        if (!isActive || internalStage === 'finished' || !currentTargetGroup || !layers) {
            console.log(`[CapsLockPractice handleInput] PRE-CONDITION FAILED.`);
            return { isExpected: false, shouldGoToNext: false };
        }

        if (input.type !== 'release') {
            return { isExpected: false, shouldGoToNext: false };
        }

        let currentErrorLayer: number | null = null;
        let currentErrorPressCode: number | null = null;
        let nextInternalStage: CapsLockPracticeStage = internalStage; // 次のステージを保持する変数

        let pressedKeyNameOnLayer4: string | null = null;
        if (layers[CAPS_LOCK_LAYER_INDEX] && input.pressCode >= 0 && input.pressCode < layers[CAPS_LOCK_LAYER_INDEX].length) {
            pressedKeyNameOnLayer4 = layers[CAPS_LOCK_LAYER_INDEX][input.pressCode]?.trim();
            console.log(`[CapsLockPractice handleInput] Key on CAPS_LOCK_LAYER_INDEX (idx ${CAPS_LOCK_LAYER_INDEX}, pressCode ${input.pressCode}): "${pressedKeyNameOnLayer4}" (trimmed). Comparing with CAPS_LOCK_KEY_NAME: "${CAPS_LOCK_KEY_NAME}"`);
        }

        // CapsLockキー自体の操作 (どのステージでも反応する)
        if (pressedKeyNameOnLayer4 === CAPS_LOCK_KEY_NAME) {
            console.log(`[CapsLockPractice handleInput] CAPS_LOCK_KEY_NAME MATCHED! Event type: ${input.type}`);
            if (input.type === 'release') { // release時にトグル
                console.log('[CapsLockPractice handleInput] Toggling isCapsLockOn.');
                setIsCapsLockOn(prev => !prev);
                // setStatus('correct'); // CapsLockキー操作自体は練習項目の正解ではないため、statusを変更しない
                // setErrorHighlightDetails(null); // 文字入力に関するエラー表示をクリアしない
                // CapsLockキー操作自体では練習は進めない
                // CapsLockキー操作自体は「不正解」ではないため isExpected: true を返す
                return { isExpected: true, shouldGoToNext: false, errorLayer: null, errorPressCode: undefined };
            }
            return { isExpected: true, shouldGoToNext: false, errorLayer: null, errorPressCode: undefined }; // press イベントは無視
        }

        // 通常の文字入力処理 (ステージによって分岐)
        if (internalStage === 'groupInput') {
            if (pressedKeyNameOnLayer4 && pressedKeyNameOnLayer4.toLowerCase() === currentTargetGroup.toLowerCase()) {
                console.log('[CapsLockPractice handleInput] Group key MATCH. Setting stage to charInput.');
                setStatus('waiting'); // グループキー正解後、文字キー入力を待つため 'waiting' に設定
                nextInternalStage = 'charInput'; // ステージ変更
                setErrorHighlightDetails(null);
                setInternalStage(nextInternalStage); // 内部ステージを更新
                // グループキー入力は練習項目完了ではないが、不正解でもないため isExpected: true を返す。
                // shouldGoToNext: false により、App.tsx は dIdx を進めない。
                return { isExpected: true, shouldGoToNext: false, errorLayer: null, errorPressCode: undefined };
            } // グループキーが違う場合はこの後の不正解処理へ
        } else if (internalStage === 'charInput') {
            const charInputKeyNames = ["①", "②", "③", "④", "⑤", "⑥", "⑦", "⑧", "⑨", "⑩", "⑪", "⑫", "⑬", "⑭", "⑮", "⑯", "⑰", "⑱", "⑲", "⑳"];
            const expectedCharKeyName = (dIdx < charInputKeyNames.length) ? charInputKeyNames[dIdx] : null;
            if (layers[5] && expectedCharKeyName) { // レイヤー5の存在チェック
                const pressedKeyNameOnLayer5 = layers[5][input.pressCode]?.trim();
                if (pressedKeyNameOnLayer5 === expectedCharKeyName) {
                    console.log('[CapsLockPractice handleInput] Char key MATCH. Calling onAdvance.');
                    setStatus('correct');
                    onAdvance?.(); // dIdx を進める
                    setErrorHighlightDetails(null);
                    // 次のステージは useEffect で dIdx の変更を検知して groupInput に設定される
                    // ここで明示的に groupInput に戻しても良い
                    nextInternalStage = 'groupInput';
                    setInternalStage(nextInternalStage); // 内部ステージを更新
                    // 文字キー入力成功は練習項目完了だが、進行管理はフック自身で行うため shouldGoToNext: false を返す
                    return { isExpected: true, shouldGoToNext: false, errorLayer: null, errorPressCode: undefined };
                } else {
                    // 文字キーが違う場合
                    console.log('[CapsLockPractice handleInput] Char key NO MATCH. Setting error and returning to groupInput.');
                    currentErrorLayer = 5;
                    currentErrorPressCode = input.pressCode;
                    nextInternalStage = 'groupInput';
                }
            } else { // レイヤー5がない、または期待する文字キー名がない (通常はありえない)
                console.log('[CapsLockPractice handleInput] Layer 5 or expectedCharKeyName missing. Setting error and returning to groupInput.');
                currentErrorLayer = 5; // とりあえずレイヤー5でエラーとしておく
                currentErrorPressCode = input.pressCode;
                nextInternalStage = 'groupInput';
            }
        }

        // 上記のどの条件にも一致しない場合 (またはグループキー/文字キーが不正解だった場合)
        if (internalStage === 'groupInput' && !currentErrorLayer) { // グループキー入力ステージで、まだエラーが設定されていなければ
            currentErrorLayer = CAPS_LOCK_LAYER_INDEX; // レイヤー4でエラー
            currentErrorPressCode = input.pressCode;
            // nextInternalStage は 'groupInput' のまま
        }

        console.log('[CapsLockPractice handleInput] NO MATCH or error occurred.');
        setStatus('incorrect');
        setErrorHighlightDetails(currentErrorLayer !== null && currentErrorPressCode !== null ? { layer: currentErrorLayer, pressCode: currentErrorPressCode } : null);
        if (nextInternalStage !== internalStage) { // ステージが変更された場合のみ更新
            setInternalStage(nextInternalStage);
        }
        return { isExpected: false, shouldGoToNext: false, errorLayer: currentErrorLayer, errorPressCode: currentErrorPressCode ?? undefined };
    }, [isActive, internalStage, currentTargetGroup, gIdx, dIdx, onAdvance, layers, setIsCapsLockOn, isCapsLockOn, setErrorHighlightDetails, setInternalStage, setStatus]);

    const getHighlightClassName = useCallback((keyName: string, layoutIndex: number, keyIndex: number): PracticeHighlightResult => { // Added keyIndex
        if (!isActive || internalStage === 'finished' || !currentTargetGroup || (errorHighlightDetails && errorHighlightDetails.layer === layoutIndex)) {
            return { className: null, overrideKey: null };
        }
        const currentGroupKeyNameFromState = currentTargetGroup;

        if (internalStage === 'groupInput') {
            if (layoutIndex === CAPS_LOCK_LAYER_INDEX && keyName.trim().toLowerCase() === currentGroupKeyNameFromState.toLowerCase()) {
                return { className: 'bg-blue-100', overrideKey: null };
            }
        } else if (internalStage === 'charInput') {
            // 文字入力ステージでは、レイヤー5の個別文字キーをハイライト
            // ただし、isCapsLockOn の状態によって表示する文字を変える (これは headingChars で対応)
            const charInputKeyNames = ["①", "②", "③", "④", "⑤", "⑥", "⑦", "⑧", "⑨", "⑩", "⑪", "⑫", "⑬", "⑭", "⑮", "⑯", "⑰", "⑱", "⑲", "⑳"];
            const expectedCharKeyName = (dIdx < charInputKeyNames.length) ? charInputKeyNames[dIdx] : null;
            if (layoutIndex === 5 && keyName.trim() === expectedCharKeyName) {
                return { className: 'bg-blue-100', overrideKey: null };
            }
        }
        return { className: null, overrideKey: null }; // keyIndex is not used in logic, so not a dependency
    }, [isActive, internalStage, currentTargetGroup, dIdx, isCapsLockOn, errorHighlightDetails]);

    const headingChars = useMemo(() => {
        if (gIdx < alphabetPracticeGroups.length) {
            const group = alphabetPracticeGroups[gIdx]; // "abc"
            // CapsLock ON なら大文字、OFF なら小文字で表示
            return isCapsLockOn ? [group.toUpperCase()] : [group.toLowerCase()];
        }
        return [];
    }, [gIdx, alphabetPracticeGroups, isCapsLockOn]); // isCapsLockOn を依存配列に追加

    const targetChar = useMemo(() => {
        if (currentTargetGroup && dIdx < currentTargetGroup.length) {
            const char = currentTargetGroup[dIdx];
            return isCapsLockOn ? char.toUpperCase() : char.toLowerCase();
        }
        return undefined;
    }, [currentTargetGroup, dIdx, isCapsLockOn]); // isCapsLockOn を依存配列に追加

    const clearErrorState = useCallback(() => {
        setErrorHighlightDetails(null);
        setStatus('waiting'); // エラークリア時は waiting に戻す
    }, [setErrorHighlightDetails, setStatus]);

    return {
        status,
        handleInput,
        getHighlightClassName,
        targetChar,
        reset,
        targetLayerIndex: CAPS_LOCK_LAYER_INDEX, // CapsLockキーはレイヤー4にあると仮定
        headingChars,
        isCapsLockOn,
        targetLayerIndices: [4, 5],
        isInvalidInputTarget: (targetPressCode, currentLayoutIndex, currentKeyIndex, pressedLayerIndex) => {
            if (!isActive || !errorHighlightDetails) return false;
            // App.tsxから渡される pressedLayerIndex はエラーが発生したレイヤーのはず
            // errorHighlightDetails.layer もエラーが発生したレイヤー
            // targetPressCode はエラーとなったキーの0-based index
            return currentLayoutIndex === errorHighlightDetails.layer && currentKeyIndex === targetPressCode;
        },
        errorHighlightLayer: errorHighlightDetails?.layer ?? null, // App.tsx が参照する可能性のあるプロパティ
        clearErrorState,
    };
};

export default useCapsLockPractice;
