import React from 'react'; // useState を削除
import { PracticeMode } from '../hooks/usePracticeCommons';

interface PracticeMenuProps {
  practice: PracticeMode | '';
  handlePracticeSelect: (item: PracticeMode) => void;
  isAlphaNumericMode?: boolean;
  // App.tsx から開閉状態とセッターを受け取る
  isKanaStepUpOpen: boolean;
  setIsKanaStepUpOpen: React.Dispatch<React.SetStateAction<boolean>>;
  isAlphaNumericSymbolStepUpOpen: boolean;
  setIsAlphaNumericSymbolStepUpOpen: React.Dispatch<React.SetStateAction<boolean>>;
}

// かな練習メニューに表示する項目を定義 (基本練習のみ)
const kanaBasicPracticeMenuItems: PracticeMode[] = [
    '清音の基本練習',
    '拗音の基本練習', // 'ゃゅょの基本練習' から '拗音の基本練習' へ変更
    '濁音の基本練習',
    '半濁音の基本練習',
    '小文字の基本練習',
    '数字の基本練習',
];

// かな練習モード時に表示する記号練習メニューの項目
const kanaSymbolPracticeMenuItems: PracticeMode[] = [
    '記号の基本練習１',
    '記号の基本練習２',
    '記号の基本練習３',
];

// 英数字練習に表示する項目を定義 (基本練習のみ)
const alphaNumericBasicPracticeMenuItems: PracticeMode[] = [
    '英字の基本練習',
    'Caps Lock操作',
    '大文字変換操作',
    '数字の基本練習',
];

// 記号練習メニューに表示する項目を定義
const symbolPracticeMenuItems: PracticeMode[] = [
    '記号の基本練習１',
    '記号の基本練習２',
    '記号の基本練習３',
];

// 英数記号ステップアップメニュー項目
const alphaNumericSymbolStepUpPracticeMenuItems: PracticeMode[] = [
    '英字記号の練習１',
    '英字記号の練習２',
];

// 英数練習用のチャレンジメニュー項目
const alphaNumericChallengeMenuItems: PracticeMode[] = [
    '英字入力１分間トレーニング',
    '英数短文入力３分間トレーニング',
];

// かな練習用のステップアップメニュー項目
const kanaStepUpPracticeMenuItems: PracticeMode[] = [
    '拗濁音の練習',
    '拗半濁音の練習',
    '拗音拡張１', // 'ゃゅょ拡張１' から '拗音拡張１' へ変更
    '拗音拡張２', // 'ゃゅょ拡張２' から '拗音拡張２' へ変更
];

// かな練習用のチャレンジメニュー項目
const kanaChallengePracticeMenuItems: PracticeMode[] = [
    'かな入力１分間トレーニング',
    '記号入力１分間トレーニング',
    '短文入力３分間トレーニング',
];

const PracticeMenu: React.FC<PracticeMenuProps> = ({
    practice,
    handlePracticeSelect,
    isAlphaNumericMode = false,
    isKanaStepUpOpen,
    setIsKanaStepUpOpen,
    isAlphaNumericSymbolStepUpOpen,
    setIsAlphaNumericSymbolStepUpOpen }) => {

  return (
    <div className="p-4 border-r border-gray-300 dark:border-gray-700 overflow-y-auto h-full">
            {isAlphaNumericMode ? (
                <>
                    {/* 1. 英数字練習 */}
                    <h2 className="text-lg font-semibold mb-3 text-gray-700 dark:text-gray-200">英数字練習</h2>
                    <ul className="space-y-1">
                        {alphaNumericBasicPracticeMenuItems.map((item: PracticeMode) => (
                            <li key={item}>
                                <button
                                    onClick={() => handlePracticeSelect(item as PracticeMode)}
                                    className={`w-full text-left px-3 py-2 rounded-md text-sm font-medium transition-colors
                                        ${practice === item
                                            ? 'bg-blue-500 text-white'
                                            : 'text-gray-700 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-700'
                                        }`}
                                >
                                    {item}
                                </button>
                            </li>
                        ))}
                    </ul>

                    {/* 2. 記号練習メニュー (英数モード時のみ表示) */}
                    <h2 className='text-lg font-semibold mt-6 mb-3 text-gray-700 dark:text-gray-200'>英数記号練習</h2>
                    <ul className="space-y-1">
                        {symbolPracticeMenuItems.map((item: PracticeMode) => (
                            <li key={item === '記号の基本練習３' ? '記号の基本練習３_英数' : item}>
                                <button
                                    onClick={() => {
                                        const modeToSelect = item === '記号の基本練習３'
                                            ? '記号の基本練習３_英数' as PracticeMode
                                            : item;
                                        console.log(`[PracticeMenu onClick] Attempting to select mode: "${modeToSelect}" for item: "${item}"`);
                                        handlePracticeSelect(modeToSelect);
                                    }}
                                    className={`w-full text-left px-3 py-2 rounded-md text-sm font-medium transition-colors
                                        ${practice === (item === '記号の基本練習３' ? '記号の基本練習３_英数' : item)
                                            ? 'bg-blue-500 text-white'
                                            : 'text-gray-700 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-700'
                                        }`}
                                >
                                    {item}
                                </button>
                            </li>
                        ))}
                    </ul>

                    {/* 英数記号ステップアップ */}
                    <div className="mt-6">
                        <button
                            onClick={() => setIsAlphaNumericSymbolStepUpOpen(!isAlphaNumericSymbolStepUpOpen)}
                            className="w-full flex justify-between items-center text-lg font-semibold mb-3 text-gray-700 dark:text-gray-200 focus:outline-none"
                        >
                            英数記号ステップアップ
                            <span>{isAlphaNumericSymbolStepUpOpen ? '▲' : '▼'}</span>
                        </button>
                        {isAlphaNumericSymbolStepUpOpen && (
                            <ul className="space-y-1">
                                {alphaNumericSymbolStepUpPracticeMenuItems.map((item: PracticeMode) => (
                                    <li key={item}>
                                        <button
                                            onClick={() => handlePracticeSelect(item)}
                                            className={`w-full text-left px-3 py-2 rounded-md text-sm font-medium transition-colors
                                                ${practice === item
                                                    ? 'bg-blue-500 text-white'
                                                    : 'text-gray-700 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-700'
                                                }`}
                                        >
                                            {item}
                                        </button>
                                    </li>
                                ))}
                            </ul>
                        )}
                    </div>

                    {/* 3. 英数チャレンジメニュー (英数モード時のみ表示) */}
                    <h2 className='text-lg font-semibold mt-6 mb-3 text-gray-700 dark:text-gray-200'>英数チャレンジ</h2>
                    <ul className="space-y-1">
                        {alphaNumericChallengeMenuItems.map((item: PracticeMode) => (
                            <li key={item}>
                                <button
                                    onClick={() => handlePracticeSelect(item)}
                                    className={`w-full text-left px-3 py-2 rounded-md text-sm font-medium transition-colors
                                        ${practice === item
                                            ? 'bg-blue-500 text-white'
                                            : 'text-gray-700 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-700'
                                        }`}
                                >
                                    {item}
                                </button>
                            </li>
                        ))}
                    </ul>
                    {/* ... (other commented out sections) ... */}
                </>
            ) : (
                <>
                    {/* かな数字練習モード時の表示 */}
                    <h2 className="text-lg font-semibold mb-3 text-gray-700 dark:text-gray-200">かな数字練習</h2>
                    <ul className="space-y-1">
                        {kanaBasicPracticeMenuItems.map((item: PracticeMode) => (
                            <li key={item}>
                                <button
                                    // 「数字の基本練習」も他の項目と同様に item をそのまま渡す
                                    onClick={() => handlePracticeSelect(item as PracticeMode)}
                                    className={`w-full text-left px-3 py-2 rounded-md text-sm font-medium transition-colors
                                        // アクティブ状態の判定も item をそのまま使用
                                        ${practice === item
                                            ? 'bg-blue-500 text-white'
                                            : 'text-gray-700 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-700'
                                        }`}
                                >
                                    {item}
                                </button>
                            </li>
                        ))}
                    </ul>
                    {/* 新しい記号練習セクション */}
                    <h2 className="text-lg font-semibold mt-6 mb-3 text-gray-700 dark:text-gray-200">かな記号練習</h2>
                    <ul className="space-y-1">
                        {kanaSymbolPracticeMenuItems.map((item: PracticeMode) => (
                            <li key={item}>
                                <button
                                    onClick={() => handlePracticeSelect(item)}
                                    className={`w-full text-left px-3 py-2 rounded-md text-sm font-medium transition-colors
                                        ${practice === item
                                            ? 'bg-blue-500 text-white'
                                            : 'text-gray-700 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-700'
                                        }`}
                                >
                                    {item}
                                </button>
                            </li>
                        ))}
                    </ul>
                    {/* かなステップアップ (折りたたみ可能) */}
                    <div className="mt-6">
                        <button
                            onClick={() => setIsKanaStepUpOpen(!isKanaStepUpOpen)}
                            className="w-full flex justify-between items-center text-lg font-semibold mb-3 text-gray-700 dark:text-gray-200 focus:outline-none"
                        >
                            かなステップアップ
                            <span>{isKanaStepUpOpen ? '▲' : '▼'}</span>
                        </button>
                        {isKanaStepUpOpen && (
                            <ul className="space-y-1">
                                {kanaStepUpPracticeMenuItems.map((item: PracticeMode) => (
                                    <li key={item}>
                                        <button
                                            onClick={() => handlePracticeSelect(item)}
                                            className={`w-full text-left px-3 py-2 rounded-md text-sm font-medium transition-colors
                                                ${practice === item
                                                    ? 'bg-blue-500 text-white'
                                                    : 'text-gray-700 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-700'
                                                }`}
                                        >
                                            {item}
                                        </button>
                                    </li>
                                ))}
                            </ul>
                        )}
                    </div>

                    <h2 className='text-lg font-semibold mt-6 mb-3 text-gray-700 dark:text-gray-200'>かなチャレンジ</h2>
                    <ul className="space-y-1">
                        {kanaChallengePracticeMenuItems.map((item: PracticeMode) => (
                            <li key={item}>
                                <button
                                    onClick={() => handlePracticeSelect(item)}
                                    className={`w-full text-left px-3 py-2 rounded-md text-sm font-medium transition-colors
                                        ${practice === item
                                            ? 'bg-blue-500 text-white'
                                            : 'text-gray-700 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-700'
                                        }`}
                                >
                                    {item}
                                </button>
                            </li>
                        ))}
                    </ul>
                </>
            )}
    </div>
  );
};

export default PracticeMenu;
