// /home/coffee/my-keymap-viewer/src/hooks/useKigoChallengePractice.ts
import { useState, useEffect, useCallback, useRef, useMemo } from 'react'; // useMemo をインポート
import {
    PracticeHookProps,
    PracticeHookResult,
    PracticeInputInfo,
    ChallengeResult, PracticeInputResult, // PracticeInputResult をインポート
    PracticeHighlightResult,
    HighlightInfo,
} from './usePracticeCommons';
import {
    kigoPractice1Data,
    kigoPractice2Data,
    kigoPractice3Data,
    kigoMapping2,
    EQUAL_KIGO_KEY_NAME, // Import EQUAL_KIGO_KEY_NAME
    kigoMapping3,
} from '../data/keymapData';
// import { type } from 'os'; // このインポートは不要そうなので削除検討

// 長押し判定時間 (ミリ秒) - useKigoPractice1.ts からコピー
const LONG_PRESS_DURATION = 300; // 0.3秒に変更

// カウントダウン秒数
const COUNTDOWN_SECONDS = 3; // 3秒に変更

// 記号チャレンジモードの状態
interface KigoChallengeState {
    targetChar: string | null;
    targetGyouKey: string | null;
    targetLayerIndex: number | null;
    targetType: 'kigo1' | 'kigo2' | 'kigo3' | null; // どのデータセット由来か
    isEqualSign: boolean; // = キーが必要か
    currentStep: number; // 0: 初期状態, 1: 1打鍵目完了
    remainingTime: number;
    status: 'idle' | 'countdown' | 'running' | 'finished' | 'incorrect'; // 'incorrect' を追加
    countdownValue: number;
    startTime: number | null;
    endTime: number | null;
    totalQuestions: number; // 正解シーケンス完了数
    correctAnswers: number; // 正解シーケンス完了数 (totalQuestionsと同じ)
    totalCharsTyped: number; // 入力した総文字数（正誤問わず）
    totalKeyPresses: number; // 総打鍵数
    incorrectKeyPresses: number; // 不正解打鍵数
    currentConsecutiveCorrect: number; // 現在の連続正解数
    maxConsecutiveCorrect: number; // 最大連続正解数
    challengeResults: ChallengeResult | null;
    isLongPressSuccess: boolean;
    showHighlightForEqualSign?: boolean;
    currentInputErrorLayer: number | null;
}

const initialState: KigoChallengeState = {
    targetChar: null, targetGyouKey: null, targetType: null,
    isEqualSign: false, currentStep: 0, remainingTime: 60000, status: 'idle', targetLayerIndex: 6,
    countdownValue: COUNTDOWN_SECONDS, startTime: null, endTime: null, totalQuestions: 0,
    correctAnswers: 0, totalCharsTyped: 0, totalKeyPresses: 0, incorrectKeyPresses: 0,
    currentConsecutiveCorrect: 0, maxConsecutiveCorrect: 0, // 初期状態に連続正解数フィールドを追加
    challengeResults: null, isLongPressSuccess: false, showHighlightForEqualSign: false,
    currentInputErrorLayer: null, // 初期状態にエラーレイヤーフィールドを追加
};


// 記号チャレンジモードのカスタムフック
const useKigoChallengePractice = (props: PracticeHookProps): PracticeHookResult => {
    const { isActive, side, layers, kb } = props; // layers, kb を受け取る

    const [state, setState] = useState<KigoChallengeState>(initialState);
    const stateRef = useRef(state); // 常に最新の state を参照するための ref (useCallback 内で使う)
    const pressInfoRef = useRef<{ code: number; timestamp: number } | null>(null);
    const longPressTimerRef = useRef<number | null>(null); // Timer for Kigo1 long press
    const equalSignHighlightTimerRef = useRef<number | null>(null); // Timer for re-highlighting '='
    const prevIsActiveRef = useRef(isActive); // isActive の前回の値を保持

    // state が更新されるたびに ref も更新
    useEffect(() => {
        stateRef.current = state;
    }, [state]);

    // 組み合わせた全記号リストを作成 (初回のみ)
    const allKigoChars = useRef<
        { char: string; gyouKey: string | null; layerIndex: number; type: 'kigo1' | 'kigo2' | 'kigo3'; isEqualSign: boolean }[] // gyouKey を null 許容に
    >([]).current;

    useEffect(() => {
        if (allKigoChars.length === 0) {
            // kigoPractice1Data からの記号には layerIndex: 6 を設定
            kigoPractice1Data.forEach(group => {
                group.chars.forEach((char, index) => {
                    const keyName = group.inputs[index]?.keyName ?? null;
                    allKigoChars.push({ char, gyouKey: keyName, layerIndex: 6, type: 'kigo1', isEqualSign: false });
                });
            });
            // kigoPractice2Data からの記号には layerIndex: 7 を設定
            kigoPractice2Data.forEach(group => {
                group.chars.forEach((char: string, index: number) => {
                    const gyouKey = group.inputs[index]?.gyouKey ?? null;
                    allKigoChars.push({ char, gyouKey: gyouKey, layerIndex: 7, type: 'kigo2', isEqualSign: false });
                });
            });
            kigoPractice3Data.forEach(group => {
                group.chars.forEach((char, index) => {
                    const isEqualSign = char === '＝';
                    const gyouKey = isEqualSign ? null : (group.inputs[index]?.gyouKey ?? null);
                    allKigoChars.push({ char, gyouKey: gyouKey, layerIndex: 8, type: 'kigo3', isEqualSign: isEqualSign });
                });
            });
        }
    }, [allKigoChars]);
    // 次のターゲットを選択する関数
    const clearLongPressTimer = useCallback(() => {
        if (longPressTimerRef.current !== null) {
            clearTimeout(longPressTimerRef.current);
            longPressTimerRef.current = null;
        }
    }, []);

    const selectNextTarget = useCallback(() => {
        if (allKigoChars.length === 0) {
            console.warn("[KigoChallenge] allKigoChars is empty, cannot select next target.");
            return;
        }

        const randomIndex = Math.floor(Math.random() * allKigoChars.length);
        const selected = allKigoChars[randomIndex];

        setState((prev: KigoChallengeState) => ({
            ...prev,
            targetChar: selected.char,
            targetGyouKey: selected.gyouKey,
            targetLayerIndex: selected.layerIndex,
            targetType: selected.type,
            isEqualSign: selected.isEqualSign,
            currentStep: 0,
            isLongPressSuccess: false,
            showHighlightForEqualSign: false,
            currentInputErrorLayer: null,
        }));
        pressInfoRef.current = null;
        clearLongPressTimer();
        if (equalSignHighlightTimerRef.current) {
            clearTimeout(equalSignHighlightTimerRef.current);
            equalSignHighlightTimerRef.current = null;
        }
    }, [allKigoChars, clearLongPressTimer]);

    const startChallenge = useCallback(() => {
        setState({
            ...initialState,
            status: 'countdown',
            remainingTime: 60000,
            countdownValue: COUNTDOWN_SECONDS,
            startTime: Date.now(),
            endTime: null,
            challengeResults: null,
            isLongPressSuccess: false,
            showHighlightForEqualSign: false,
            currentConsecutiveCorrect: 0,
            maxConsecutiveCorrect: 0,
            currentInputErrorLayer: null,
        });
        pressInfoRef.current = null;
        clearLongPressTimer();
    }, [clearLongPressTimer]);

    useEffect(() => {
        if (isActive && state.status === 'countdown') {
            if (state.countdownValue > 0) {
                const timerId = setTimeout(() => {
                    setState((prev: KigoChallengeState) => ({ ...prev, countdownValue: prev.countdownValue - 1 }));
                }, 1000);
                return () => clearTimeout(timerId);
            } else {
                setState((prev: KigoChallengeState) => ({
                    ...prev,
                    status: 'running',
                    remainingTime: 60000,
                    startTime: Date.now(),
                    currentInputErrorLayer: null,
                }));
            }
        }
    }, [isActive, state.status, state.countdownValue]);

    useEffect(() => {
        if (state.status === 'running' && !state.targetChar) {
            selectNextTarget();
        }
    }, [state.status, selectNextTarget, state.targetChar]);

    const finishChallenge = useCallback(() => {
        const now = Date.now();
        const { startTime, totalQuestions, correctAnswers, totalCharsTyped, totalKeyPresses, incorrectKeyPresses, maxConsecutiveCorrect } = stateRef.current;
        const durationSeconds = startTime ? (now - startTime) / 1000 : 0;

        const durationMinutes = durationSeconds / 60;
        const cpmValue = durationMinutes > 0 ? Math.round(correctAnswers / durationMinutes) : 0;
        const accuracy = totalKeyPresses > 0 ? (totalKeyPresses - incorrectKeyPresses) / totalKeyPresses : 0;
        const calculatedMissCount = incorrectKeyPresses;

        const results: ChallengeResult = {
            totalQuestions,
            totalCharsTyped: totalKeyPresses,
            correctCharsCount: correctAnswers,
            correctCount: correctAnswers,
            missCount: calculatedMissCount,
            accuracy: accuracy,
            score: 0,
            cpm: cpmValue,
            rankMessage: '',
            maxConsecutiveCorrectChars: maxConsecutiveCorrect,
        };

        setState((prev: KigoChallengeState) => ({
            ...prev,
            endTime: now,
            status: 'finished',
            challengeResults: results,
            targetChar: null,
            targetGyouKey: null,
            targetLayerIndex: null,
            showHighlightForEqualSign: false,
            currentInputErrorLayer: null,
        }));
        clearLongPressTimer();
        if (equalSignHighlightTimerRef.current) {
            clearTimeout(equalSignHighlightTimerRef.current);
            equalSignHighlightTimerRef.current = null;
        }
    }, [clearLongPressTimer]);
  
    useEffect(() => {
        let intervalId: NodeJS.Timeout | null = null;
        if (isActive && state.status === 'running') {
            intervalId = setInterval(() => {
                setState((prev: KigoChallengeState) => {
                    const newRemainingTime = Math.max(0, prev.remainingTime - 100);
                    if (newRemainingTime === 0 && prev.status === 'running') {
                        return { ...prev, remainingTime: 0, status: 'finished' };
                    }
                    return { ...prev, remainingTime: newRemainingTime, showHighlightForEqualSign: prev.showHighlightForEqualSign };
                });
            }, 100);
        } else if (intervalId) {
            clearInterval(intervalId);
        }
        return () => {
            if (intervalId) clearInterval(intervalId);
        };
    }, [isActive, state.status]);

    useEffect(() => {
        if (state.status === 'finished' && !state.endTime) {
            finishChallenge();
        }
    }, [state.status, state.endTime, finishChallenge]);

    useEffect(() => {
        if (!isActive && prevIsActiveRef.current) {
            setState(initialState);
            pressInfoRef.current = null;
            clearLongPressTimer();
            if (equalSignHighlightTimerRef.current) {
                clearTimeout(equalSignHighlightTimerRef.current);
                equalSignHighlightTimerRef.current = null;
            }
        } else if (isActive && state.status === 'idle') {
            startChallenge();
        }
        prevIsActiveRef.current = isActive;
    }, [isActive, state.status, startChallenge, clearLongPressTimer]);

    const handleInput = useCallback((info: PracticeInputInfo): PracticeInputResult => {
        const { targetChar, targetGyouKey, targetLayerIndex, isEqualSign, currentStep, targetType } = stateRef.current;
        const { isLongPressSuccess } = stateRef.current;

        const findKeyCode = (keyName: string | null, layerIdx: number | null): number => {
            if (!keyName) return -1;
            const expectedLayer = layerIdx ?? -1;
            if (expectedLayer === -1 || !layers[expectedLayer]) {
                console.log(`[findKeyCode] Invalid layer. keyName: ${keyName}, layerIdx: ${layerIdx}, expectedLayer: ${expectedLayer}, layers[expectedLayer] exists: ${!!layers[expectedLayer]}`);
                return -1;
            }
            
            const layer = layers[expectedLayer];
            const trimmedKeyName = keyName.trim(); 
            console.log(`[findKeyCode] Searching for "${trimmedKeyName}" in layer ${expectedLayer}:`, layer.map(k => k.trim()));
            for (let i = 0; i < layer.length; i++) {
                if (layer[i] && layer[i].trim() === trimmedKeyName) {
                    console.log(`[findKeyCode] Found "${trimmedKeyName}" at index ${i} (keyCode ${i+1})`);
                    return i + 1;
                }
            }
            console.log(`[findKeyCode] "${trimmedKeyName}" NOT FOUND in layer ${expectedLayer}`);
            return -1;
        };

        const getExpectedKeyCodeForKigo1 = (): number => findKeyCode(targetGyouKey, 6);
        const getKigoKeyCodeForKigo2 = (): number => findKeyCode('記号', 7);
        const getKigoKeyCodeForKigo3 = (): number => findKeyCode(EQUAL_KIGO_KEY_NAME, 8);
        const getExpectedKeyCodeForKigo2Layer7 = (): number => findKeyCode(targetChar, 7);
        const getExpectedKeyCodeForKigo3Layer8 = (): number => {
            const currentTargetCharFromState = stateRef.current.targetChar;
            const currentTargetGyouKeyFromState = stateRef.current.targetGyouKey;
            const currentIsEqualSignFromState = stateRef.current.isEqualSign;

            console.log(`[getExpectedKeyCodeForKigo3Layer8] currentTargetCharFromState: '${currentTargetCharFromState}', currentTargetGyouKeyFromState: '${currentTargetGyouKeyFromState}', currentIsEqualSignFromState: ${currentIsEqualSignFromState}`);

            if (currentIsEqualSignFromState) {
                return findKeyCode(EQUAL_KIGO_KEY_NAME, 8);
            }
            if (currentTargetCharFromState === '…') {
                const keyCode = findKeyCode('…', 8);
                console.log(`[getExpectedKeyCodeForKigo3Layer8] For '…', found keycode: ${keyCode}`);
                return keyCode;
            }
            const keyCode = findKeyCode(currentTargetGyouKeyFromState, 8);
            console.log(`[getExpectedKeyCodeForKigo3Layer8] For '${currentTargetGyouKeyFromState}' (target char '${currentTargetCharFromState}'), found keycode: ${keyCode}`);
            return keyCode;
        };

        if (info.type === 'press') {
            const pressCode = info.pressCode;
            const { targetType } = stateRef.current;
            if (stateRef.current.targetChar === '`') {
                console.log(`[KigoChallenge DEBUG \` target PRESS] info:`, JSON.stringify(info));
                console.log(`  stateRef.current (press):`, JSON.stringify(stateRef.current));
                const expectedFirstKeyCodeDbg = getKigoKeyCodeForKigo3();
                const expectedSecondKeyCodeDbg = findKeyCode('`', 8);
                console.log(`  expectedFirstKeyCodeDbg (=\\n記号): ${expectedFirstKeyCodeDbg}, expectedSecondKeyCodeDbg (\`): ${expectedSecondKeyCodeDbg}`);
            }

            if (targetType === 'kigo1') {
                const expectedKeyCode = getExpectedKeyCodeForKigo1();
                if ((pressCode + 1) === expectedKeyCode) {
                    pressInfoRef.current = { code: pressCode, timestamp: info.timestamp };
                    clearLongPressTimer();
                    longPressTimerRef.current = window.setTimeout(() => {
                        if (pressInfoRef.current && pressInfoRef.current.code === pressCode) {
                            setState((prev: KigoChallengeState) => ({ ...prev, isLongPressSuccess: true }));
                        }
                        longPressTimerRef.current = null;
                    }, LONG_PRESS_DURATION);
                }
            }
            return { isExpected: false, shouldGoToNext: false, errorLayer: null };
        }

        const currentTargetCharForLog = stateRef.current.targetChar;
        if (currentTargetCharForLog === '`' || currentTargetCharForLog === '?') {
            console.log(`[KigoChallenge DEBUG ${currentTargetCharForLog} target RELEASE] info:`, JSON.stringify(info));
            console.log(`  stateRef.current (release):`, JSON.stringify(stateRef.current));
            const expectedFirstKeyCodeDbg = getKigoKeyCodeForKigo3();
            // When currentTargetCharForLog is '`' or '?', expectedSecondKeyNameForLog is the char itself.
            const expectedSecondKeyNameForLog = currentTargetCharForLog; 
            const expectedSecondKeyCodeDbg = findKeyCode(expectedSecondKeyNameForLog, 8);
            console.log(`  expectedFirstKeyCodeDbg (=\\n記号): ${expectedFirstKeyCodeDbg}, expectedSecondKeyCodeDbg (${expectedSecondKeyNameForLog}): ${expectedSecondKeyCodeDbg}`);
        }

        if (stateRef.current.status !== 'running' || !targetChar) {
            return { isExpected: false, shouldGoToNext: false, errorLayer: null };
        }

        setState((prev: KigoChallengeState) => ({ ...prev, totalKeyPresses: prev.totalKeyPresses + 1 }));

        let expectedFirstKeyCode = -1;
        let expectedSecondKeyCode = -1;
        let isCorrectInput = false;
        let isSequenceCompletedThisTurn = false;
        let nextStep = currentStep;
        let shouldGoToNext = false;
        let errorLayer: number | null = null;

        if (currentStep === 0) {
            if (targetType === 'kigo1') {
                expectedFirstKeyCode = getExpectedKeyCodeForKigo1();
                if (pressInfoRef.current && (pressInfoRef.current.code + 1) === expectedFirstKeyCode) {
                    const pressTimestamp = pressInfoRef.current.timestamp;
                    const releaseTimestamp = info.timestamp;
                    const duration = releaseTimestamp - pressTimestamp;
                    clearLongPressTimer();
                    if (duration >= LONG_PRESS_DURATION && isLongPressSuccess) {
                        isCorrectInput = true; nextStep = 1; shouldGoToNext = true; isSequenceCompletedThisTurn = true; errorLayer = null;
                    } else {
                        isCorrectInput = false; errorLayer = 6; isSequenceCompletedThisTurn = true;
                    }
                } else {
                    isCorrectInput = false; errorLayer = 6; isSequenceCompletedThisTurn = true;
                }
                pressInfoRef.current = null;
                setState((prev: KigoChallengeState) => ({ ...prev, isLongPressSuccess: false }));
            } else if (targetType === 'kigo2') {
                expectedFirstKeyCode = getExpectedKeyCodeForKigo2Layer7();
                if ((info.pressCode + 1) === expectedFirstKeyCode) {
                    isCorrectInput = true; nextStep = 1; errorLayer = null; isSequenceCompletedThisTurn = false;
                } else {
                    isCorrectInput = false; errorLayer = 7; isSequenceCompletedThisTurn = true;
                }
            } else if (targetType === 'kigo3') {
                expectedFirstKeyCode = getKigoKeyCodeForKigo3();
                console.log(`[KigoChallenge DEBUG kigo3 step0 !isEqualSign] targetChar: "${targetChar}", pressCode+1: ${info.pressCode + 1}, expectedFirstKeyCode (=\\n記号): ${expectedFirstKeyCode}`);
                if ((info.pressCode + 1) === expectedFirstKeyCode) {
                    console.log(`[KigoChallenge DEBUG kigo3 step0 !isEqualSign] MATCH!`);
                    isCorrectInput = true;
                    if (isEqualSign) {
                        if (equalSignHighlightTimerRef.current) clearTimeout(equalSignHighlightTimerRef.current);
                        setState(prev => ({ ...prev, showHighlightForEqualSign: false }));
                        equalSignHighlightTimerRef.current = window.setTimeout(() => {
                            if (stateRef.current.targetChar === '＝' && stateRef.current.currentStep === 1) {
                                setState(prev => ({ ...prev, showHighlightForEqualSign: true }));
                            }
                            equalSignHighlightTimerRef.current = null;
                        }, 300);
                    }
                    nextStep = 1; errorLayer = null; isSequenceCompletedThisTurn = false;
                } else {
                    console.log(`[KigoChallenge DEBUG kigo3 step0 !isEqualSign] NO MATCH.`);
                    isCorrectInput = false; errorLayer = 8; isSequenceCompletedThisTurn = true;
                }
            }
        } else if (currentStep === 1) {
            if (targetType === 'kigo2') {
                expectedSecondKeyCode = getKigoKeyCodeForKigo2();
                if ((info.pressCode + 1) === expectedSecondKeyCode) {
                    isCorrectInput = true; nextStep = 2; shouldGoToNext = true; isSequenceCompletedThisTurn = true; errorLayer = null;
                } else {
                    isCorrectInput = false; errorLayer = 7; isSequenceCompletedThisTurn = true;
                }
            } else if (targetType === 'kigo3') {
                expectedSecondKeyCode = getExpectedKeyCodeForKigo3Layer8();
                let keyCodeToCompare = expectedSecondKeyCode;
                // Ellipsis specific handling is now inside getExpectedKeyCodeForKigo3Layer8

                if (keyCodeToCompare === -1) { // findKeyCode が -1 を返した場合
                    console.error(`[KigoChallenge handleInput] Target char '${targetChar}' not found in layer 8 keymap!`);
                    isCorrectInput = false;
                    errorLayer = 8;
                    isSequenceCompletedThisTurn = true; // シーケンスは失敗として完了
                } else if ((info.pressCode + 1) === keyCodeToCompare) {
                    isCorrectInput = true; nextStep = 2; shouldGoToNext = true; isSequenceCompletedThisTurn = true; errorLayer = null;
                } else {
                    isCorrectInput = false; errorLayer = 8; isSequenceCompletedThisTurn = true;
                }
            } else {
                isCorrectInput = false; isSequenceCompletedThisTurn = true;
            }
        }

        if (isSequenceCompletedThisTurn) {
            if (isCorrectInput) {
                setState((prev: KigoChallengeState) => ({
                    ...prev,
                    currentConsecutiveCorrect: prev.currentConsecutiveCorrect + 1,
                    maxConsecutiveCorrect: Math.max(prev.maxConsecutiveCorrect, prev.currentConsecutiveCorrect + 1),
                }));
            } else {
                setState((prev: KigoChallengeState) => ({ ...prev, currentConsecutiveCorrect: 0 }));
            }
        }

        if (isCorrectInput) {
            setState((prev: KigoChallengeState) => ({
                ...prev,
                currentStep: nextStep,
                totalCharsTyped: prev.totalCharsTyped + (targetChar?.length ?? 0),
                currentInputErrorLayer: null,
            }));
            if (shouldGoToNext) {
                setState((prev: KigoChallengeState) => ({
                    ...prev,
                    totalQuestions: prev.totalQuestions + 1,
                    correctAnswers: prev.correctAnswers + 1,
                }));
                selectNextTarget();
            }
            return { isExpected: true, shouldGoToNext: false, errorLayer: null };
        } else {
            setState((prev: KigoChallengeState) => ({
                ...prev,
                currentStep: 0,
                incorrectKeyPresses: prev.incorrectKeyPresses + 1,
                currentInputErrorLayer: errorLayer,
            }));
            if (isEqualSign && currentStep === 1) {
                if (equalSignHighlightTimerRef.current) clearTimeout(equalSignHighlightTimerRef.current);
                setState(prev => ({ ...prev, showHighlightForEqualSign: false }));
            }
            return { isExpected: false, shouldGoToNext: false, errorLayer };
        }
    }, [layers, selectNextTarget, clearLongPressTimer, stateRef]);

    const reset = useCallback(() => {
        setState(initialState);
        pressInfoRef.current = null;
        clearLongPressTimer();
        if (equalSignHighlightTimerRef.current) {
            clearTimeout(equalSignHighlightTimerRef.current);
            equalSignHighlightTimerRef.current = null;
        }
    }, [clearLongPressTimer]);

    const headingChars = useMemo(() => {
        if (state.status === 'countdown') {
            return [`${state.countdownValue}秒`];
        } else if (state.status === 'finished' && state.challengeResults) {
            return ['終了！'];
        } else if (state.status === 'running' && state.targetChar) {
            return [state.targetChar];
        }
        return ['読み込み中...'];
    }, [state.status, state.countdownValue, state.targetChar, state.challengeResults]);

    const getHighlightClassName = useCallback((keyName: string, layoutIndex: number): PracticeHighlightResult => {
        const noHighlight: PracticeHighlightResult = { className: null, overrideKey: null };
        const { status, targetGyouKey, targetLayerIndex, isEqualSign, currentStep, targetType, targetChar, showHighlightForEqualSign, currentInputErrorLayer } = state;

        if (status !== 'running' || targetLayerIndex === null) {
            return noHighlight;
        }
        if (currentInputErrorLayer !== null && layoutIndex === currentInputErrorLayer) {
            return noHighlight;
        }

        let expectedKeyDisplayName: string | null = null;
        let expectedLayoutIndex: number | null = null;

        if (targetType === 'kigo1') {
            expectedKeyDisplayName = targetGyouKey;
            expectedLayoutIndex = 6;
            if (layoutIndex === expectedLayoutIndex && keyName.trim() === expectedKeyDisplayName?.trim()) {
                return { className: state.isLongPressSuccess ? 'bg-green-200' : 'bg-blue-100', overrideKey: null };
            }
        } else if (targetType === 'kigo2') {
            expectedLayoutIndex = 7;
            if (currentStep === 0) {
                expectedKeyDisplayName = state.targetChar;
            } else if (currentStep === 1) {
                expectedKeyDisplayName = '記号';
            }
            if (layoutIndex === expectedLayoutIndex && keyName.trim() === expectedKeyDisplayName?.trim()) {
                return { className: 'bg-blue-100', overrideKey: null };
            }
        } else if (targetType === 'kigo3') {
            expectedLayoutIndex = 8;
            if (currentStep === 0) {
                expectedKeyDisplayName = EQUAL_KIGO_KEY_NAME;
            } else if (currentStep === 1) {
                if (isEqualSign) {
                    expectedKeyDisplayName = EQUAL_KIGO_KEY_NAME;
                } else {
                    expectedKeyDisplayName = targetChar; // keyName は displayLayers で変換後のため、targetChar と一致させる
                }
            }
            if (isEqualSign && currentStep === 1 && !showHighlightForEqualSign) {
                return noHighlight;
            }
            if (layoutIndex === expectedLayoutIndex && keyName.trim() === expectedKeyDisplayName?.trim()) {
                return { className: 'bg-blue-100', overrideKey: null };
            }
        }
        return noHighlight;
    }, [state]);

    const isInvalidInputTarget = useCallback((pressCode: number, layoutIndex: number, keyIndex: number): boolean => {
        const { status, targetLayerIndex, currentInputErrorLayer } = stateRef.current;
        if (status !== 'running' || targetLayerIndex === null) {
            return false;
        }
        if (currentInputErrorLayer !== null && layoutIndex === currentInputErrorLayer && keyIndex === pressCode) {
            return true;
        }
        return false;
    }, [stateRef]);

    const getHighlight = useCallback((): HighlightInfo => {
        return { start: null, end: null };
    }, []);

    const clearErrorState = useCallback(() => {
        setState(prev => ({ ...prev, currentInputErrorLayer: null }));
    }, [setState]);

    const displayLayers = useMemo(() => {
        // For kigo3, if the target is '.' or ',', modify layer 8 for display
        // Always apply this transformation for kigo3 targets to ensure consistent display of , and .
        if (state.targetType === 'kigo3') {
            const newLayers = layers.map((layer, index) => {
                if (index === 8) { // Layer 8
                    return layer.map(key => {
                        const trimmedKey = key.trim();
                        if (trimmedKey === '。') return ','; // Display '。' as ','
                        if (trimmedKey === '、') return '.'; // Display '、' as '.'
                        return key;
                    });
                }
                return layer;
            });
            return newLayers;
        }
        return layers;
    }, [state.targetType, layers]); // state.targetChar is removed from dependencies for this part

    return {
        handleInput,
        reset,
        headingChars,
        getHighlightClassName,
        isInvalidInputTarget,
        getHighlight,
        targetLayerIndex: state.targetLayerIndex,
        displayLayers,
        challengeResults: state.challengeResults,
        status: state.status,
        countdownValue: state.countdownValue,
        targetChar: state.targetChar ?? undefined,
        challengeTimer: state.remainingTime,
        challengeTotalTime: 60000,
        errorHighlightLayer: state.currentInputErrorLayer,
        clearErrorState,
    };
};

export default useKigoChallengePractice;
