import { useMemo, useState, useEffect, useCallback, useRef } from 'react';
import { alphaSymbolPractice1Data } from '../data/keymapData'; // 練習データをインポート
import {
    PracticeHookProps,
    PracticeInputInfo,
    PracticeInputResult,
    PracticeHookResult,
    CharInfoAlphaSymbol1, // 新しい型をインポート
    allAlphaSymbol1CharInfos, // 新しいデータ配列をインポート
    PracticeHighlightResult,
    PracticeStatus,
} from './usePracticeCommons';

// Define stages for the practice mode
type AlphaSymbolPractice1Stage = 'initialSymbolKey' | 'targetSymbolChar';

const useAlphaSymbolPractice1 = ({
    isActive,
    gIdx, // 通常モード用に gIdx を受け取る
    dIdx, // 通常モード用に dIdx を受け取る
    layers,
    isRandomMode, // isRandomMode を受け取る
}: PracticeHookProps): PracticeHookResult => {
    const [status, setStatus] = useState<PracticeStatus>('idle');
    const [stage, setStage] = useState<AlphaSymbolPractice1Stage>('initialSymbolKey');
    const prevIsActiveRef = useRef(isActive);
    const prevGIdxRef = useRef(gIdx); // Declare prevGIdxRef
    const prevDIdxRef = useRef(dIdx); // Declare prevDIdxRef
    const prevIsRandomModeRef = useRef(isRandomMode); // Track previous random mode
    const [randomTargetInfo, setRandomTargetInfo] = useState<CharInfoAlphaSymbol1 | null>(null); // ランダムモード用

    // 通常モード用のターゲット情報を計算
    const normalModeTargetInfo = useMemo((): CharInfoAlphaSymbol1 | null => {
        if (isRandomMode || !isActive || gIdx === undefined || dIdx === undefined || gIdx < 0 || gIdx >= alphaSymbolPractice1Data.length) return null;
        const group = alphaSymbolPractice1Data[gIdx];
        if (!group || dIdx < 0 || dIdx >= group.chars.length) return null;
        const char = group.chars[dIdx];
        return { type: 'alphaSymbol1', char, layerIndex: 9 }; // layerIndex は固定
    }, [isActive, isRandomMode, gIdx, dIdx]);

    // 現在の練習ターゲット情報を決定 (ランダムモードか通常モードかによる)
    const currentTargetInfo = useMemo(() => {
        return isRandomMode ? randomTargetInfo : normalModeTargetInfo;
    }, [isRandomMode, randomTargetInfo, normalModeTargetInfo]);

    // The actual symbol to be typed (e.g., '@', '-', '_', '/', '°')
    const actualSymbolTarget = useMemo(() => {
        // currentTargetInfo から直接 char を取得
        const char = currentTargetInfo?.char ?? null;
        console.log(`[AlphaSymbolPractice1 actualSymbolTarget] currentTargetInfo: ${JSON.stringify(currentTargetInfo)}, char: ${char}`);
        return char;
    }, [currentTargetInfo]); // currentTargetInfo に依存

    // This is what the user is currently expected to press on the keyboard
    const currentKeyboardExpectedInput = useMemo(() => {
        if (stage === 'initialSymbolKey') return '記号';
        return actualSymbolTarget;
    }, [stage, actualSymbolTarget]);
    
    // What the user is currently expected to type (either "記号" or the actualSymbolTarget)
    const currentExpectedInputChar = useMemo(() => {
        if (stage === 'initialSymbolKey') return '記号';
        return actualSymbolTarget;
    }, [stage, actualSymbolTarget]);

    // ヘッダー文字
    const headingChars = useMemo(() => {
        if (!isActive) {
            return [];
        }

        if (isRandomMode) {
            // ランダムモード時はターゲットの1文字だけ
            return currentTargetInfo ? [{ char: currentTargetInfo.char, isPracticeTarget: true }] : [];
        } else {
            // 通常モード時は現在のグループの全文字を表示し、ターゲットをマーク
            if (gIdx === undefined || gIdx < 0 || gIdx >= alphaSymbolPractice1Data.length) {
                return []; // Invalid group index
            }
            const groupChars = alphaSymbolPractice1Data[gIdx].chars;
            const charsToDisplay = groupChars.map((char, index) => ({
                char,
                isPracticeTarget: index === dIdx
            }));

            // 0文字または1文字の場合はスペースを挿入する必要なし
            if (charsToDisplay.length <= 1) {
                return charsToDisplay;
            }

            // 各文字オブジェクトの間にスペースオブジェクトを挿入
            return charsToDisplay.flatMap((item, index) =>
                index < charsToDisplay.length - 1 ? [item, { char: '\u00A0', isPracticeTarget: false }] : [item]
            );
        }
    }, [isActive, isRandomMode, currentTargetInfo, gIdx, dIdx]);

    // Advance to the next random symbol
    const advanceToNextRandomSymbol = useCallback(() => {
        // allAlphaSymbol1CharInfos からランダムに次のターゲットを選択
        if (allAlphaSymbol1CharInfos.length > 0) {
            const randomIndex = Math.floor(Math.random() * allAlphaSymbol1CharInfos.length);
            const nextTarget = allAlphaSymbol1CharInfos[randomIndex];
            setRandomTargetInfo(nextTarget); // ランダムターゲットを更新
            console.log(`[AlphaSymbolPractice1 advanceToNextSymbol] New random target selected: ${nextTarget.char}`);
        }
        setStage('initialSymbolKey'); // Always expect "記号" key first for the new symbol
        setStatus('waiting');
    }, [setRandomTargetInfo, setStage, setStatus]); // allAlphaSymbol1CharInfos is stable

    // リセット関数
    const resetPractice = useCallback(() => {
        setStage('initialSymbolKey');
        setStatus('idle');
        setRandomTargetInfo(null); // ランダムターゲットをリセット
        // 通常モードのターゲットは normalModeTargetInfo で再計算される
    }, [setStage, setStatus, setRandomTargetInfo]);

    // 初期化とアクティブ状態の監視
    useEffect(() => {
        if (!isActive && prevIsActiveRef.current) {
            resetPractice();
        }
        if (isActive) { // isActive が true の場合のみ処理
            if (!prevIsActiveRef.current) { // ちょうどアクティブになった場合
                setStatus('waiting');
                if (isRandomMode) {
                    advanceToNextRandomSymbol(); // ランダムモードなら最初のターゲットを選択
                } else {
                    // 通常モードの場合、currentTargetInfo は normalModeTargetInfo で gIdx, dIdx に基づいて設定される
                    // ターゲットが正常に設定されていればステージを初期化
                    if (normalModeTargetInfo) {
                        setStage('initialSymbolKey');
                    }
                }
            } else if (isRandomMode && !randomTargetInfo) { // ランダムモードでターゲットがない場合 (リセット後など)
                advanceToNextRandomSymbol();
            } else if (!isRandomMode && (!normalModeTargetInfo || gIdx !== prevGIdxRef.current || dIdx !== prevDIdxRef.current)) {
                // 通常モードでターゲットがない、またはインデックスが変わった場合
                // currentTargetInfo は normalModeTargetInfo で更新されるので、ステージをリセット
                setStage('initialSymbolKey');
                setStatus('waiting');
            }
        } else { // Not active
            // If it became inactive, ensure status is idle. resetPractice handles other state.
            if (prevIsActiveRef.current) { // Was active, now inactive
                 setStatus('idle');
            }
        }
        prevIsActiveRef.current = isActive; // Update ref at the end
        prevGIdxRef.current = gIdx; // gIdx, dIdx の前回値も更新
        prevDIdxRef.current = dIdx;
        prevIsRandomModeRef.current = isRandomMode; // Update prevIsRandomModeRef
    }, [isActive, isRandomMode, gIdx, dIdx, resetPractice, setStatus, advanceToNextRandomSymbol, randomTargetInfo, normalModeTargetInfo, setStage]);

    // 入力処理
    const handleInput = useCallback((inputInfo: PracticeInputInfo): PracticeInputResult => {
        if (!isActive || !actualSymbolTarget) {
            return { isExpected: false, shouldGoToNext: false, errorLayer: null };
        }
        if (inputInfo.type !== 'release') {
            return { isExpected: false, shouldGoToNext: false, errorLayer: null };
        }

        const pressCode = inputInfo.pressCode; // 0-based
        const layer9 = layers[9] ?? [];
        const pressedKeyOnLayer9 = layer9[pressCode]?.trim();

        let isExpected = false;
        let nextStatus: PracticeStatus = status;
        let errorLayer: number | null = null;
        // let shouldGoToNextSymbol = false; // This variable was unused
        let isCorrectAndFinalInputForSymbol = false; // Flag to indicate the actual target symbol was correctly pressed

        if (stage === 'initialSymbolKey') {
            if (pressedKeyOnLayer9 === '記号') {
                isExpected = true;
                setStage('targetSymbolChar'); // Transition to expect the symbol character
                nextStatus = 'waiting';
            } else {
                isExpected = false;
                nextStatus = 'incorrect';
                errorLayer = 9;
            }
        } else if (stage === 'targetSymbolChar') {
            if (pressedKeyOnLayer9 && pressedKeyOnLayer9 === actualSymbolTarget) {
                isExpected = true;
                nextStatus = 'correct';
                isCorrectAndFinalInputForSymbol = true; // Mark that the final input for the symbol was correct
                console.log(`[AlphaSymbolPractice1 handleInput] Correct target symbol: ${actualSymbolTarget}.`);
                if (isRandomMode) {
                    console.log(`  [AlphaSymbolPractice1 handleInput] Random mode: Advancing to next random symbol.`);
                    advanceToNextRandomSymbol(); // ランダムモードなら次のターゲットへ
                } else {
                    console.log(`  [AlphaSymbolPractice1 handleInput] Normal mode: Signaling to advance (shouldGoToNext will be true).`);
                    // Normal mode progression is handled by usePracticeManagement via shouldGoToNext: true
                }
            } else {
                isExpected = false;
                nextStatus = 'incorrect';
                errorLayer = 9;
                setStage('initialSymbolKey'); // 不正解時は「記号」キー入力に戻す
            }
        }

        setStatus(nextStatus);

        // Only advance in normal mode if the specific target symbol (not just "記号" key) was correctly entered.
        const shouldAdvance = !isRandomMode && isCorrectAndFinalInputForSymbol;
        if (shouldAdvance) {
            console.log(`[AlphaSymbolPractice1 handleInput] Setting stage to initialSymbolKey for next char.`);
            setStage('initialSymbolKey'); // Reset stage for the next symbol in normal mode
        }

        return { isExpected, shouldGoToNext: shouldAdvance, errorLayer };
    }, [
        isActive,
        layers,
        actualSymbolTarget,
        stage,
        setStatus,
        advanceToNextRandomSymbol,
        status,
        isRandomMode, setStage // setStage を依存配列に追加
    ]);

    // ハイライト処理
    const getHighlightClassName = useCallback((keyName: string, layoutIndex: number): PracticeHighlightResult => {
        const noHighlight: PracticeHighlightResult = { className: null, overrideKey: null };
        if (!isActive) { // 練習がアクティブでない場合はハイライトしない
            return noHighlight;
        }

        // レイヤー9のみを対象
        if (layoutIndex === 9) {
            const trimmedKeyName = keyName.trim();
            if (stage === 'initialSymbolKey' && trimmedKeyName === '記号') {
                return { className: 'bg-blue-100', overrideKey: null }; // 青色でハイライト
            }
            if (stage === 'targetSymbolChar' && actualSymbolTarget && trimmedKeyName === actualSymbolTarget) {
                return { className: 'bg-blue-100', overrideKey: null }; // 青色でハイライト
            }
        }
        return noHighlight;
    }, [
        isActive,
        stage,
        actualSymbolTarget,
    ]);

    // 不正入力ターゲット判定 (この練習モードではエラーレイヤー上の全てのキーを不正入力としない)
    const isInvalidInputTarget = useCallback((pressCode: number, layoutIndex: number, keyIndex: number): boolean => {
        if (!isActive || status !== 'incorrect') return false;
        // エラーが発生したレイヤー(9)で、実際に押されたキー(pressCode)と、
        // 現在レンダリング中のキー(keyIndex)が一致する場合にtrue
        return layoutIndex === 9 && keyIndex === pressCode;
    }, [isActive, status]);

    const clearErrorState = useCallback(() => {
        setStatus('waiting'); // エラークリア時は waiting に戻す
    }, [setStatus]);

    return {
        headingChars,
        handleInput,
        getHighlightClassName,
        reset: resetPractice,
        status,
        targetChar: actualSymbolTarget ?? undefined, // Header should highlight the actual symbol
        errorHighlightLayer: status === 'incorrect' ? 9 : null,
        isInvalidInputTarget,
        clearErrorState,
        targetLayerIndex: 9, // 常にレイヤー9をターゲットとする
        targetLayerIndices: [9], // 表示するレイヤーもレイヤー9のみ
    };
};

export default useAlphaSymbolPractice1;
