// /home/coffee/my-keymap-viewer/src/hooks/useGairaigoPractice.ts
import { useState, useEffect, useCallback, useMemo, useRef } from 'react';
import {
    PracticeHookProps, PracticeHookResult, PracticeInputInfo, PracticeInputResult,
    getHidKeyCodes,
    PracticeHighlightResult,
    CharInfoGairaigo,
    allGairaigoCharInfos,
    HeadingCharDisplayInfo, // Import HeadingCharDisplayInfo
    PracticeStage, // <<< PracticeStage をインポート
} from './usePracticeCommons';
import { gairaigoPracticeData, GairaigoPracticeTarget } from '../data/keymapData';

// import { stat } from 'fs'; // fsモジュールはブラウザ環境では使えないためコメントアウトまたは削除

const useGairaigoPractice = ({
    gIdx, dIdx, isActive, side, layers, kb, isRandomMode // isRandomMode を受け取る
}: PracticeHookProps): PracticeHookResult => {
    const [stage, setStage] = useState<PracticeStage>('key1'); // Use common PracticeStage
    const [pressedKeys, setPressedKeys] = useState<Map<number, number>>(new Map());
    const [randomTarget, setRandomTarget] = useState<CharInfoGairaigo | null>(null);
    const prevGIdxRef = useRef(gIdx); // Re-add declaration
    const prevDIdxRef = useRef(dIdx); // Re-add declaration
    // prevGIdxRef and prevDIdxRef are used in the main useEffect for change detection
    const isInitialMount = useRef(true);
    const prevIsRandomModeRef = useRef(isRandomMode);
    // const prevIndicesRef = useRef({ gIdx, dIdx }); // Store previous gIdx and dIdx
    const prevIsActiveRef = useRef(isActive); // isActive の変更を追跡 (命名変更)
    const [errorHighlightLayer, setErrorHighlightLayer] = useState<number | null>(null); // エラーハイライト用
    const [status, setStatus] = useState<'waiting' | 'correct' | 'incorrect'>('waiting'); // 練習の状態

    const selectNextRandomTarget = useCallback(() => {
        if (allGairaigoCharInfos.length > 0) {
            const randomIndex = Math.floor(Math.random() * allGairaigoCharInfos.length);
            const nextTarget = allGairaigoCharInfos[randomIndex];
            console.log(`[Gairaigo selectNextRandomTarget] New random target: ${nextTarget.char}`);
            setRandomTarget(nextTarget);
            setStage('key1'); // 常に最初のステージから
            setPressedKeys(new Map()); // キー押下状態もリセット
            setErrorHighlightLayer(null);
        } else {
            setRandomTarget(null);
        }
    }, [setRandomTarget, setStage, setPressedKeys]);
    
    const reset = useCallback(() => {
        setStage('key1');
        setPressedKeys(new Map());
        setRandomTarget(null);
        prevGIdxRef.current = -1;
        prevDIdxRef.current = -1;
        isInitialMount.current = true;
        prevIsRandomModeRef.current = false;
        setErrorHighlightLayer(null);
        setStatus('waiting');
        // prevIsActiveRef.current は useEffect の最後で更新されるのでここでは不要
    }, [setStage, setPressedKeys, setRandomTarget]);

    // Helper to calculate target for normal mode
    const calculateNormalModeTarget = useCallback((
        currentGIdx: number,
        currentDIdx: number,
        currentIsActive: boolean,
    ): CharInfoGairaigo | null => {
        if (!currentIsActive || currentGIdx < 0 || currentGIdx >= gairaigoPracticeData.length) return null;
        const currentGroup = gairaigoPracticeData[currentGIdx];
        if (!currentGroup || !currentGroup.targets || currentDIdx < 0 || currentDIdx >= currentGroup.targets.length) {
            return null;
        }
        const gptTarget = currentGroup.targets[currentDIdx];
        return gptTarget ? { ...gptTarget, type: 'gairaigo' } : null;
    }, []); // No dependencies, it's a pure function based on arguments

    // Define currentTargetInfo based on mode and state/props
    const currentTargetInfo = useMemo((): CharInfoGairaigo | null => {
        if (isRandomMode) {
            return randomTarget;
        } else {
            return calculateNormalModeTarget(gIdx, dIdx, isActive);
        }
    }, [isRandomMode, randomTarget, gIdx, dIdx, isActive, calculateNormalModeTarget]);

    useEffect(() => {
        if (!isActive && prevIsActiveRef.current) {
            reset();
        } else if (isActive) {
            const gIdxChanged = prevGIdxRef.current !== gIdx;
            const dIdxChanged = prevDIdxRef.current !== dIdx;
            const randomModeChanged = prevIsRandomModeRef.current !== isRandomMode;
            const justActivated = !prevIsActiveRef.current && isActive;
            if (randomModeChanged) {
                reset();
                if (isRandomMode) {
                    selectNextRandomTarget();
                }
            } else if (justActivated) { // 練習モードがアクティブになった直後 (isActive が false -> true に変わった)
                if (isRandomMode) {
                    if (!randomTarget) selectNextRandomTarget(); // If no target yet
                } else {
                    // Normal mode: currentTargetInfo will be calculated based on gIdx/dIdx
                    setStage('key1');
                    setPressedKeys(new Map());
                    setStatus('waiting');
                    setErrorHighlightLayer(null);
                }
                isInitialMount.current = false; // 初期マウントフラグを解除
            } else if (!isRandomMode && (gIdxChanged || dIdxChanged)) {
                // Normal mode, indices changed: currentTargetInfo will be recalculated
                setStage('key1');
                setPressedKeys(new Map()); // 押下キーもリセット
                setStatus('waiting');
                setErrorHighlightLayer(null);
            } else if (isRandomMode && !randomTarget && !isInitialMount.current) { // ランダムモードでターゲットがない場合 (初期マウント後)
                selectNextRandomTarget();
            }
        }

        prevIsActiveRef.current = isActive;
        prevGIdxRef.current = gIdx;
        prevDIdxRef.current = dIdx;
        // prevIndicesRef.current = { gIdx, dIdx }; // Update prevIndicesRef here
        prevIsRandomModeRef.current = isRandomMode;
    }, [isActive, gIdx, dIdx, isRandomMode, reset, selectNextRandomTarget, randomTarget, prevGIdxRef, prevDIdxRef]); // Added prevGIdxRef, prevDIdxRef
    
    // currentTarget is now currentTargetInfo
    const expectedKey1Codes = useMemo(() => currentTargetInfo ? getHidKeyCodes(currentTargetInfo.keys[0], layers, kb, side) : [], [currentTargetInfo, layers, kb, side]);
    const expectedKey2Codes = useMemo(() => currentTargetInfo ? getHidKeyCodes(currentTargetInfo.actualSecondKey, layers, kb, side) : [], [currentTargetInfo, layers, kb, side]);
    const expectedKey3Codes = useMemo(() => {
        if (!currentTargetInfo) return [];
        const keyToUse = currentTargetInfo.actualThirdKey ?? currentTargetInfo.keys[2];
        return getHidKeyCodes(keyToUse, layers, kb, side);
    }, [currentTargetInfo, layers, kb, side]);
    const expectedKey4Codes = useMemo(() => {
        if (currentTargetInfo && currentTargetInfo.keys.length > 3 && currentTargetInfo.keys[3]) {
            return getHidKeyCodes(currentTargetInfo.keys[3], layers, kb, side);
        }
        return [];
    }, [currentTargetInfo, layers, kb, side]);


    const handleInput = useCallback((info: PracticeInputInfo): PracticeInputResult => {
        if (!isActive || !currentTargetInfo) { // Use currentTargetInfo
            setErrorHighlightLayer(null);
            return { isExpected: false, shouldGoToNext: undefined, errorLayer: null };
        }

        let isExpected = false;
        let shouldGoToNext_final: boolean | undefined = undefined;
        const { type, pressCode } = info;
        let errorPressCodeToReturn: number | undefined = undefined;
        let errorExpectedLayerToReturn: number | undefined = undefined;
        let errorLayerForHook: number | null = null;

        if (type === 'press') {
            setPressedKeys(prev => new Map(prev).set(pressCode, Date.now()));
            return { isExpected: false, shouldGoToNext: undefined, errorLayer: currentTargetInfo ? errorHighlightLayer : null }; // Use currentTargetInfo
        }

        // --- Release Event ---
        let nextInternalStage: PracticeStage = stage;

        if (stage === 'key1') {
            if (expectedKey1Codes.includes(pressCode + 1)) { // pressCode を 1-based にして比較
                nextInternalStage = 'key2';
                isExpected = true;
                errorLayerForHook = null;
            } else {
                console.log(`[Gairaigo handleInput key1] Key1 NO MATCH.`);
                nextInternalStage = 'key1';
                errorPressCodeToReturn = pressCode;
                errorExpectedLayerToReturn = 2; // Key1 is expected on layer 2
                errorLayerForHook = 2;
            }
        } else if (stage === 'key2') {
            if (expectedKey2Codes.includes(pressCode + 1)) { // pressCode を 1-based にして比較
                isExpected = true;
                errorLayerForHook = null;
                if (currentTargetInfo.keys.length > 2) { // Use currentTargetInfo
                    nextInternalStage = 'key3';
                } else {
                    nextInternalStage = 'key1';
                    if (isRandomMode) {
                        selectNextRandomTarget();
                        shouldGoToNext_final = false;
                    } else {
                        // 2打鍵で完了する文字の場合、ここで練習を進める
                        shouldGoToNext_final = true;
                    }
                }
            } else {
                nextInternalStage = 'key1';
                errorPressCodeToReturn = pressCode;
                errorExpectedLayerToReturn = 2; // Key2 is expected on layer 2
                errorLayerForHook = 2;
            }
        } else if (stage === 'key3') {
            if (expectedKey3Codes.includes(pressCode + 1)) { // pressCode を 1-based にして比較
                isExpected = true;
                errorLayerForHook = null;
                if (currentTargetInfo.keys.length > 3) { // Use currentTargetInfo
                    nextInternalStage = 'key4';
                } else {
                    nextInternalStage = 'key1';
                    if (isRandomMode) {
                        selectNextRandomTarget();
                        shouldGoToNext_final = false;
                    } else {
                        // 3打鍵で完了する文字の場合、ここで練習を進める
                        shouldGoToNext_final = true;
                    }
                }
            } else {
                nextInternalStage = 'key1';
                errorPressCodeToReturn = pressCode;
                const thirdKey = currentTargetInfo.actualThirdKey ?? currentTargetInfo.keys[2]; // Use currentTargetInfo
                errorExpectedLayerToReturn = thirdKey === '” ° 小' ? 2 : (thirdKey.endsWith('段') ? 3 : 2);
                errorLayerForHook = errorExpectedLayerToReturn;
            }
        } else if (stage === 'key4') {
            if (expectedKey4Codes.includes(pressCode + 1)) { // pressCode を 1-based にして比較
                isExpected = true;
                errorLayerForHook = null;
                nextInternalStage = 'key1';
                if (isRandomMode) {
                    selectNextRandomTarget();
                    shouldGoToNext_final = false;
                } else {
                    const currentGroupData = gairaigoPracticeData[gIdx];
                    const isLastPracticeItemInGroup = currentGroupData ? dIdx === currentGroupData.targets.length - 1 : false;
                    shouldGoToNext_final = isLastPracticeItemInGroup;
                }
            } else {
                nextInternalStage = 'key1';
                errorPressCodeToReturn = pressCode;
                errorExpectedLayerToReturn = 3; // Key4 is expected on layer 3
                errorLayerForHook = 3;
            }
        }

        setErrorHighlightLayer(errorLayerForHook);
        setStatus(isExpected ? 'correct' : 'incorrect');

        if (nextInternalStage !== stage) {
            setStage(nextInternalStage);
        } else if (!isExpected && stage !== 'key1') {
            setStage('key1');
        }

        return {
            isExpected,
            shouldGoToNext: shouldGoToNext_final, // undefined の場合 App.tsx が gIdx, dIdx を進めない
            errorPressCode: errorPressCodeToReturn,
            errorExpectedLayer: errorExpectedLayerToReturn,
            errorLayer: errorLayerForHook, // フック内部のエラーレイヤーも返す
        };
    }, [
        isActive, stage, currentTargetInfo, // Use currentTargetInfo
        expectedKey1Codes, expectedKey2Codes, expectedKey3Codes, expectedKey4Codes,
        setStage, isRandomMode, selectNextRandomTarget, gIdx, dIdx, setErrorHighlightLayer, setStatus, errorHighlightLayer
    ]);


    const headingChars = useMemo((): HeadingCharDisplayInfo[] => {
        if (!isActive) return [];
        const currentTargetCharacter = currentTargetInfo?.char;

        if (isRandomMode) {
            if (!currentTargetCharacter) return [];
            // For random mode, split the target character and mark ALL characters as practice targets.
            return currentTargetCharacter.split('').map((char, index) => {
                return { char, isPracticeTarget: true };
            });
        } else {
            // For normal mode, use gIdx to get headerChars and dIdx to mark the target
            const currentGroup = gairaigoPracticeData[gIdx] ?? null;
            if (!currentGroup || !currentGroup.headerChars) return [];
            return currentGroup.headerChars.map(char => ({
                char,
                isPracticeTarget: char === currentTargetCharacter
            }));
        }
    }, [isActive, isRandomMode, randomTarget, gIdx, dIdx, currentTargetInfo, stage]); // Use currentTargetInfo and add stage

    const getHighlightClassName = useCallback((key: string, layoutIndex: number): PracticeHighlightResult => {
        const noHighlight: PracticeHighlightResult = { className: null, overrideKey: null };
        if (!isActive || !currentTargetInfo) { // Use currentTargetInfo
            return noHighlight;
        }
        if (errorHighlightLayer === layoutIndex) {
            return noHighlight; // エラーレイヤーのキーはハイライトしない (isInvalidInputTarget で処理)
        }

        const currentStageForHighlight = stage;

        let expectedKeyName = '';
        let displayKeyName = '';
        let targetLayoutIndex: number | null = null;

        if (currentStageForHighlight === 'key1') {
            expectedKeyName = currentTargetInfo.keys[0]; // Use currentTargetInfo
            displayKeyName = currentTargetInfo.keys[0] ?? ''; // Use currentTargetInfo
            targetLayoutIndex = 2;
        } else if (currentStageForHighlight === 'key2') {
            expectedKeyName = currentTargetInfo.actualSecondKey; // Use currentTargetInfo
            displayKeyName = currentTargetInfo.keys[1]; // Use currentTargetInfo
            targetLayoutIndex = 2;
        } else if (currentStageForHighlight === 'key3') {
            expectedKeyName = currentTargetInfo.actualThirdKey ?? currentTargetInfo.keys[2]; // Use currentTargetInfo
            displayKeyName = currentTargetInfo.keys[2]; // Use currentTargetInfo
            if (currentTargetInfo.actualThirdKey === '” ° 小' || (!currentTargetInfo.actualThirdKey && currentTargetInfo.keys[2] === '” ° 小')) { // Use currentTargetInfo
                targetLayoutIndex = 2;
            } else if ((currentTargetInfo.actualThirdKey && currentTargetInfo.actualThirdKey.endsWith('段')) || (!currentTargetInfo.actualThirdKey && currentTargetInfo.keys[2].endsWith('段'))) { // Use currentTargetInfo
                targetLayoutIndex = 3;
            } else {
                 targetLayoutIndex = 2;
            }
        } else if (currentStageForHighlight === 'key4') {
            const fourthKey = currentTargetInfo.keys.length > 3 ? currentTargetInfo.keys[3] : undefined; // Use currentTargetInfo
            expectedKeyName = typeof fourthKey === 'string' ? fourthKey : '';
            displayKeyName = expectedKeyName;
            targetLayoutIndex = 3;
        }

        if (expectedKeyName === null || expectedKeyName === undefined) {
             return noHighlight;
        }

        if (layoutIndex === targetLayoutIndex && key === expectedKeyName) {
            return {
                className: 'bg-blue-100',
                overrideKey: displayKeyName
            };
        }

        return noHighlight;
    }, [stage, currentTargetInfo, isActive, errorHighlightLayer]); // Use currentTargetInfo

    const isInvalidInputTarget = useCallback((pressCode: number, layoutIndex: number, idx: number): boolean => {
        if (!isActive || !currentTargetInfo) return false; // Use currentTargetInfo
        if (errorHighlightLayer !== layoutIndex) return false; // エラーレイヤーでない場合は不正入力ターゲットではない

        // エラーレイヤー上で、実際に押されたキー (pressCode) と、現在レンダリング中のキー (idx) が一致する場合に true
        return idx === pressCode;
    }, [isActive, currentTargetInfo, errorHighlightLayer]);

    const clearErrorState = useCallback(() => {
        setErrorHighlightLayer(null);
        setStatus('waiting'); // エラークリア時は waiting に戻す
    }, [setErrorHighlightLayer, setStatus]);


    return {
        handleInput,
        headingChars,
        getHighlightClassName,
        reset,
        isInvalidInputTarget,
        currentTarget: currentTargetInfo ?? undefined, // Use currentTargetInfo
        targetChar: currentTargetInfo?.char ?? undefined, // Use currentTargetInfo
        stage,
        errorHighlightLayer,
        clearErrorState,
        status, // status を返す
        targetLayerIndex: useMemo(() => { // targetLayerIndex を動的に計算
            if (stage === 'key1' || stage === 'key2' || (stage === 'key3' && (currentTargetInfo?.actualThirdKey === '” ° 小' || (!currentTargetInfo?.actualThirdKey && currentTargetInfo?.keys[2] === '” ° 小')))) { // Use currentTargetInfo
                return 2;
            } else if (stage === 'key4' || (stage === 'key3' && ((currentTargetInfo?.actualThirdKey && currentTargetInfo.actualThirdKey.endsWith('段')) || (!currentTargetInfo?.actualThirdKey && currentTargetInfo?.keys[2].endsWith('段'))))) { // Use currentTargetInfo
                return 3;
            }
            return undefined; // 不明な場合は undefined
        }, [stage, currentTargetInfo]), // Use currentTargetInfo
        targetLayerIndices: useMemo(() => { // targetLayerIndices を動的に計算
            const defaultLayers = [2, 3];
            if (errorHighlightLayer !== null) {
                const layersToShow = new Set(defaultLayers);
                layersToShow.add(errorHighlightLayer);
                return Array.from(layersToShow);
            }
            return defaultLayers;
        }, [errorHighlightLayer]),
    };
};

export default useGairaigoPractice;
