// /home/coffee/my-keymap-viewer/src/hooks/useKigoPractice1.ts
import { useMemo, useState, useEffect, useCallback, useRef } from 'react';
import { kigoPractice1Data, functionKeyMaps } from '../data/keymapData';
import {
    PracticeHookProps,
    PracticeInputInfo,
    PracticeInputResult,
    PracticeHookResult,
    hid2GyouHRight_Kigo1,
    hid2GyouHLeft_Kigo1,
    hid2GyouVRight_Kigo1,
    hid2GyouVLeft_Kigo1,
    CharInfoKigo1,
    allKigo1CharInfos,
    PracticeHighlightResult,
    HeadingCharDisplayInfo, // HeadingCharDisplayInfo をインポート
    PracticeStatus, // PracticeStatus をインポート
} from './usePracticeCommons';

// 長押し判定時間 (ミリ秒)
const LONG_PRESS_DURATION = 300;

const useKigoPractice1 = ({
    gIdx,
    dIdx,
    isActive,
    side,
    kb,
    layers, // layers は props として受け取るが、このフック内では直接参照しない
    isRandomMode
}: PracticeHookProps): PracticeHookResult => {
    // この hid2Gyou はレイヤー6の記号を返すマップなので、キー名比較には直接使えない
    const hid2GyouKigoLayer = useMemo(() => {
        if (kb === 'tw-20v') {
            return side === 'left' ? hid2GyouVLeft_Kigo1 : hid2GyouVRight_Kigo1;
        } else { // TW-20H
            return side === 'left' ? hid2GyouHLeft_Kigo1 : hid2GyouHRight_Kigo1;
        }
    }, [side, kb]);
    const currentFunctionKeyMap = useMemo(() => {
        return functionKeyMaps[kb]?.[side] ?? {};
    }, [kb, side]);

    const prevGIdxRef = useRef(gIdx);
    const prevDIdxRef = useRef(dIdx);
    const isInitialMount = useRef(true);
    const prevIsActiveRef = useRef(isActive);
    const prevIsRandomModeRef = useRef(isRandomMode);
    const [status, setStatus] = useState<PracticeStatus>('idle');
    const [currentErrorHighlightLayer, setCurrentErrorHighlightLayer] = useState<number | null>(null);
    const pressInfoRef = useRef<{ code: number; timestamp: number } | null>(null); // 押下情報 (0-based pressCode)
    const longPressTimerRef = useRef<number | null>(null); // 長押しタイマーのID

    const [randomTarget, setRandomTarget] = useState<CharInfoKigo1 | null>(null);

    const clearLongPressTimer = useCallback(() => {
        if (longPressTimerRef.current !== null) {
            clearTimeout(longPressTimerRef.current);
            longPressTimerRef.current = null;
        }
    }, []);

    const selectNextRandomTarget = useCallback(() => {
        if (allKigo1CharInfos.length > 0) {
            const randomIndex = Math.floor(Math.random() * allKigo1CharInfos.length);
            const nextTarget = allKigo1CharInfos[randomIndex];
            setRandomTarget(nextTarget);
            pressInfoRef.current = null;
            clearLongPressTimer();
            setCurrentErrorHighlightLayer(null);
        } else {
            setRandomTarget(null);
        }
    }, [setRandomTarget, clearLongPressTimer]);

    // reset 関数
    const reset = useCallback(() => {
        prevGIdxRef.current = -1;
        prevDIdxRef.current = -1;
        isInitialMount.current = true;
        setRandomTarget(null);
        pressInfoRef.current = null;
        clearLongPressTimer();
        setStatus('idle'); // reset 時は idle に
        setCurrentErrorHighlightLayer(null);
        prevIsRandomModeRef.current = false; // reset 時は false
    }, [setRandomTarget, clearLongPressTimer]);

    useEffect(() => {
        // isActive が false になった最初のタイミングでリセット
        if (!isActive && prevIsActiveRef.current) {
            setStatus('idle'); // 非アクティブになったら idle に
            console.log(`[Kigo1 useEffect] Resetting state because isActive became false.`);
            reset();
        }

        if (isActive) {
            // isActive が true になった最初のタイミング、または依存関係の変更時
            if (isActive && !prevIsActiveRef.current) {
                isInitialMount.current = true; // 強制的に初期マウント扱い
            }

            const indicesChanged = gIdx !== prevGIdxRef.current || dIdx !== prevDIdxRef.current;
            const randomModeChangedToTrue = isRandomMode && !prevIsRandomModeRef.current;
            const randomModeChangedToFalse = !isRandomMode && prevIsRandomModeRef.current;

            if (randomModeChangedToFalse || (!isRandomMode && (isInitialMount.current || indicesChanged))) {
                // 通常モードやインデックス変更時は reset の一部を実行 (ターゲット選択はしない)
                pressInfoRef.current = null;
                clearLongPressTimer();
                setRandomTarget(null); // 通常モードではランダムターゲットをクリア
                setCurrentErrorHighlightLayer(null);
                prevGIdxRef.current = gIdx;
                prevDIdxRef.current = dIdx;
                isInitialMount.current = false;
                if (isActive) setStatus('waiting'); // Ensure status is waiting if becoming active in normal mode
            } else if (isRandomMode && (randomModeChangedToTrue || isInitialMount.current || !randomTarget)) {
                 console.log(`[Kigo1 useEffect] Random mode. randomModeChangedToTrue=${randomModeChangedToTrue}, isInitialMount=${isInitialMount.current}, !randomTarget=${!randomTarget}`);
                 selectNextRandomTarget(); // selectNextRandomTarget内でステージもリセットされる
                 pressInfoRef.current = null;
                 clearLongPressTimer();
                 setCurrentErrorHighlightLayer(null);
                 isInitialMount.current = false;
                 prevGIdxRef.current = gIdx;
                 prevDIdxRef.current = dIdx;
                 if (isActive) setStatus('waiting'); // Ensure status is waiting if becoming active in random mode
            }
        }

        // 最後に前回の値を更新
        prevIsActiveRef.current = isActive;
        prevIsRandomModeRef.current = isRandomMode;
        return () => {
            clearLongPressTimer();
        };
    }, [isActive, isRandomMode, gIdx, dIdx, randomTarget, reset, selectNextRandomTarget, setCurrentErrorHighlightLayer, clearLongPressTimer]);

    // 通常モード用
    const currentGroup = useMemo(() => {
        if (isRandomMode || !isActive || gIdx < 0 || gIdx >= kigoPractice1Data.length) return null;
        return kigoPractice1Data[gIdx];
    }, [isActive, isRandomMode, gIdx]);

    const currentInputDef = useMemo(() => {
        if (isRandomMode || !currentGroup?.inputs || dIdx < 0 || dIdx >= currentGroup.inputs.length) {
            return null;
        }
        return currentGroup.inputs[dIdx];
    }, [isRandomMode, currentGroup, dIdx]);

    // ランダム/通常モード共通
    const targetSymbolKeyName = useMemo(() => {
        if (isRandomMode) return randomTarget?.keyName ?? null;
        return currentInputDef?.keyName ?? null;
    }, [isRandomMode, randomTarget, currentInputDef]);

    // Calculate the target character for display/highlight
    const targetChar = useMemo(() => {
        if (!isActive) return null;
        if (isRandomMode) {
            return randomTarget?.char ?? null;
        } else {
            return currentGroup?.chars[dIdx] ?? null;
        }
    }, [isActive, isRandomMode, randomTarget, currentGroup, dIdx]);

    // ヘッダー文字
    const headingChars = useMemo((): HeadingCharDisplayInfo[] => {
        if (!isActive) {
            return [];
        } else if (isRandomMode) {
            return randomTarget ? [{ char: randomTarget.char, isPracticeTarget: true }] : [];
        } else {
            if (!currentGroup?.chars) return [];
            const groupChars = currentGroup.chars;

            const charsToDisplayObjects = groupChars.map((char, index) => ({
                char,
                isPracticeTarget: index === dIdx
            }));

            // 0文字または1文字の場合はスペースを挿入する必要なし
            if (charsToDisplayObjects.length <= 1) {
                return charsToDisplayObjects;
            }

            // 各文字オブジェクトの間にスペースオブジェクトを挿入
            return charsToDisplayObjects.flatMap((item, index) =>
                index < charsToDisplayObjects.length - 1 ? [item, { char: '\u00A0', isPracticeTarget: false }] : [item]
            );
        }
    }, [isActive, isRandomMode, randomTarget, currentGroup, dIdx]); // dIdx を依存配列に追加

    const handleInput = useCallback((inputInfo: PracticeInputInfo): PracticeInputResult => {
        const pressCode = inputInfo.pressCode;
        const targetLayerIndex = 6; // 記号の基本練習1はレイヤー6のみ対象
        // `layers` prop を直接参照する代わりに、`usePracticeCommons` からインポートされたキーマップデータや、
        // `props.layers` を使ってキー名を取得する必要がある。
        // ここでは `props.layers` を使うのが適切。
        const pressedKeyName = layers[targetLayerIndex]?.[inputInfo.pressCode]?.trim() ?? null;
        const currentTimestamp = inputInfo.timestamp;

        console.log(`[Kigo1 handleInput ENTRY #${currentTimestamp}] pressCode: 0x${pressCode.toString(16)} (dec: ${pressCode}), type: ${inputInfo.type}, targetSymbolKeyName: "${targetSymbolKeyName}", pressedKeyNameOnLayer${targetLayerIndex}: "${pressedKeyName}"`);
        console.log(`[Kigo1 handleInput INFO #${currentTimestamp}] isActive: ${isActive}, current status: ${status}, pressInfoRef: ${JSON.stringify(pressInfoRef.current)}`);
        if (!isActive || !targetSymbolKeyName) {
            console.log("[Kigo1 handleInput] Not active or no targetSymbolKeyName. Returning.");
            setCurrentErrorHighlightLayer(null);
            return { isExpected: false, shouldGoToNext: false, errorLayer: null };
        }

        let isExpected = false;
        let shouldGoToNext = false;
        let errorLayer: number | null = null;

        if (inputInfo.type === 'press') {
            if (pressInfoRef.current || longPressTimerRef.current) {
                clearLongPressTimer();
            }
            const pressedKeyOnPress = layers[targetLayerIndex]?.[inputInfo.pressCode]?.trim() ?? null;
            console.log(`[Kigo1 handleInput PRESS #${currentTimestamp}] pressedKeyOnPress: "${pressedKeyOnPress}", targetSymbolKeyName: "${targetSymbolKeyName}"`);

            if (pressedKeyOnPress === targetSymbolKeyName) {
                console.log(`[Kigo1 handleInput PRESS #${currentTimestamp}] Match! Setting pressInfoRef to {code: ${inputInfo.pressCode}, timestamp: ${inputInfo.timestamp}} and starting long press timer.`);
                pressInfoRef.current = { code: inputInfo.pressCode, timestamp: inputInfo.timestamp };
                longPressTimerRef.current = window.setTimeout(() => {
                    longPressTimerRef.current = null;
                    console.log("[Kigo1 handleInput PRESS] Long press timer fired (no action).");
                }, LONG_PRESS_DURATION);
            } else {
                console.log(`[Kigo1 handleInput PRESS #${currentTimestamp}] No match. Clearing pressInfoRef.`);
                pressInfoRef.current = null;
            }

        } else if (inputInfo.type === 'release') {
            clearLongPressTimer();
            console.log(`[Kigo1 handleInput RELEASE #${currentTimestamp}] pressInfoRef.current: ${JSON.stringify(pressInfoRef.current)}, inputInfo.pressCode: ${inputInfo.pressCode}, pressedKeyName: "${pressedKeyName}", targetSymbolKeyName: "${targetSymbolKeyName}"`);

            if (pressInfoRef.current && pressInfoRef.current.code === inputInfo.pressCode && pressedKeyName === targetSymbolKeyName) {
                console.log(`[Kigo1 handleInput RELEASE #${currentTimestamp}] Conditions met for duration check.`);
                const pressTimestamp = pressInfoRef.current.timestamp;
                const releaseTimestamp = inputInfo.timestamp;
                const duration = releaseTimestamp - pressTimestamp;
                console.log(`[Kigo1 handleInput RELEASE #${currentTimestamp}] Duration: ${duration}ms, LONG_PRESS_DURATION: ${LONG_PRESS_DURATION}ms`);

                if (duration >= LONG_PRESS_DURATION) {
                    console.log(`[Kigo1 handleInput RELEASE #${currentTimestamp}] Symbol key long press SUCCESS. Duration: ${duration}ms`);
                    isExpected = true;
                    setCurrentErrorHighlightLayer(null);
                    setStatus('correct');
                    if (isRandomMode) {
                        selectNextRandomTarget();
                        shouldGoToNext = false;
                    } else {
                        shouldGoToNext = true;
                    }
                } else {
                    console.log(`[Kigo1 handleInput RELEASE #${currentTimestamp}] Symbol key press too short. Duration: ${duration}ms`);
                    isExpected = false;
                    setCurrentErrorHighlightLayer(6);
                    errorLayer = 6;
                    setStatus('incorrect');
                }
            } else {
                console.log(`[Kigo1 handleInput RELEASE #${currentTimestamp}] Symbol key release FAILED. pressInfoRef.current: ${!!pressInfoRef.current}, code match: ${pressInfoRef.current?.code === inputInfo.pressCode}, name match: ${pressedKeyName === targetSymbolKeyName}`);
                isExpected = false;
                setCurrentErrorHighlightLayer(6);
                errorLayer = 6;
                setStatus('incorrect');
            }
            pressInfoRef.current = null;
        } else {
             console.log("[Kigo1 handleInput] Unexpected input type. Returning.");
            return { isExpected: false, shouldGoToNext: false, errorLayer: currentErrorHighlightLayer };
        }

        console.log(`[Kigo1 handleInput] End of logic. isExpected=${isExpected}, shouldGoToNext=${shouldGoToNext}, errorLayer=${errorLayer}`);
        return { isExpected, shouldGoToNext, errorLayer: errorLayer };
    }, [
       isActive, targetSymbolKeyName, layers, clearLongPressTimer,
        isRandomMode, selectNextRandomTarget, setCurrentErrorHighlightLayer, setStatus,
        gIdx, dIdx, status
    ]);

    const getHighlightClassName = useCallback((key: string, layoutIndex: number, keyIndex: number): PracticeHighlightResult => { // Added keyIndex
        const noHighlight: PracticeHighlightResult = { className: null, overrideKey: null };
        if (!isActive || !targetSymbolKeyName) {
            return noHighlight;
        }

        if (currentErrorHighlightLayer === layoutIndex) {
            return noHighlight;
        }

        if (layoutIndex === 6 && keyIndex === layers[6]?.findIndex(k => k.trim() === targetSymbolKeyName) && targetSymbolKeyName !== null) { // Use keyIndex and findIndex
            return { className: 'bg-blue-100', overrideKey: null };
        }
        return noHighlight;
    }, [
        isActive, targetSymbolKeyName, currentErrorHighlightLayer
    ]);

    const isInvalidInputTarget = useCallback((pressCode: number, layoutIndex: number, keyIndex: number): boolean => {
        if (!isActive) return false;

        if (currentErrorHighlightLayer === layoutIndex && layoutIndex === 6 && keyIndex === pressCode) {
            return true;
        }

        if (currentErrorHighlightLayer === layoutIndex && keyIndex === pressCode) {
            return true;
        }
        return false;
    }, [isActive, currentErrorHighlightLayer]);

    const clearErrorState = useCallback(() => {
        setCurrentErrorHighlightLayer(null);
    }, [setCurrentErrorHighlightLayer]);

    return {
        headingChars,
        handleInput,
        getHighlightClassName,
        reset,
        isInvalidInputTarget,
        status,
        targetChar: targetChar ?? undefined,
        errorHighlightLayer: currentErrorHighlightLayer,
        clearErrorState,
        targetLayerIndices: useMemo(() => {
            let determinedTargetLayerIndices = [6];
            if (currentErrorHighlightLayer !== null) {
                const layersToShow = new Set(determinedTargetLayerIndices);
                layersToShow.add(currentErrorHighlightLayer);
                determinedTargetLayerIndices = Array.from(layersToShow);
            }
            return determinedTargetLayerIndices;
        }, [currentErrorHighlightLayer]),
    };
};

export default useKigoPractice1;
