import { useState, useEffect, useRef, useCallback } from 'react';
import { HIDDevice, HIDInputReportEvent } from '../types/hid';

interface UseHidDeviceOptions {
    onInputReport: (event: HIDInputReportEvent) => void;
    autoConnect?: boolean; // 初期化時に自動で既存デバイスに接続試行するか
}

interface UseHidDeviceResult {
    device: HIDDevice | null;
    isConnected: boolean;
    requestDevice: () => Promise<HIDDevice | null>;
    sendReport: (reportId: number, data: Uint8Array) => Promise<void>;
    error: string | null;
}

export const useHidDevice = ({ onInputReport, autoConnect = true }: UseHidDeviceOptions): UseHidDeviceResult => {
    const [device, setDevice] = useState<HIDDevice | null>(null);
    const [isConnected, setIsConnected] = useState(false);
    const [error, setError] = useState<string | null>(null);
    const openingRef = useRef(false); // Device opening in progress
    const onInputReportRef = useRef(onInputReport);

    // Update ref when onInputReport prop changes
    useEffect(() => {
        onInputReportRef.current = onInputReport;
        console.log('[useHidDevice] onInputReport callback updated in ref.');
    }, [onInputReport]);

    // Stable event handler that calls the latest onInputReportRef.current
    const handleInputReportEvent = useCallback((event: HIDInputReportEvent) => {
        console.log('[useHidDevice] RAW INPUT REPORT EVENT FIRED (handleInputReportEvent):', event); // Log the raw event
        if (onInputReportRef.current) {
            onInputReportRef.current(event);
        } else {
            console.warn('[useHidDevice] onInputReportRef.current is null in handleInputReportEvent, not calling.');
        }
    }, []); // No dependencies, onInputReportRef.current is always up-to-date

    const openAndSetupDevice = useCallback(async (dev: HIDDevice): Promise<boolean> => {
        if (openingRef.current) {
            console.log(`[useHidDevice] openAndSetupDevice: Another open operation in progress. Skipping for ${dev.productName}.`);
            return false;
        }
        if (dev.opened) {
            console.log(`[useHidDevice] openAndSetupDevice: Device ${dev.productName} is already open. Setting up listener if not already set.`);
            // Ensure listener is set even if already open (e.g., re-render)
            if (dev.oninputreport !== handleInputReportEvent) {
                dev.oninputreport = handleInputReportEvent;
                console.log(`[useHidDevice] inputreport listener (re)assigned to already open device: ${dev.productName}`);
            }
            setDevice(dev);
            setIsConnected(true);
            return true;
        }

        openingRef.current = true;
        setError(null);
        console.log(`[useHidDevice] Attempting to open device: ${dev.productName}`);
        try {
            await dev.open();
            dev.oninputreport = handleInputReportEvent; // Assign stable event handler
            console.log('[useHidDevice] inputreport event listener (oninputreport) assigned.');
            setDevice(dev);
            setIsConnected(true);
            console.log('[useHidDevice] Device opened successfully and listener set:', dev.productName);
            return true;
        } catch (err) {
            console.error("[useHidDevice] Failed to open HID device:", err);
            setError(err instanceof Error ? err.message : String(err));
            setIsConnected(false);
            setDevice(null);
            return false;
        } finally {
            openingRef.current = false;
        }
    }, [handleInputReportEvent]); // Depends on the stable handleInputReportEvent

    const requestDevice = useCallback(async (): Promise<HIDDevice | null> => {
        if (!navigator.hid) {
            setError("WebHID is not supported by this browser.");
            console.error("[useHidDevice] WebHID not supported.");
            return null;
        }
        const filters = [{ usagePage: 0xff60, usage: 0x61 }];
        setError(null);
        console.log('[useHidDevice] Requesting HID device...');
        try {
            const devices = await navigator.hid.requestDevice({ filters });
            if (!devices || !devices.length) {
                console.log('[useHidDevice] No device selected or found.');
                return null;
            }
            const newDevice = devices[0];
            console.log(`[useHidDevice] Device selected: ${newDevice.productName}`);
            if (await openAndSetupDevice(newDevice)) {
                return newDevice;
            }
            return null; // openAndSetupDevice failed
        } catch (err) {
            console.error("[useHidDevice] Failed to request HID device:", err);
            setError(err instanceof Error ? err.message : String(err));
            return null;
        }
    }, [openAndSetupDevice]);

    // Effect for auto-connecting to previously permitted devices
    useEffect(() => {
        let isMounted = true;
        const attemptAutoConnect = async () => {
            if (autoConnect && !device && navigator.hid && isMounted) {
                console.log('[useHidDevice] Attempting auto-connect...');
                try {
                    const devices = await navigator.hid.getDevices();
                    if (isMounted && devices && devices.length > 0) {
                        console.log(`[useHidDevice] Found ${devices.length} previously permitted device(s). Trying to open the first one: ${devices[0].productName}`);
                        // Since we are in the `!device` block (device state is null),
                        // we just try to open the first found device.
                        // The checks for productId, vendorId, and opened status are not relevant here
                        // as there's no current `device` (state) to compare against.
                        await openAndSetupDevice(devices[0]);
                    } else if (isMounted) {
                        console.log('[useHidDevice] No previously permitted devices found for auto-connect.');
                    }
                } catch (err) {
                    if (isMounted) {
                        console.error('[useHidDevice] Error during auto-connect:', err);
                    }
                }
            }
        };

        attemptAutoConnect();
        return () => {
            isMounted = false;
        };
    }, [autoConnect, device, openAndSetupDevice, handleInputReportEvent]); // Add handleInputReportEvent

    // Effect for cleaning up the device connection
    useEffect(() => {
        const currentDevice = device; // Capture device for cleanup
        return () => {
            if (currentDevice && currentDevice.opened) {
                console.log(`[useHidDevice] Cleanup: Closing device ${currentDevice.productName}`);
                currentDevice.oninputreport = null; // Remove listener
                currentDevice.close().catch(err => console.error('[useHidDevice] Error closing device on cleanup:', err));
                // setDevice(null); // Optionally reset state here if appropriate
                // setIsConnected(false);
            }
        };
    }, [device]); // Re-run when device instance changes

    const sendReport = useCallback(async (reportId: number, data: Uint8Array) => {
        if (!device || !device.opened) {
            const msg = "[useHidDevice] Cannot send report: device not connected or not open.";
            console.error(msg);
            setError(msg);
            return;
        }
        try {
            await device.sendReport(reportId, data);
            // console.log(`[useHidDevice] Report sent. Report ID: ${reportId}`);
        } catch (err) {
            console.error("[useHidDevice] Failed to send HID report:", err);
            setError(err instanceof Error ? err.message : String(err));
        }
    }, [device]);

    return { device, isConnected, requestDevice, sendReport, error };
};
