// /home/coffee/my-keymap-viewer/src/hooks/useSeionPractice.ts
import { useState, useEffect, useCallback, useMemo, useRef } from 'react';
import {
    PracticeHookProps,
    PracticeHookResult,
    PracticeInputInfo,
    PracticeInputResult,
    PracticeHighlightResult,
    hid2Gyou,
    hid2Dan,
} from './usePracticeCommons';
import { gyouList, danOrder, gyouChars, danList } from '../data/keymapData';

type SeionStage = 'gyouInput' | 'danInput';

const useSeionPractice = ({
    gIdx: initialGIdx, // Props from usePracticeManagement, represents the current gyou index
    dIdx: initialDIdx, // Props from usePracticeManagement, represents the current dan index within the gyou
    isActive,
    side,
    // layers, // layers is not directly used in this hook's logic after hid2Gyou/hid2Dan are derived
    kb,
    isRandomMode,
}: PracticeHookProps): PracticeHookResult => {
    // Internal state for gIdx and dIdx, synchronized with props.
    // This allows the hook to manage its own target selection in random mode,
    // while following props in normal mode.
    const [gIdx, setGIdx] = useState(initialGIdx);
    const [dIdx, setDIdx] = useState(initialDIdx);

    const [targetChar, setTargetChar] = useState<string | null>(null); // The actual character to type, e.g., "か"
    const [stage, setStage] = useState<SeionStage>('gyouInput'); // Current input stage: 'gyouInput' or 'danInput'
    const [randomTarget, setRandomTarget] = useState<{ char: string; gyouKey: string; danKey: string } | null>(null); // Stores the target in random mode
    const [currentErrorHighlightLayer, setCurrentErrorHighlightLayer] = useState<number | null>(null); // Layer to highlight on error

    // Refs to store previous prop values for change detection
    const prevGIdxRef = useRef(gIdx);
    const prevDIdxRef = useRef(dIdx);
    const isInitialMount = useRef(true); // Flag for initial mount
    const prevIsActiveRef = useRef(isActive);
    const prevIsRandomModeRef = useRef(isRandomMode);

    // Memoized expected Gyou key name (e.g., "あ行")
    const expectedGyouKey = useMemo(() => {
        if (!isActive) return null;
        if (isRandomMode) return randomTarget?.gyouKey ?? null;
        if (gIdx < 0 || gIdx >= gyouList.length) return null;
        return gyouList[gIdx];
    }, [isActive, isRandomMode, randomTarget, gIdx]);

    // Memoized expected Dan key name (e.g., "あ段")
    const expectedDanKey = useMemo(() => {
        if (!isActive) return null;
        if (isRandomMode) return randomTarget?.danKey ?? null;
        if (gIdx < 0 || gIdx >= gyouList.length) return null;

        const currentGyouKey = gyouList[gIdx];
        // Special handling for 'や行' as its dan keys don't map directly via danList index
        if (currentGyouKey === 'や行') {
            if (dIdx === 0) return 'あ段'; // や
            if (dIdx === 1) return 'う段'; // ゆ
            if (dIdx === 2) return 'お段'; // よ
            return null;
        }
        // For other gyou, determine danKey based on the character at dIdx
        const charsForGyou = gyouChars[currentGyouKey]; // e.g., ['あ', 'い', 'う', 'え', 'お']
        if (!charsForGyou || dIdx < 0 || dIdx >= charsForGyou.length) return null;

        const charToFind = charsForGyou[dIdx]; // The character we are looking for, e.g., 'い'
        const danOrderForGyou = danOrder[currentGyouKey]; // e.g., ['あ', 'い', 'う', 'え', 'お']
        if (!danOrderForGyou) return null;

        const charIndexInDanOrder = danOrderForGyou.indexOf(charToFind);
        if (charIndexInDanOrder === -1 || charIndexInDanOrder >= danList.length) return null;

        return danList[charIndexInDanOrder]; // e.g., "い段"
    }, [isActive, isRandomMode, randomTarget, gIdx, dIdx]);

    // Function to update the target character and input stage
    const updateTargetAndStage = useCallback(() => {
        // console.log(`[SeionPractice updateTargetAndStage] isActive: ${isActive}, isRandomMode: ${isRandomMode}, gIdx(state): ${gIdx}, dIdx(state): ${dIdx}`);
        if (!isActive) {
            setTargetChar(null);
            setRandomTarget(null);
            setStage('gyouInput');
            return;
        }

        if (isRandomMode) {
            const allSeion: { char: string; gyouKey: string; danKey: string }[] = [];
            gyouList.forEach((gyouKey) => {
                const actualCharsInGyou = gyouChars[gyouKey];
                if (actualCharsInGyou) {
                    actualCharsInGyou.forEach((char) => {
                        let determinedDanKey: string | null = null;
                        if (gyouKey === 'や行') {
                            if (char === 'や') determinedDanKey = 'あ段';
                            else if (char === 'ゆ') determinedDanKey = 'う段';
                            else if (char === 'よ') determinedDanKey = 'お段';
                        } else {
                            const danOrderForCurrentGyou = danOrder[gyouKey];
                            if (danOrderForCurrentGyou) {
                                const charDisplayIndex = danOrderForCurrentGyou.indexOf(char);
                                if (charDisplayIndex !== -1 && charDisplayIndex < danList.length) {
                                    determinedDanKey = danList[charDisplayIndex];
                                }
                            }
                        }
                        if (determinedDanKey) {
                            allSeion.push({ char, gyouKey, danKey: determinedDanKey });
                        }
                    });
                }
            });

            if (allSeion.length > 0) {
                const randomIndex = Math.floor(Math.random() * allSeion.length);
                const newTarget = allSeion[randomIndex];
                setRandomTarget(newTarget);
                setTargetChar(newTarget.char);
                // console.log(`[SeionPractice updateTargetAndStage RANDOM] Set randomTarget: ${newTarget.char}, targetChar: ${newTarget.char}`);
            } else {
                console.error("[Seion Random] No seion characters found for random mode.");
                setRandomTarget(null);
                setTargetChar(null);
            }
        } else { // Normal mode
            if (gIdx < 0 || gIdx >= gyouList.length) {
                setTargetChar(null);
                setRandomTarget(null);
                return;
            }
            const currentGyouKey = gyouList[gIdx];
            const charsForGyou = gyouChars[currentGyouKey];
            if (!charsForGyou || dIdx < 0 || dIdx >= charsForGyou.length) {
                setTargetChar(null);
                setRandomTarget(null);
                return;
            }
            const char = charsForGyou[dIdx];
            setTargetChar(char);
            setRandomTarget(null); // Clear random target in normal mode
            // console.log(`[SeionPractice updateTargetAndStage NORMAL] Set targetChar: ${char} for gIdx=${gIdx}, dIdx=${dIdx}`);
        }
        setStage('gyouInput'); // Always reset stage to gyouInput when target changes
    }, [isActive, gIdx, dIdx, isRandomMode]); // Dependencies: internal gIdx, dIdx

    // Effect to synchronize props (initialGIdx, initialDIdx) with internal state (gIdx, dIdx)
    useEffect(() => {
        if (initialGIdx !== gIdx) setGIdx(initialGIdx);
        if (initialDIdx !== dIdx) setDIdx(initialDIdx);
    }, [initialGIdx, initialDIdx, gIdx, dIdx]);

    // Main effect for managing practice logic based on isActive, gIdx, dIdx, isRandomMode
    useEffect(() => {
        // console.log(`[SeionPractice useEffect MainLogic] isActive: ${isActive}, isRandomMode: ${isRandomMode}, gIdx(state): ${gIdx}, dIdx(state): ${dIdx}, isInitialMount: ${isInitialMount.current}, prevIsRandomMode: ${prevIsRandomModeRef.current}, prevIsActive: ${prevIsActiveRef.current}`);

        if (!isActive) {
            if (prevIsActiveRef.current) { // Only reset if it was previously active
                // console.log("[SeionPractice useEffect MainLogic] Practice became inactive. Resetting state.");
                setTargetChar(null);
                setStage('gyouInput');
                setRandomTarget(null);
                setCurrentErrorHighlightLayer(null);
                // Reset prev refs to ensure correct behavior on reactivation
                prevGIdxRef.current = -1;
                prevDIdxRef.current = -1;
                isInitialMount.current = true; // Treat next activation as initial mount
            }
        } else { // Practice is active
            const justActivated = !prevIsActiveRef.current;
            if (justActivated) {
                isInitialMount.current = true; // Force initial mount logic if just activated
            }

            const internalIndicesChanged = gIdx !== prevGIdxRef.current || dIdx !== prevDIdxRef.current;
            const randomModeChanged = isRandomMode !== prevIsRandomModeRef.current;

            let needsTargetUpdate = false;
            if (isRandomMode) {
                if (randomModeChanged || isInitialMount.current || !randomTarget) {
                    needsTargetUpdate = true;
                    // console.log(`[SeionPractice useEffect MainLogic] RANDOM mode. Needs target update. randomModeChanged=${randomModeChanged}, isInitialMount=${isInitialMount.current}, !randomTarget=${!randomTarget}`);
                }
            } else { // Normal Mode
                if (randomModeChanged || isInitialMount.current || internalIndicesChanged) {
                    needsTargetUpdate = true;
                    // console.log(`[SeionPractice useEffect MainLogic] NORMAL mode. Needs target update. randomModeChanged=${randomModeChanged}, isInitialMount=${isInitialMount.current}, internalIndicesChanged=${internalIndicesChanged}`);
                }
            }

            if (needsTargetUpdate) {
                updateTargetAndStage();
                setCurrentErrorHighlightLayer(null);
            }

            if (needsTargetUpdate || justActivated) {
                isInitialMount.current = false;
            }
            prevGIdxRef.current = gIdx;
            prevDIdxRef.current = dIdx;
        }
        // Update prev refs at the end of the effect
        prevIsActiveRef.current = isActive;
        prevIsRandomModeRef.current = isRandomMode;
    }, [isActive, gIdx, dIdx, isRandomMode, randomTarget, updateTargetAndStage]);


    // Input handling logic
    const handleInput = useCallback((inputInfo: PracticeInputInfo): PracticeInputResult => {
        if (!isActive || !targetChar || !expectedGyouKey || !expectedDanKey || inputInfo.type !== 'release') {
            setCurrentErrorHighlightLayer(null); // Clear any previous error if input is not processed
            return { isExpected: false, shouldGoToNext: undefined, errorLayer: null };
        }

        const { pressCode } = inputInfo;
        let isExpected = false;
        let errorLayer: number | null = null;
        let shouldGoToNext: boolean | undefined = undefined;

        // Get actual gyou/dan keys from HID code (0-based pressCode needs +1 for 1-based HID maps)
        const actualGyouKey = hid2Gyou(pressCode + 1, kb, side);
        const actualDanKey = hid2Dan(pressCode + 1, kb, side);

        if (stage === 'gyouInput') {
            if (actualGyouKey === expectedGyouKey) {
                isExpected = true;
                setStage('danInput');
                setCurrentErrorHighlightLayer(null);
                // shouldGoToNext remains undefined as the sequence is not complete
            } else {
                isExpected = false;
                setCurrentErrorHighlightLayer(2); // Error on gyouInput (layer 2)
                errorLayer = 2;
                // shouldGoToNext remains undefined
            }
        } else if (stage === 'danInput') {
            if (actualDanKey === expectedDanKey) {
                isExpected = true;
                setStage('gyouInput'); // Reset stage for the next character
                setCurrentErrorHighlightLayer(null);

                if (isRandomMode) {
                    updateTargetAndStage(); // Select next random target
                    shouldGoToNext = false; // App.tsx should not advance gIdx/dIdx in random mode
                } else { // Normal mode
                    // Determine if this is the last character in the current gyou
                    const currentGyouKeyFromProps = gyouList[initialGIdx]; // Use prop gIdx
                    const charsInCurrentGyou = gyouChars[currentGyouKeyFromProps];
                    if (charsInCurrentGyou && initialDIdx >= charsInCurrentGyou.length - 1) {
                        shouldGoToNext = true; // Last char, App.tsx should advance gIdx
                    } else {
                        shouldGoToNext = false; // Not last char, App.tsx should advance dIdx
                    }
                }
            } else {
                isExpected = false;
                setCurrentErrorHighlightLayer(3); // Error on danInput (layer 3)
                setStage('gyouInput'); // Reset to gyouInput on error
                errorLayer = 3;
                // shouldGoToNext remains undefined
            }
        }
        // console.log(`[SeionPractice handleInput] Returning: isExpected=${isExpected}, shouldGoToNext=${shouldGoToNext}, errorLayer=${errorLayer}`);
        return { isExpected, shouldGoToNext, errorLayer };
    }, [
        isActive, targetChar, stage, expectedGyouKey, expectedDanKey, kb, side,
        isRandomMode, updateTargetAndStage, initialGIdx, initialDIdx, // Use props gIdx/dIdx for shouldGoToNext logic
        setStage, setCurrentErrorHighlightLayer
    ]);

    // Reset function (can be called externally, e.g., by usePracticeManagement)
    const reset = useCallback(() => {
        setStage('gyouInput');
        setCurrentErrorHighlightLayer(null);
        // Target character and randomTarget will be reset by the main useEffect
        // when isActive becomes true or indices change.
        // If called while inactive, main useEffect will handle reset on next activation.
    }, [setStage, setCurrentErrorHighlightLayer]);

    // Characters to display in the heading
    const headingChars = useMemo(() => {
        if (!isActive) return [];
        if (isRandomMode) {
            return targetChar ? [targetChar] : [];
        }
        // Normal mode: use internal gIdx to get the current gyou's characters
        if (gIdx < 0 || gIdx >= gyouList.length) return [];
        const currentGyouKey = gyouList[gIdx];
        return gyouChars[currentGyouKey] ?? [];
    }, [isActive, isRandomMode, targetChar, gIdx]); // Depends on internal gIdx

    // Function to determine highlight class for a key
    const getHighlightClassName = useCallback((keyName: string, layoutIndex: number, keyIndex: number): PracticeHighlightResult => { // Added keyIndex
        const noHighlight: PracticeHighlightResult = { className: null, overrideKey: null };
        if (!isActive || !targetChar || currentErrorHighlightLayer === layoutIndex) {
            return noHighlight;
        }

        const currentStageForHighlight = stage; // Use current internal stage

        if (currentStageForHighlight === 'gyouInput') {
            if (keyName === expectedGyouKey && layoutIndex === 2) {
                return { className: 'bg-blue-100', overrideKey: null };
            }
        } else if (currentStageForHighlight === 'danInput') {
            if (keyName === expectedDanKey && layoutIndex === 3) {
                 return { className: 'bg-blue-100', overrideKey: null };
            }
        }
        return noHighlight;
    }, [isActive, targetChar, stage, expectedGyouKey, expectedDanKey, currentErrorHighlightLayer]); // keyIndex is not used in logic, so not a dependency

    // Function to determine if a key was an invalid input target
    const isInvalidInputTarget = useCallback((targetPressCode: number, currentLayoutIndex: number, currentKeyIndex: number): boolean => {
        if (!isActive || currentErrorHighlightLayer === null) return false;
        return currentErrorHighlightLayer === currentLayoutIndex && currentKeyIndex === targetPressCode;
    }, [isActive, currentErrorHighlightLayer]);

    // Function to clear the current error state
    const clearErrorState = useCallback(() => {
        setCurrentErrorHighlightLayer(null);
    }, [setCurrentErrorHighlightLayer]);

    // Memoized return value of the hook
    return useMemo(() => {
        let determinedTargetLayerIndices = [2, 3]; // Default for Seion
        if (currentErrorHighlightLayer !== null) {
            const layersToShow = new Set([2, 3]);
            layersToShow.add(currentErrorHighlightLayer);
            determinedTargetLayerIndices = Array.from(layersToShow);
        }

        return {
            headingChars,
            handleInput,
            getHighlightClassName: (keyName, layoutIndex, keyIndex) => getHighlightClassName(keyName, layoutIndex, keyIndex), // Adapt signature
            reset,
            isInvalidInputTarget,
            targetChar: targetChar ?? undefined, // Ensure undefined if null
            targetLayerIndices: determinedTargetLayerIndices,
            errorHighlightLayer: currentErrorHighlightLayer,
            clearErrorState,
            stage, // Expose stage for potential debugging or advanced logic by consumer
        };
    }, [
        headingChars, handleInput, getHighlightClassName, reset, isInvalidInputTarget,
        targetChar, stage, currentErrorHighlightLayer, clearErrorState
    ]);
};

export default useSeionPractice;
