/* 練習フックの共通プロパティ */
export interface PracticeHookProps {
    gIdx: number;
    dIdx: number;
    isActive: boolean;
    side: KeyboardSide;
    layers: string[][];
    kb: KeyboardModel;
    isRandomMode?: boolean;
    showKeyLabels: boolean; // Add showKeyLabels
    onAdvance?: () => void; // <<< App.tsx から進行を伝えるコールバックを受け取る
    isAlphaNumericMode?: boolean; // App.tsx から渡されるため追加
    practice?: PracticeMode | ''; // Add practice mode to common props
}

/* 練習フックの入力情報 */
export interface PracticeInputInfo {
    type: 'press' | 'release';
    timestamp: number;
    pressCode: number; // 押下時のHIDコード (0x01 ~ maxStartLayoutKeyCode)
    layerIndex: number;
}

/* 練習フックの入力処理結果 */
export interface PracticeInputResult {
  isExpected: boolean;
  shouldGoToNext?: boolean; // undefined の場合は App.tsx 側で dIdx/gIdx を進めない
  errorLayer?: number | null; // エラーが発生したレイヤー (エラーがない場合は null or undefined)
  errorPressCode?: number; // 不正解だったキーの pressCode (0-based)
  errorExpectedLayer?: number; // 不正解時に期待されていたレイヤー
}

export interface PracticeHighlightResult {
    className: string | null; // ハイライト用クラス名 (例: 'bg-blue-100')
    overrideKey: string | null; // 上書き表示するキー名 (null なら上書きしない)
}

// ▼▼▼ かなチャレンジ結果の型定義 ▼▼▼
export interface ChallengeResult {
    totalQuestions: number; // 問題数（クリアした数）
    totalCharsTyped: number; // 総入力文字数
    correctCharsCount?: number; // 正しく入力完了した文字数（かな/短文）
    correctCount: number; // 正解タイプ数
    missCount: number; // ミスタイプ数
    accuracy: number; // 正答率 (0-1)
    score: number; // 計算後のスコア
    cpm: number; // Characters Per Minute (入力速度)
    rankMessage: string; // ランクメッセージ
    maxConsecutiveCorrectChars?: number;
}
