/* 練習フックの共通戻り値型 */
export interface PracticeHookResult {
    // App.tsx で usePracticeManagement から分割代入されているプロパティ
    practice?: PracticeMode | ''; // オプショナルに変更 (個別の練習フックはこれを返さないため)
    gIdx?: number; // オプショナルに変更
    dIdx?: number; // オプショナルに変更
    isRandomMode?: boolean; // オプショナルに変更
    activePractice?: PracticeHookResult | null;
    headingChars: string[] | HeadingCharDisplayInfo[]; // Allow string array or array of objects
    isChallengeFinished?: boolean; // オプショナルに変更
    handlePracticeSelect?: (item: PracticeMode) => void; // オプショナルに変更
    toggleRandomMode?: () => void; // オプショナルに変更
    setPractice?: React.Dispatch<React.SetStateAction<PracticeMode | ''>>; // オプショナルに変更
    setGIdx?: React.Dispatch<React.SetStateAction<number>>; // オプショナルに変更
    setDIdx?: React.Dispatch<React.SetStateAction<number>>; // オプショナルに変更
    setIsRandomMode?: React.Dispatch<React.SetStateAction<boolean>>; // オプショナルに変更
    nextStage?: (currentPractice: PracticeMode) => void; // オプショナルに変更
    isRandomModeAvailable?: boolean; // オプショナルに変更

    // 個別の練習フックが返すプロパティ (共通化)
    targetChar?: string | object | null; // ターゲット文字またはオブジェクト
    targetLayerIndex?: number | null; // ターゲットレイヤーのインデックス
    targetLayerIndices?: number[]; // 表示するレイヤーのインデックス配列
    displayLayers?: string[][]; // 表示するレイヤーデータ (記号チャレンジ用など)
    getHighlightClassName: (keyName: string, layoutIndex: number, keyIndex: number) => PracticeHighlightResult;
    getHighlight?: () => HighlightInfo;
    getProgressInfo?: () => TanbunProgressInfo; // TanbunChallengePractice用
    clearErrorState?: () => void;
    handleInput: (info: PracticeInputInfo) => PracticeInputResult;
    reset?: () => void;
    isInvalidInputTarget: (targetPressCode: number, currentLayoutIndex: number, currentKeyIndex: number, pressedLayerIndex?: number) => boolean;
    status?: PracticeStatus;
    countdownValue?: number;
    challengeResults?: ChallengeResult | null;
    errorHighlightLayer?: number | null;
    currentTarget?: KanaTargetInfo | string | undefined; // Changed PracticeTargetObject to KanaTargetInfo
    stage?: PracticeStage | string;
    typedEndIndex?: number | null | undefined;
    isCapsLockOn?: boolean;
    capitalizedCharIndices?: Set<number>;
    errorLayer?: number | null;
    challengeTimer?: number;
    challengeTotalTime?: number;
    isSpecialRowHighlightActive?: boolean;
}

// Define KanaTargetInfo as a union of specific character info types
// This type is used by functions like getCurrentKanaTargetInfo
export type KanaTargetInfo =
    | CharInfoSeion
    | CharInfoYouon
    | CharInfoDakuon
    | CharInfoHandakuon
    | CharInfoSokuonKomoji
    | CharInfoYoudakuon
    | CharInfoYouhandakuon
    | CharInfoGairaigo // Add Gairaigo
    | CharInfoYouonKakucho // Add YouonKakucho
    | CharInfoKigo3 // Add CharInfoKigo3 for kigo practice 3
    // A generic kigo type for single symbols not covered by Kigo1/2/3 structures
    | { type: 'kigo'; char: string; keyName?: string; gyouKey?: string; layerIndex?: number; }
    | { type: 'unknown'; char: string };

// Define a common structure for heading characters that need more than just the string
export interface HeadingCharDisplayInfo {
  char: string;
  isPracticeTarget?: boolean; // Optional, as not all practices might use it
  isValidInGroup?: boolean;   // Add this for ゃゅょ拡張１
  // Add other common properties if needed, or allow any other props:
  [key: string]: any;
}

