// /home/coffee/my-keymap-viewer/src/hooks/useAlphabetChallengePractice.ts
import { useState, useEffect, useCallback, useRef, useMemo } from 'react';
import {
    PracticeHookProps,
    PracticeInputInfo,
    PracticeInputResult,
    PracticeHookResult,
    PracticeHighlightResult,
    ChallengeResult,
} from './usePracticeCommons';
import { alphabetPracticeGroups, alphaNumTanbunSentences } from '../data/keymapData'; // 英字練習グループと英数字短文をインポート

type ChallengeStatus = 'idle' | 'countdown' | 'running' | 'finished';
type AlphabetChallengeStage = 'waitingForLayer4Key' | 'waitingForLayer5Key';

const COUNTDOWN_SECONDS = 5;
const TRAINING_DURATION_MS = 60 * 1000; // 1分 (英字入力１分間トレーニング用)
const ALPHANUM_TRAINING_DURATION_MS = 3 * 60 * 1000; // 3分 (英数短文入力３分間トレーニング用)

// --- ランクメッセージ判定関数 (英字用) ---
const getRankMessageAlphabet = (score: number, accuracy: number): string => { // score は新しい総合スコアを想定
    // ランクメッセージの基準は新しいスコアに合わせて調整が必要
    if (score >= 10000 && accuracy >= 98) return "Typing Master! 驚異的！";
    if (score >= 7000 && accuracy >= 95) return "Excellent! 素晴らしい！";
    if (score >= 4000 && accuracy >= 90) return "Great! とても良い調子！";
    if (score >= 2000 && accuracy >= 85) return "Good! 良いですね！";
    if (score >= 1000) return "Nice! その調子！";
    return "Keep Trying! 練習あるのみ！";
};
// --- ランクメッセージ判定関数 (英数字短文用) ---
const getRankMessageAlphaNum = (score: number, accuracy: number): string => { // score は新しい総合スコアを想定
    // ランクメッセージの基準は新しいスコアに合わせて調整が必要
    if (score >= 20000 && accuracy >= 98) return "Typing God! 驚異的！";
    if (score >= 15000 && accuracy >= 97) return "Pro Typer! プロ級！";
    if (score >= 10000 && accuracy >= 95) return "Excellent! 素晴らしい！";
    if (score >= 7000 && accuracy >= 90) return "Great! とても良い調子！";
    if (score >= 4000 && accuracy >= 85) return "Good! 良いですね！";
    if (score >= 2000) return "Nice! その調子！";
    return "Keep Trying! 練習あるのみ！";
};


// ランダムなアルファベット1文字を選択する関数 (英字入力１分間トレーニング用)
const getRandomChar = (): string => {
    const allChars = alphabetPracticeGroups.join('');
    const randomIndex = Math.floor(Math.random() * allChars.length);
    return allChars[randomIndex];
};

interface UseAlphabetChallengePracticeProps extends PracticeHookProps {
    practiceName: '英字入力１分間トレーニング' | '英数短文入力３分間トレーニング';
}

const useAlphabetChallengePractice = ({
    isActive,
    side,
    kb,
    layers,
    practiceName, // 練習モード名を受け取る
}: UseAlphabetChallengePracticeProps): PracticeHookResult => {
    const [status, setStatus] = useState<ChallengeStatus>('idle');
    const [countdownValue, setCountdownValue] = useState<number>(COUNTDOWN_SECONDS);
    const [remainingTime, setRemainingTime] = useState<number>(
        practiceName === '英字入力１分間トレーニング' ? TRAINING_DURATION_MS : ALPHANUM_TRAINING_DURATION_MS
    );
    const [currentTargetChar, setCurrentTargetChar] = useState<string | null>(null);
    const [challengeStage, setChallengeStage] = useState<AlphabetChallengeStage>('waitingForLayer4Key');
    const [currentSentence, setCurrentSentence] = useState<string>(''); // 英数短文用
    const [currentSentenceIndex, setCurrentSentenceIndex] = useState<number>(0); // 英数短文用
    const [typedHistory, setTypedHistory] = useState<string[]>([]); // 英数短文用
    const [totalTypedCount, setTotalTypedCount] = useState(0); // 英数短文用: 正しく入力された文字数
    const [errorCount, setErrorCount] = useState(0); // 英数短文用: ミスタイプ数
    const [degreeSymbolStep, setDegreeSymbolStep] = useState(0); // 0: initial, 1: '記号' key pressed
    const [currentTargetGroupIndex, setCurrentTargetGroupIndex] = useState<number | null>(null);


    const countdownTimerRef = useRef<number | null>(null);
    const trainingTimerRef = useRef<number | null>(null);
    const startTimeRef = useRef<number | null>(null);
    const currentSentenceRef = useRef<string>(''); // 英数短文用

    const correctCharsTypedRef = useRef(0); // 英字入力１分間用: 正しく入力された総文字数
    const totalCharsAttemptedRef = useRef(0); // 英字入力１分間用: ユーザーが試みた総入力キー数
    const errorCountRef = useRef(0); // 英字入力１分間用: ミスタイプの総数
    const completedSentencesRef = useRef(0);
    const currentConsecutiveCorrectRef = useRef(0);
    const maxConsecutiveCorrectRef = useRef(0);

    const [challengeResults, setChallengeResults] = useState<ChallengeResult | null>(null);
    const prevIsActiveRef = useRef(isActive);

    const practiceSentences = useMemo(() => {
        if (practiceName === '英字入力１分間トレーニング') {
            return []; // このモードでは使わない
        }
        return alphaNumTanbunSentences; // 英数短文を使用
    }, [practiceName]);

    // 英数短文モードで、ターゲット文字に基づいてグループインデックスとチャレンジステージを更新するヘルパー関数
    const updateTargetGroupAndStageForAlphaNum = useCallback((char: string | null) => {
        console.log(`[DEBUG updateTargetGroupAndStageForAlphaNum ENTRY] practiceName: ${practiceName}, char: "${char}"`);
        if (practiceName !== '英数短文入力３分間トレーニング' || !char) { // practiceName のチェックは残す
            setCurrentTargetGroupIndex(null);
            setChallengeStage('waitingForLayer4Key'); // デフォルトはグループ入力待ち
            return;
        }

        if (char.match(/^[a-zA-Z]$/)) { // アルファベットの場合
            const groupIndex = alphabetPracticeGroups.findIndex(group => group.includes(char.toLowerCase()));
            setCurrentTargetGroupIndex(groupIndex !== -1 ? groupIndex : null);
            setChallengeStage('waitingForLayer4Key'); // アルファベットなら必ずグループキーから
        } else { // アルファベット以外（記号など）
            setCurrentTargetGroupIndex(null);
            setChallengeStage('waitingForLayer4Key'); // 記号の場合もデフォルトステージに戻す
        }
    }, [practiceName]);


    // 次のターゲット文字/短文を選択
    const selectNextTarget = useCallback(() => {
        if (practiceName === '英字入力１分間トレーニング') {
            console.log("[AlphabetChallenge selectNextTarget] Selecting next char for 英字入力１分間トレーニング");
            const nextChar = getRandomChar();
            setCurrentTargetChar(nextChar);
            setChallengeStage('waitingForLayer4Key');
        } else { // 英数短文入力３分間トレーニング
            if (practiceSentences.length > 0) {
                const randomIndex = Math.floor(Math.random() * practiceSentences.length);
                const sentence = practiceSentences[randomIndex];
                setCurrentSentence(sentence);
                currentSentenceRef.current = sentence;
                const firstChar = sentence[0];
                setCurrentTargetChar(firstChar);
                setCurrentSentenceIndex(0);
                setTypedHistory([]);
                setDegreeSymbolStep(0);
                updateTargetGroupAndStageForAlphaNum(firstChar);
            }
        }
    }, [practiceName, practiceSentences, updateTargetGroupAndStageForAlphaNum]);

    // カウントダウン処理
    useEffect(() => {
        if (isActive && status === 'countdown') {
            if (countdownValue > 0) {
                countdownTimerRef.current = window.setTimeout(() => {
                    setCountdownValue(prev => prev - 1);
                }, 1000);
            } else {
                setStatus('running');
                setRemainingTime(
                    practiceName === '英字入力１分間トレーニング' ? TRAINING_DURATION_MS : ALPHANUM_TRAINING_DURATION_MS
                );
                startTimeRef.current = Date.now();
                if (practiceName === '英字入力１分間トレーニング') {
                    correctCharsTypedRef.current = 0;
                    totalCharsAttemptedRef.current = 0;
                    errorCountRef.current = 0;
                    currentConsecutiveCorrectRef.current = 0;
                    completedSentencesRef.current = 0;
                    maxConsecutiveCorrectRef.current = 0;
                } else {
                    setTotalTypedCount(0);
                    setErrorCount(0);
                    currentConsecutiveCorrectRef.current = 0; // 英数短文でも初期化
                    maxConsecutiveCorrectRef.current = 0;     // 英数短文でも初期化
                }
                selectNextTarget();
            }
        }
        return () => {
            if (countdownTimerRef.current !== null) clearTimeout(countdownTimerRef.current);
        };
    }, [isActive, status, countdownValue, selectNextTarget, practiceName]);

    // トレーニング時間計測 & 終了処理
    useEffect(() => {
        if (isActive && status === 'running') {
            trainingTimerRef.current = window.setInterval(() => {
                const duration = practiceName === '英字入力１分間トレーニング' ? TRAINING_DURATION_MS : ALPHANUM_TRAINING_DURATION_MS;
                const elapsed = Date.now() - (startTimeRef.current ?? Date.now());
                const newRemaining = Math.max(0, duration - elapsed);
                setRemainingTime(newRemaining);

                if (newRemaining === 0) {
                    setStatus('finished');
                    if (trainingTimerRef.current) clearInterval(trainingTimerRef.current);

                    let results: ChallengeResult;
                    if (practiceName === '英字入力１分間トレーニング') {
                        const durationMinutes = (TRAINING_DURATION_MS / 1000) / 60;
                        const cpm = durationMinutes > 0 ? Math.round(correctCharsTypedRef.current / durationMinutes) : 0;
                        const totalPresses = totalCharsAttemptedRef.current;
                        const accuracyValue = totalPresses > 0
                            ? (totalPresses - errorCountRef.current) / totalPresses
                            : 0; // 0.0 ~ 1.0
                        console.log(`[AlphabetChallenge DEBUG Results 英字] correctChars: ${correctCharsTypedRef.current}, totalAttempted: ${totalCharsAttemptedRef.current}, errors: ${errorCountRef.current}, accuracyValue: ${accuracyValue}, cpm: ${cpm}`);
                        
                        const P_base = correctCharsTypedRef.current * 10;
                        const M_acc = Math.max(0, Math.pow(accuracyValue, 2));
                        const P_speed = Math.max(0, cpm - 50) * 5;
                        const P_combo = Math.log(1 + (maxConsecutiveCorrectRef.current ?? 0)) * 100;
                        const P_task = correctCharsTypedRef.current * 50; // 「英字入力」では達成課題数 = 正解文字数
                        const totalScore = Math.round((P_base + P_speed + P_combo + P_task) * M_acc);
                        const rankMessage = getRankMessageAlphabet(totalScore, accuracyValue * 100);

                        results = {
                            totalQuestions: correctCharsTypedRef.current,
                            totalCharsTyped: totalCharsAttemptedRef.current,
                            correctCharsCount: correctCharsTypedRef.current,
                            correctCount: correctCharsTypedRef.current,
                            missCount: errorCountRef.current,
                            accuracy: accuracyValue, // 0.0-1.0
                            score: totalScore,
                            cpm: cpm, // CPM を追加
                            maxConsecutiveCorrectChars: maxConsecutiveCorrectRef.current,
                            rankMessage: rankMessage,
                        };
                    } else { // 英数短文入力３分間トレーニング
                        const durationMinutes = (ALPHANUM_TRAINING_DURATION_MS / 1000) / 60;
                        const cpm = durationMinutes > 0 ? Math.round(totalTypedCount / durationMinutes) : 0;
                        const accuracyValue = (totalTypedCount + errorCount) > 0
                            ? Math.max(0, totalTypedCount / (totalTypedCount + errorCount))
                            : 0; // 0.0 ~ 1.0
                        console.log(`[AlphabetChallenge DEBUG Results 英数短文] totalTypedCount: ${totalTypedCount}, errorCount: ${errorCount}, accuracy: ${accuracyValue}, cpm: ${cpm}`);

                        const P_base_alphaNum = totalTypedCount * 10;
                        const M_acc_alphaNum = Math.max(0, Math.pow(accuracyValue, 2));
                        const P_speed_alphaNum = Math.max(0, cpm - 50) * 5;
                        const P_combo_alphaNum = Math.log(1 + (maxConsecutiveCorrectRef.current ?? 0)) * 100;
                        // 「英数短文」では達成課題数は別途カウントが必要。ここでは正解文字数(totalTypedCount)を使う。
                        // もし完了した短文数を P_task に使いたい場合は、そのカウントロジックが必要。
                        const P_task_alphaNum = totalTypedCount * 50;
                        const totalScore_alphaNum = Math.round((P_base_alphaNum + P_speed_alphaNum + P_combo_alphaNum + P_task_alphaNum) * M_acc_alphaNum);
                        const rankMessage = getRankMessageAlphaNum(totalScore_alphaNum, accuracyValue * 100);

                        results = {
                            totalQuestions: completedSentencesRef.current,
                            totalCharsTyped: totalTypedCount + errorCount,
                            correctCharsCount: totalTypedCount,
                            correctCount: totalTypedCount,
                            missCount: errorCount,
                            accuracy: accuracyValue, // 0.0-1.0
                            score: totalScore_alphaNum,
                            cpm: cpm, // CPM を追加
                            maxConsecutiveCorrectChars: maxConsecutiveCorrectRef.current,
                            // console.log(`[AlphabetChallenge DEBUG Results 英数短文 FINAL] maxConsecutiveCorrectRef.current: ${maxConsecutiveCorrectRef.current}`); // Cannot put console.log here
                            rankMessage: rankMessage,
                        };
                    }
                    setChallengeResults(results);
                }
            }, 100);
        } else if (status === 'finished' && trainingTimerRef.current) {
            clearInterval(trainingTimerRef.current);
        }
        return () => {
            if (trainingTimerRef.current !== null) clearInterval(trainingTimerRef.current);
        };
    }, [isActive, status, practiceName, totalTypedCount, errorCount]);

    // リセット処理
    const reset = useCallback(() => {
        console.log(`[AlphabetChallengePractice reset] Called for practice: ${practiceName}.`);
        setStatus('idle');
        setCountdownValue(COUNTDOWN_SECONDS);
        setRemainingTime(
            practiceName === '英字入力１分間トレーニング' ? TRAINING_DURATION_MS : ALPHANUM_TRAINING_DURATION_MS
        );
        setCurrentTargetChar(null);
        setChallengeStage('waitingForLayer4Key');
        setChallengeResults(null);
        startTimeRef.current = null;
        if (practiceName === '英字入力１分間トレーニング') {
            correctCharsTypedRef.current = 0;
            totalCharsAttemptedRef.current = 0;
            errorCountRef.current = 0;
            currentConsecutiveCorrectRef.current = 0;
            completedSentencesRef.current = 0;
            maxConsecutiveCorrectRef.current = 0;
        } else {
            setCurrentSentence('');
            currentSentenceRef.current = '';
            setCurrentSentenceIndex(0);
            setTypedHistory([]);
            setTotalTypedCount(0);
            setErrorCount(0);
            setDegreeSymbolStep(0);
            setCurrentTargetGroupIndex(null);
            currentConsecutiveCorrectRef.current = 0; // 英数短文でも初期化
            completedSentencesRef.current = 0;
            maxConsecutiveCorrectRef.current = 0;     // 英数短文でも初期化
        }
        if (countdownTimerRef.current !== null) clearTimeout(countdownTimerRef.current);
        if (trainingTimerRef.current !== null) clearInterval(trainingTimerRef.current);
    }, [practiceName]);

    // practiceName が変更されたときに状態をリセットする
    useEffect(() => {
        console.log(`[AlphabetChallengePractice useEffect practiceName] practiceName changed to: ${practiceName}. Calling reset.`);
        reset();
        setRemainingTime(
            practiceName === '英字入力１分間トレーニング' ? TRAINING_DURATION_MS : ALPHANUM_TRAINING_DURATION_MS
        );
        setStatus('idle');
    }, [practiceName, reset]);

    useEffect(() => {
        if (!isActive && prevIsActiveRef.current) {
            console.log(`[AlphabetChallengePractice useEffect isActive] Practice became inactive. Calling reset. Current status: ${status}`);
            reset();
        } else if (isActive && status === 'idle') {
            console.log(`[AlphabetChallengePractice useEffect isActive] Practice is active and status is idle. Setting status to 'countdown'.`);
            setStatus('countdown');
        }
        console.log(`[AlphabetChallengePractice useEffect isActive] End of effect. isActive: ${isActive}, status: ${status}, prevIsActive: ${prevIsActiveRef.current}`);
        prevIsActiveRef.current = isActive;
    }, [isActive, status, reset]);

    const handleInput = useCallback((inputInfo: PracticeInputInfo): PracticeInputResult => {
        if (status !== 'running' || !currentTargetChar || !layers) {
            return { isExpected: false, shouldGoToNext: false, errorLayer: null };
        }
        if (inputInfo.type !== 'release') {
            return { isExpected: false, shouldGoToNext: false, errorLayer: null };
        }

        const pressCode = inputInfo.pressCode;
        const layerIndex = inputInfo.layerIndex;
        let isCorrect = false;
        let currentErrorLayer: number | null = null;

        if (practiceName === '英字入力１分間トレーニング') {
            const targetGroup = alphabetPracticeGroups.find(group => group.includes(currentTargetChar.toLowerCase()));
            if (!targetGroup) {
                return { isExpected: false, shouldGoToNext: false, errorLayer: null };
            }

            if (challengeStage === 'waitingForLayer4Key') {
                totalCharsAttemptedRef.current += 1;
                const pressedKeyNameOnLayer4 = layers[4]?.[pressCode]?.trim().toLowerCase();
                if (pressedKeyNameOnLayer4 && pressedKeyNameOnLayer4 === targetGroup) {
                    isCorrect = true;
                    setChallengeStage('waitingForLayer5Key');
                } else {
                    errorCountRef.current += 1;
                    currentErrorLayer = 4;
                    currentConsecutiveCorrectRef.current = 0;
                }
            } else if (challengeStage === 'waitingForLayer5Key') {
                totalCharsAttemptedRef.current += 1;
                const charIndexInGroup = targetGroup.indexOf(currentTargetChar.toLowerCase());
                const charInputKeyNames = ["①", "②", "③", "④"];
                const expectedLayer5KeyName = (charIndexInGroup !== -1 && charIndexInGroup < charInputKeyNames.length)
                    ? charInputKeyNames[charIndexInGroup]
                    : null;
                const pressedKeyNameOnLayer5 = layers[5]?.[pressCode]?.trim();
                console.log(`[AlphabetChallenge DEBUG Layer5] Target: ${currentTargetChar}, ExpectedKey: ${expectedLayer5KeyName}, PressedKey: ${pressedKeyNameOnLayer5}, targetGroup: ${targetGroup}, charIndexInGroup: ${charIndexInGroup}`);

                if (expectedLayer5KeyName && pressedKeyNameOnLayer5 === expectedLayer5KeyName) {
                    console.log(`[AlphabetChallenge DEBUG Layer5] MATCH! Incrementing correctCharsTypedRef. Before: ${correctCharsTypedRef.current}`);
                    isCorrect = true;
                    correctCharsTypedRef.current += 1;
                    currentConsecutiveCorrectRef.current += 1;
                    maxConsecutiveCorrectRef.current = Math.max(maxConsecutiveCorrectRef.current, currentConsecutiveCorrectRef.current);
                    console.log(`[AlphabetChallenge DEBUG Layer5] After increment: ${correctCharsTypedRef.current}`);
                    selectNextTarget();
                } else {
                    console.log(`[AlphabetChallenge DEBUG Layer5] NO MATCH. Incrementing errorCountRef. Before: ${errorCountRef.current}`);
                    errorCountRef.current += 1;
                    currentConsecutiveCorrectRef.current = 0;
                    console.log(`[AlphabetChallenge DEBUG Layer5] After error increment: ${errorCountRef.current}`);
                    currentErrorLayer = 5;
                    setChallengeStage('waitingForLayer4Key');
                }
            }
        } else { // 英数短文入力３分間トレーニング
            const inputKey = layers[layerIndex]?.[pressCode]?.trim();
            let expected = false;

            if (currentTargetChar === '°') {
                if (degreeSymbolStep === 0 && layerIndex === DEGREE_SYMBOL_LAYER_INDEX && pressCode === DEGREE_SYMBOL_FIRST_KEY_INDEX) {
                    setDegreeSymbolStep(1);
                    expected = true;
                } else if (degreeSymbolStep === 1 && layerIndex === DEGREE_SYMBOL_LAYER_INDEX && pressCode === DEGREE_SYMBOL_SECOND_KEY_INDEX) {
                    setTypedHistory(prev => [...prev, currentTargetChar]);
                    setTotalTypedCount(prev => prev + 1);
                    const nextCharIndex = currentSentenceIndex + 1;
                    if (nextCharIndex < currentSentenceRef.current.length) {
                        const nextChar = currentSentenceRef.current[nextCharIndex];
                        setCurrentTargetChar(nextChar);
                        setCurrentSentenceIndex(nextCharIndex);
                        updateTargetGroupAndStageForAlphaNum(nextChar);
                    } else {
                        selectNextTarget();
                    }
                    setDegreeSymbolStep(0);
                    expected = true;
                    currentConsecutiveCorrectRef.current += 1;
                    maxConsecutiveCorrectRef.current = Math.max(maxConsecutiveCorrectRef.current, currentConsecutiveCorrectRef.current);
                } else {
                    setErrorCount(prev => prev + 1);
                    currentErrorLayer = DEGREE_SYMBOL_LAYER_INDEX;
                    setDegreeSymbolStep(0);
                    currentConsecutiveCorrectRef.current = 0;
                }
            } else if (currentTargetChar.match(/^[a-zA-Z]$/)) {
                if (challengeStage === 'waitingForLayer4Key') {
                    const targetGroup = currentTargetGroupIndex !== null ? alphabetPracticeGroups[currentTargetGroupIndex] : null;
                    if (targetGroup && layerIndex === ALPHABET_LAYER_INDEX && inputKey?.toLowerCase() === targetGroup) {
                        setChallengeStage('waitingForLayer5Key');
                        expected = true;
                    } else {
                        setErrorCount(prev => prev + 1);
                        currentErrorLayer = ALPHABET_LAYER_INDEX;
                        currentConsecutiveCorrectRef.current = 0;
                    }
                } else if (challengeStage === 'waitingForLayer5Key') {
                    const targetGroup = currentTargetGroupIndex !== null ? alphabetPracticeGroups[currentTargetGroupIndex] : null;
                    if (targetGroup) {
                        const charIndexInGroup = targetGroup.indexOf(currentTargetChar.toLowerCase());
                        const charInputKeyNames = ["①", "②", "③", "④"];
                        const expectedLayer5KeyName = (charIndexInGroup !== -1 && charIndexInGroup < charInputKeyNames.length)
                            ? charInputKeyNames[charIndexInGroup]
                            : null;
                        if (layerIndex === ALPHABET_SHIFT_LAYER_INDEX && inputKey === expectedLayer5KeyName) {
                            setTypedHistory(prev => [...prev, currentTargetChar]);
                            setTotalTypedCount(prev => prev + 1);
                            const nextCharIndex = currentSentenceIndex + 1;
                            if (nextCharIndex < currentSentenceRef.current.length) {
                                const nextChar = currentSentenceRef.current[nextCharIndex];
                                setCurrentTargetChar(nextChar);
                                setCurrentSentenceIndex(nextCharIndex);
                                updateTargetGroupAndStageForAlphaNum(nextChar);
                            } else {
                                selectNextTarget();
                            }
                            expected = true;
                            currentConsecutiveCorrectRef.current += 1;
                            maxConsecutiveCorrectRef.current = Math.max(maxConsecutiveCorrectRef.current, currentConsecutiveCorrectRef.current);
                        } else {
                            setErrorCount(prev => prev + 1);
                            currentErrorLayer = ALPHABET_SHIFT_LAYER_INDEX;
                            updateTargetGroupAndStageForAlphaNum(currentTargetChar);
                            currentConsecutiveCorrectRef.current = 0;
                        }
                    } else {
                        setErrorCount(prev => prev + 1);
                        updateTargetGroupAndStageForAlphaNum(currentTargetChar);
                        currentConsecutiveCorrectRef.current = 0;
                    }
                }
            } else if (inputKey && inputKey.toLowerCase() === currentTargetChar.toLowerCase()) {
                setTypedHistory(prev => [...prev, currentTargetChar]);
                setTotalTypedCount(prev => prev + 1);
                const nextCharIndex = currentSentenceIndex + 1;
                if (nextCharIndex < currentSentenceRef.current.length) {
                    const nextChar = currentSentenceRef.current[nextCharIndex];
                    setCurrentTargetChar(nextChar);
                    setCurrentSentenceIndex(nextCharIndex);
                    updateTargetGroupAndStageForAlphaNum(nextChar);
                } else {
                    selectNextTarget();
                }
                expected = true;
                currentConsecutiveCorrectRef.current += 1;
                maxConsecutiveCorrectRef.current = Math.max(maxConsecutiveCorrectRef.current, currentConsecutiveCorrectRef.current);
            } else {
                setErrorCount(prev => prev + 1);
                currentErrorLayer = layerIndex;
                updateTargetGroupAndStageForAlphaNum(currentTargetChar);
                currentConsecutiveCorrectRef.current = 0;
            }
            isCorrect = expected;
        }

        return { isExpected: isCorrect, shouldGoToNext: false, errorLayer: currentErrorLayer };
    }, [status, currentTargetChar, challengeStage, layers, selectNextTarget, practiceName, currentSentenceIndex, degreeSymbolStep, updateTargetGroupAndStageForAlphaNum, currentTargetGroupIndex]);

    const getHighlightClassName = useCallback((keyName: string, layoutIndex: number, keyIndex: number): PracticeHighlightResult => {
        const noHighlight: PracticeHighlightResult = { className: null, overrideKey: null };
        console.log(`[DEBUG getHighlightClassName ${practiceName}] targetChar: "${currentTargetChar}", challengeStage: "${challengeStage}", currentTargetGroupIndex: ${currentTargetGroupIndex}, degreeSymbolStep: ${degreeSymbolStep}, keyName: "${keyName}", layoutIdx: ${layoutIndex}, keyIdx: ${keyIndex}, status: ${status}`);

        if (status !== 'running' || !currentTargetChar) {
            return noHighlight;
        }

        if (practiceName === '英字入力１分間トレーニング') {
            const targetCharLower = currentTargetChar.toLowerCase();
            const keyNameNormalized = keyName.trim().toLowerCase();

            if (challengeStage === 'waitingForLayer4Key') {
                if (layoutIndex === ALPHABET_LAYER_INDEX) {
                    const targetGroup = alphabetPracticeGroups.find(group => group.includes(targetCharLower));
                    if (targetGroup && keyNameNormalized === targetGroup) {
                        return { className: 'bg-blue-100 dark:bg-blue-300', overrideKey: null };
                    }
                }
            } else if (challengeStage === 'waitingForLayer5Key') {
                if (layoutIndex === ALPHABET_SHIFT_LAYER_INDEX) {
                    const targetGroup = alphabetPracticeGroups.find(group => group.includes(targetCharLower));
                    if (targetGroup) {
                        const charIndexInGroup = targetGroup.indexOf(targetCharLower);
                        const charInputKeyNames = ["①", "②", "③", "④"];
                        const expectedLayer5KeyName = (charIndexInGroup !== -1 && charIndexInGroup < charInputKeyNames.length)
                            ? charInputKeyNames[charIndexInGroup]
                            : null;
                        if (expectedLayer5KeyName && keyName.trim() === expectedLayer5KeyName) {
                            return { className: 'bg-blue-100 dark:bg-blue-300', overrideKey: null };
                        }
                    }
                }
            }
        } else { // 英数短文入力３分間トレーニング
            if (currentTargetChar === '°') {
                if (layoutIndex === DEGREE_SYMBOL_LAYER_INDEX) {
                    if (degreeSymbolStep === 0 && keyIndex === DEGREE_SYMBOL_FIRST_KEY_INDEX) {
                        return { className: 'bg-blue-100 dark:bg-blue-300', overrideKey: null };
                    }
                    if (degreeSymbolStep === 1 && keyIndex === DEGREE_SYMBOL_SECOND_KEY_INDEX) {
                        return { className: 'bg-blue-100 dark:bg-blue-300', overrideKey: null };
                    }
                }
            } else if (currentTargetChar.match(/^[a-zA-Z]$/)) {
                console.log(`[DEBUG getHighlightClassName ${practiceName} Alpha] Evaluating alphabet. Stage: ${challengeStage}, TargetGroupIndex: ${currentTargetGroupIndex}`);
                if (challengeStage === 'waitingForLayer4Key') {
                    if (layoutIndex === ALPHABET_LAYER_INDEX && keyIndex === currentTargetGroupIndex) {
                        console.log(`[DEBUG getHighlightClassName ${practiceName} Alpha Group] MATCH! Highlighting group key for ${currentTargetChar} (group index ${currentTargetGroupIndex}) at keyIndex ${keyIndex}`);
                        return { className: 'bg-blue-100 dark:bg-blue-300', overrideKey: null };
                    }
                } else if (challengeStage === 'waitingForLayer5Key') {
                    if (layoutIndex === ALPHABET_SHIFT_LAYER_INDEX) {
                        const targetGroup = currentTargetGroupIndex !== null ? alphabetPracticeGroups[currentTargetGroupIndex] : null;
                        if (targetGroup) {
                            const charIndexInGroup = targetGroup.indexOf(currentTargetChar.toLowerCase());
                            const charInputKeyNames = ["①", "②", "③", "④"];
                            const expectedLayer5KeyName = (charIndexInGroup !== -1 && charIndexInGroup < charInputKeyNames.length)
                                ? charInputKeyNames[charIndexInGroup]
                                : null;
                            if (expectedLayer5KeyName && keyName.trim() === expectedLayer5KeyName) {
                                console.log(`[DEBUG getHighlightClassName ${practiceName} Alpha Individual] MATCH! Highlighting individual key "${keyName.trim()}" for ${currentTargetChar}.`);
                                return { className: 'bg-blue-100 dark:bg-blue-300', overrideKey: null };
                            }
                        }
                    }
                }
            } else {
                const keyLabel = layers[layoutIndex]?.[keyIndex]?.trim();
                if (keyLabel && (keyLabel.toLowerCase() === currentTargetChar.toLowerCase() || (currentTargetChar === ' ' && keyLabel === 'SP'))) {
                    if (layoutIndex !== DEGREE_SYMBOL_LAYER_INDEX) {
                        console.log(`[DEBUG getHighlightClassName ${practiceName} Other] MATCH! Highlighting key "${keyLabel}" for target "${currentTargetChar}" on layer ${layoutIndex}.`);
                        return { className: 'bg-blue-100 dark:bg-blue-300', overrideKey: null };
                    }
                }
            }
        }
        return noHighlight;
    }, [status, currentTargetChar, challengeStage, practiceName, degreeSymbolStep, layers, currentTargetGroupIndex]);


    const practiceResult = useMemo(() => {
        let calculatedHeadingChars: string[] = [];
        if (status === 'countdown') {
            calculatedHeadingChars = [`${countdownValue}秒`];
        } else if (status === 'running') {
            if (practiceName === '英字入力１分間トレーニング' && currentTargetChar) {
                calculatedHeadingChars = [currentTargetChar];
            } else if (practiceName === '英数短文入力３分間トレーニング' && currentSentence) {
                calculatedHeadingChars = [currentSentence];
            }
        } else if (status === 'finished') {
            calculatedHeadingChars = ['終了！'];
        }

        let currentTargetLayerIndices = [ALPHABET_LAYER_INDEX, ALPHABET_SHIFT_LAYER_INDEX];
        if (practiceName === '英数短文入力３分間トレーニング') {
            if (currentTargetChar === '°') {
                currentTargetLayerIndices = [DEGREE_SYMBOL_LAYER_INDEX];
            } else if (currentTargetChar && !currentTargetChar.match(/^[a-zA-Z]$/)) {
                currentTargetLayerIndices = [ALPHABET_LAYER_INDEX, ALPHABET_SHIFT_LAYER_INDEX];
            }
        }


        return {
            headingChars: calculatedHeadingChars,
            handleInput,
            getHighlightClassName,
            reset,
            challengeResults,
            status,
            countdownValue,
            targetLayerIndices: currentTargetLayerIndices,
            typedEndIndex: practiceName === '英数短文入力３分間トレーニング' ? currentSentenceIndex : null,
            isInvalidInputTarget: () => false,
            targetChar: status === 'finished' ? null : (currentTargetChar ?? undefined),
            displayLayers: layers,
            challengeTimer: remainingTime,
            challengeTotalTime: practiceName === '英字入力１分間トレーニング' ? TRAINING_DURATION_MS : ALPHANUM_TRAINING_DURATION_MS,
        };
    }, [
        status, countdownValue, currentTargetChar, currentSentence, currentSentenceIndex,
        handleInput, getHighlightClassName, reset, challengeResults, practiceName, layers, remainingTime
    ]);

    return practiceResult;
};

const ALPHABET_LAYER_INDEX = 4;
const ALPHABET_SHIFT_LAYER_INDEX = 5;
const DEGREE_SYMBOL_LAYER_INDEX = 9;
const DEGREE_SYMBOL_FIRST_KEY_INDEX = 4;
const DEGREE_SYMBOL_SECOND_KEY_INDEX = 13;

export default useAlphabetChallengePractice;
