// /home/coffee/my-keymap-viewer/src/hooks/useYouonPractice.ts
import { useState, useCallback, useMemo, useEffect, useRef } from 'react';
import {
    PracticeHookProps,
    PracticeInputInfo,
    PracticeInputResult,
    PracticeHookResult,
    youonGyouList,
    youonGyouChars,
    youonDanMapping,
    hid2GyouHRight_Kana,
    hid2GyouHLeft_Kana,
    hid2DanHRight_Kana,
    hid2DanHLeft_Kana,
    hid2GyouVRight_Kana,
    hid2GyouVLeft_Kana,
    hid2DanVRight_Kana,
    hid2DanVLeft_Kana,
    CharInfoYouon,
    allYouonCharInfos,
    PracticeHighlightResult,
    HeadingCharDisplayInfo, // HeadingCharDisplayInfo をインポート
    PracticeStatus, // PracticeStatus をインポート
} from './usePracticeCommons';

type YouonStage = 'gyouInput' | 'youonInput' | 'danInput';

export default function useYouonPractice({ gIdx, dIdx, isActive, side, kb, isRandomMode }: PracticeHookProps): PracticeHookResult {
    const [currentErrorHighlightLayer, setCurrentErrorHighlightLayer] = useState<number | null>(null);
    const [stage, setStage] = useState<YouonStage>('gyouInput');
    const [status, setStatus] = useState<PracticeStatus>('waiting'); // status state を追加
    const { hid2Gyou, hid2Dan } = useMemo(() => {
        if (kb === 'tw-20v') {
            return side === 'left'
                ? { hid2Gyou: hid2GyouVLeft_Kana, hid2Dan: hid2DanVLeft_Kana }
                : { hid2Gyou: hid2GyouVRight_Kana, hid2Dan: hid2DanVRight_Kana };
        } else { // TW-20H
            return side === 'left'
                ? { hid2Gyou: hid2GyouHLeft_Kana, hid2Dan: hid2DanHLeft_Kana }
                : { hid2Gyou: hid2GyouHRight_Kana, hid2Dan: hid2DanHRight_Kana };
        }
    }, [side, kb]);

    const youonKeyCode = useMemo(() => {
        const gyouMap = kb === 'tw-20v'
            ? (side === 'left' ? hid2GyouVLeft_Kana : hid2GyouVRight_Kana)
            : (side === 'left' ? hid2GyouHLeft_Kana : hid2GyouHRight_Kana);
        // 押下コードのみを対象とする (<= 0x14 or 0x10)
        const maxPressCode = kb === 'tw-20v' ? 0x10 : 0x14; // TW-20V は 0x01-0x10, TW-20H は 0x01-0x14
        const entry = Object.entries(gyouMap).find(([codeStr, name]) => name === 'ゃゅょ' && parseInt(codeStr) <= maxPressCode);
        return entry ? parseInt(entry[0]) : null;
    }, [side, kb]);

    const [randomTarget, setRandomTarget] = useState<CharInfoYouon | null>(null);
    const prevGIdxRef = useRef(gIdx);
    const prevDIdxRef = useRef(dIdx);
    const isInitialMount = useRef(true);
    const prevIsActiveRef = useRef(isActive); // isActive の前回の値を保持
    const prevIsRandomModeRef = useRef(isRandomMode);

    const selectNextRandomTarget = useCallback(() => {
        if (allYouonCharInfos.length > 0) {
            const randomIndex = Math.floor(Math.random() * allYouonCharInfos.length);
            setRandomTarget(allYouonCharInfos[randomIndex]);
            setStage('gyouInput'); // ステージもリセット
            setStatus('waiting'); // 新しいターゲットで waiting に
        } else {
            setRandomTarget(null);
            setStatus('finished'); // ターゲットがなければ finished
        }
    }, [setRandomTarget, setStage, setStatus]);

    // reset 関数
    const reset = useCallback(() => {
        setStage('gyouInput');
        setRandomTarget(null);
        prevGIdxRef.current = -1;
        prevDIdxRef.current = -1;
        setCurrentErrorHighlightLayer(null);
        console.log("[YouonPractice reset] Error highlight layer cleared.");
        isInitialMount.current = true;
        prevIsRandomModeRef.current = false; // reset 時は false に
        setStatus('waiting'); // reset 時は waiting に
    }, [setStage, setRandomTarget, setStatus]);

    useEffect(() => {
        // isActive が false になった最初のタイミングでリセット
        if (!isActive && prevIsActiveRef.current) {
            console.log(`[Youon useEffect] Resetting state because isActive became false.`);
            reset(); // reset 関数内で必要なリセット処理を行う
            setCurrentErrorHighlightLayer(null);
        }

        if (isActive) {
            // isActive が true になった最初のタイミング、または依存関係の変更時
            if (isActive && !prevIsActiveRef.current) {
                isInitialMount.current = true; // 強制的に初期マウント扱い
                setStatus('waiting'); // アクティブになったら waiting
            }

            const indicesChanged = gIdx !== prevGIdxRef.current || dIdx !== prevDIdxRef.current;
            const randomModeChangedToTrue = isRandomMode && !prevIsRandomModeRef.current;
            const randomModeChangedToFalse = !isRandomMode && prevIsRandomModeRef.current;

            // --- リセット条件 ---
            if (randomModeChangedToFalse || (!isRandomMode && (isInitialMount.current || indicesChanged))) {
                console.log(`[Youon useEffect] Normal mode. isInitialMount=${isInitialMount.current}, indicesChanged=${indicesChanged}`);
                setStage('gyouInput'); // 通常モードやインデックス変更時はステージをリセット
                setRandomTarget(null); // ランダムターゲットはクリア
                setCurrentErrorHighlightLayer(null);
                setStatus('waiting'); // 通常モード開始/変更時も waiting
                prevGIdxRef.current = gIdx;
                prevDIdxRef.current = dIdx;
                isInitialMount.current = false;
            }
            // --- ランダムターゲット選択条件 (初回のみ or リセット後) ---
            else if (isRandomMode && (randomModeChangedToTrue || isInitialMount.current || !randomTarget)) {
                 console.log(`[Youon useEffect] Random mode. randomModeChangedToTrue=${randomModeChangedToTrue}, isInitialMount=${isInitialMount.current}, !randomTarget=${!randomTarget}`);
                 selectNextRandomTarget(); // selectNextRandomTarget 内で status も設定
                 setCurrentErrorHighlightLayer(null);
                 isInitialMount.current = false;
                 prevGIdxRef.current = gIdx;
                 prevDIdxRef.current = dIdx;
            }
        }

        // 最後に前回の値を更新
        prevIsActiveRef.current = isActive;
        prevIsRandomModeRef.current = isRandomMode;

    }, [isActive, isRandomMode, gIdx, dIdx, randomTarget, reset, selectNextRandomTarget, setCurrentErrorHighlightLayer, setStatus]);


    // 通常モード用の現在行・段キー
    const currentGyouKey = useMemo(() => {
        if (!isActive || isRandomMode || gIdx < 0 || gIdx >= youonGyouList.length) return null;
        return youonGyouList[gIdx];
    }, [isActive, isRandomMode, gIdx]);

    const currentDanKey = useMemo(() => {
        if (!isActive || isRandomMode || !currentGyouKey || !youonDanMapping[currentGyouKey]) {
            return null;
        }
        const danList = youonDanMapping[currentGyouKey];
        if (!danList || dIdx < 0 || dIdx >= danList.length) return null;
        return danList[dIdx];
    }, [isActive, isRandomMode, currentGyouKey, dIdx]);

    // ヘッダー文字
    const headingChars = useMemo((): HeadingCharDisplayInfo[] => {
        if (!isActive) return [];
        if (isRandomMode) {
            return randomTarget ? [{ char: randomTarget.char, isPracticeTarget: true }] : [];
        } else {
            if (!currentGyouKey || !youonGyouChars[currentGyouKey]) return [];
            const charsForGyou = youonGyouChars[currentGyouKey] || [];
            return charsForGyou.map((char, index) => ({
                char,
                isPracticeTarget: index === dIdx
            }));
        }
    }, [isActive, isRandomMode, randomTarget, currentGyouKey, dIdx]);

    // 期待キー
    const expectedGyouKey = useMemo(() => (isRandomMode ? randomTarget?.gyouKey : currentGyouKey) ?? null, [isRandomMode, randomTarget, currentGyouKey]);
    const expectedDanKey = useMemo(() => (isRandomMode ? randomTarget?.danKey : currentDanKey) ?? null, [isRandomMode, randomTarget, currentDanKey]);

    // handleInput
    const handleInput = useCallback((info: PracticeInputInfo): PracticeInputResult => {
        if (!isActive || !expectedGyouKey || !expectedDanKey || youonKeyCode === null || info.type !== 'release') {
            setCurrentErrorHighlightLayer(null);
            // status は変更しない (エラー時は incorrect にしたいが、ここではまだ判定前)
            return { isExpected: false, shouldGoToNext: undefined, errorLayer: null };
        }

        let isExpected = false;
        let shouldGoToNext_final: boolean | undefined = undefined;
        let errorLayer: number | null = null;
        let nextStatus: PracticeStatus = 'waiting'; // デフォルトは次の入力待ち

        if (stage === 'gyouInput') {
            const expectedGyouKeyCodes = Object.entries(hid2Gyou)
                .filter(([_, name]) => name === expectedGyouKey)
                .map(([codeStr, _]) => parseInt(codeStr) -1); // 0-based に変換
            if (expectedGyouKeyCodes.includes(info.pressCode)) {
                setStage('youonInput');
                isExpected = true;
                shouldGoToNext_final = undefined; // 文字入力はまだ完了していない
                setCurrentErrorHighlightLayer(null);
                errorLayer = null;
                nextStatus = 'waiting'; // 次のゃゅょキー待ち
            } else {
                isExpected = false;
                shouldGoToNext_final = undefined; // 不正解、文字入力は完了していない
                setCurrentErrorHighlightLayer(2); errorLayer = 2;
                nextStatus = 'incorrect';
            }
        } else if (stage === 'youonInput') {
            if (info.pressCode === (youonKeyCode ? youonKeyCode -1 : -1)) { // 0-based に変換
                setStage('danInput');
                isExpected = true;
                shouldGoToNext_final = undefined; // 文字入力はまだ完了していない
                setCurrentErrorHighlightLayer(null);
                errorLayer = null;
                nextStatus = 'waiting'; // 次の段キー待ち
            } else {
                isExpected = false;
                setStage('gyouInput'); // 不正解なら行入力からやり直し
                shouldGoToNext_final = undefined; // 不正解、文字入力は完了していない
                setCurrentErrorHighlightLayer(2); errorLayer = 2;
                nextStatus = 'incorrect';
            }
        } else if (stage === 'danInput') {
            const expectedDanKeyCodes = Object.entries(hid2Dan)
                .filter(([_, name]) => name === expectedDanKey)
                .map(([codeStr, _]) => parseInt(codeStr) -1); // 0-based に変換
            if (expectedDanKeyCodes.includes(info.pressCode)) {
                isExpected = true;
                setStage('gyouInput'); // 次の文字/ターゲットのためにステージをリセット
                setCurrentErrorHighlightLayer(null);
                errorLayer = null;
                nextStatus = 'correct'; // 正解完了
                if (isRandomMode) {
                    selectNextRandomTarget(); // selectNextRandomTarget 内で status が waiting に設定される
                    shouldGoToNext_final = false; // App.tsx は gIdx/dIdx を進めない
                } else {
                    // 通常モード: 現在の文字がその行の最後かどうかを判定
                    const currentGyouKeyFromProps = youonGyouList[gIdx]; // props の gIdx を使用
                    const charsInCurrentGyou = youonGyouChars[currentGyouKeyFromProps];
                    if (charsInCurrentGyou && dIdx >= charsInCurrentGyou.length - 1) {
                        shouldGoToNext_final = true; // 行の最後の文字なら App.tsx は gIdx を進める
                    } else {
                        shouldGoToNext_final = false; // 行の途中の文字なら App.tsx は dIdx を進める
                    }
                }
            } else {
                isExpected = false;
                setStage('gyouInput'); // 不正解なら行入力からやり直し
                shouldGoToNext_final = undefined; // 不正解、文字入力は完了していない
                setCurrentErrorHighlightLayer(3); errorLayer = 3;
                nextStatus = 'incorrect';
            }
        }

        console.log(`[YouonPractice handleInput] Returning: isExpected=${isExpected}, shouldGoToNext=${shouldGoToNext_final}, currentStage=${stage}`);
        setStatus(nextStatus); // status を更新
        return { isExpected, shouldGoToNext: shouldGoToNext_final, errorLayer };
    }, [
        isActive, stage, expectedGyouKey, expectedDanKey, youonKeyCode,
        hid2Gyou, hid2Dan, isRandomMode, selectNextRandomTarget, setStage,
        gIdx, dIdx, setCurrentErrorHighlightLayer, setStatus // setStatus を追加
    ]);

    const currentTargetCharForHook = useMemo(() => {
        if (!isActive) return undefined;
        if (isRandomMode) {
            return randomTarget?.char;
        } else {
            if (!currentGyouKey || !youonGyouChars[currentGyouKey]) return undefined;
            return youonGyouChars[currentGyouKey]?.[dIdx];
        }
    }, [isActive, isRandomMode, randomTarget, currentGyouKey, dIdx]);

    const getHighlightClassName = useCallback((key: string, layoutIndex: number, keyIndex: number): PracticeHighlightResult => { // Added keyIndex
        const noHighlight: PracticeHighlightResult = { className: null, overrideKey: null };
        console.log(`[YouonPractice getHighlightClassName] START. key: "${key}", layoutIndex: ${layoutIndex}, isActive: ${isActive}, stage: ${stage}, expectedGyouKey: "${expectedGyouKey}", expectedDanKey: "${expectedDanKey}", currentErrorHighlightLayer: ${currentErrorHighlightLayer}, randomTargetChar: ${randomTarget?.char}`);

        if (!isActive) {
            console.log(`[YouonPractice getHighlightClassName] END - !isActive`);
            return noHighlight;
        }
        if (!expectedGyouKey || !expectedDanKey) {
            console.log(`[YouonPractice getHighlightClassName] END - !expectedGyouKey (${!expectedGyouKey}) or !expectedDanKey (${!expectedDanKey}). Gyou: ${expectedGyouKey}, Dan: ${expectedDanKey}`);
            return noHighlight;
        }

        if (currentErrorHighlightLayer === layoutIndex) {
            console.log(`[YouonPractice getHighlightClassName] END - currentErrorHighlightLayer === layoutIndex (${currentErrorHighlightLayer})`);
            return noHighlight;
        }

        const currentStageForHighlight = stage; // 常に現在のステージに基づいてハイライト

        let expectedKeyName: string | null = null;
        let targetLayoutIndex: number | null = null;

        if (currentStageForHighlight === 'gyouInput') {
            expectedKeyName = expectedGyouKey;
            targetLayoutIndex = 2; // スタートレイヤー
        } else if (currentStageForHighlight === 'youonInput') {
            expectedKeyName = 'ゃゅょ';
            targetLayoutIndex = 2; // スタートレイヤー
        } else if (currentStageForHighlight === 'danInput') {
            expectedKeyName = expectedDanKey;
            targetLayoutIndex = 3; // エンドレイヤー
        }
        console.log(`[YouonPractice getHighlightClassName] Determined expectedKeyName: "${expectedKeyName}", targetLayoutIndex: ${targetLayoutIndex}`);

        if (expectedKeyName !== null && layoutIndex === targetLayoutIndex && key.trim() === expectedKeyName.trim()) {
            console.log(`[YouonPractice getHighlightClassName] END - MATCH! Highlighting.`);
            return { className: 'bg-blue-100', overrideKey: null };
        } // keyIndex is not used in logic, so not a dependency
        console.log(`[YouonPractice getHighlightClassName] END - NO MATCH. Conditions: expectedKeyName !== null (${expectedKeyName !== null}), layoutIndex === targetLayoutIndex (${layoutIndex === targetLayoutIndex}), key.trim() === expectedKeyName.trim() (${key.trim() === expectedKeyName?.trim()})`);
        return noHighlight;
    }, [isActive, stage, expectedGyouKey, expectedDanKey, currentErrorHighlightLayer, randomTarget]); // Removed currentTargetCharForHook from dependencies

    // isInvalidInputTarget
    const isInvalidInputTarget = useCallback((targetPressCode: number, currentLayoutIndex: number, currentKeyIndex: number): boolean => {
        if (!isActive) return false;

        if (currentErrorHighlightLayer === currentLayoutIndex && currentKeyIndex === targetPressCode) {
            return true;
        }
        return false;
    }, [isActive, currentErrorHighlightLayer]);

    const clearErrorState = useCallback(() => {
        setCurrentErrorHighlightLayer(null);
        setStatus('waiting'); // エラークリア時は waiting に戻す
    }, [setCurrentErrorHighlightLayer, setStatus]);


    return {
        handleInput,
        getHighlightClassName,
        headingChars,
        reset,
        isInvalidInputTarget,
        errorHighlightLayer: currentErrorHighlightLayer,
        clearErrorState,
        targetChar: currentTargetCharForHook,
        status, // status を返す
        targetLayerIndices: useMemo(() => {
            let determinedTargetLayerIndices = [2, 3]; // Default for Youon
            if (currentErrorHighlightLayer !== null) {
                const layersToShow = new Set([2, 3]);
                layersToShow.add(currentErrorHighlightLayer);
                determinedTargetLayerIndices = Array.from(layersToShow);
            }
            return determinedTargetLayerIndices;
        }, [currentErrorHighlightLayer]),
        stage, // Add stage to the return object
    };
}
