// --- ゃゅょ拡張１の練習データ ---
export interface YouonKakuchoInputDef {
  char: string; // 'きぃ', 'きぇ' など
  gyouKey: string; // 'か行' など
  dan: string; // 'い段', 'え段' など (youonKakuchoChars の dIdx に対応する段)
}

export interface YouonKakuchoPracticeGroup {
  // gyouKey: string; // youonGyouList[gIdx] で取得できるので不要かも
  inputs: (YouonKakuchoInputDef | null)[]; // dIdx (0-4) に対応する配列。対象外は null
}

// ゃゅょ拡張１の練習データ (例: 「きぃ」「きぇ」など)
// youonGyouList と youonKakuchoChars を元に生成
export const youonKakuchoPracticeData: YouonKakuchoPracticeGroup[] = youonGyouList.map(gyou => {
  const inputs: (YouonKakuchoInputDef | null)[] = [];
  const chars = (youonKakuchoChars[gyou] && youonKakuchoChars[gyou][0]) ? youonKakuchoChars[gyou][0].match(/.{1,2}/g) ?? [] : []; // "きゃきぃ..." を ["きゃ", "きぃ", ...] に分割
  for (let i = 0; i < 5; i++) { // 常に5要素 (あ段、い段、う段、え段、お段に対応)
    if (i === 1 || i === 3) { // 'ぃ' (dIdx=1) と 'ぇ' (dIdx=3) のみ定義
      inputs.push({
        char: chars[i] ?? '', // 分割後の配列から取得
        gyouKey: gyou,
        dan: i === 1 ? 'い段' : 'え段', // 'ぃ' なら 'い段', 'ぇ' なら 'え段'
      });
    } else {
      inputs.push(null); // それ以外の段は練習対象外
    }
  }
  return { inputs };
});


export interface GairaigoPracticeTarget {
  char: string;
  headerIndex: number;
  // Allow 3 or 4 keys using a union type for the tuple
  keys: [string, string, string] | [string, string, string, string]; // 1: 1st key, 2: 2nd display, 3: 3rd key, 4: optional 4th key
  // ▼▼▼ 2打目の実際の入力キーを追加 ▼▼▼
  actualSecondKey: string; // 2打目の実際の入力キー名 (例: "さ行")
  // ▲▲▲ 追加 ▲▲▲
  actualThirdKey?: string; // Actual key for 3rd input (optional) // Remove extra brace here
}

