import React from 'react';
import KeyboardLayout from './KeyboardLayout';
import { layerNames } from '../data/keymapData';

interface LayoutViewProps {
    layers: string[][];
    title: string;
    cols: number;
    fixedWidth: string;
    fw: string | null;
    sn: string | null;
    training: boolean; // KeyboardLayout に渡すため
}

const LayoutView: React.FC<LayoutViewProps> = ({
    layers,
    title,
    cols,
    fixedWidth,
    fw,
    sn,
    training,
}) => {
    return (
        <>
            <h1 className='text-lg font-semibold mb-4'>{title}</h1>
            {(fw || sn) && (
                <div className='mb-4 text-sm text-gray-700 space-y-1'>
                    <p>FW: {fw ?? '不明'}</p>
                    <p>SN: {sn ?? '不明'}</p>
                </div>
            )}
            <div className={`grid ${layers.length > 1 ? 'grid-cols-2' : 'grid-cols-1'} gap-4`}>
                {layers.map((layerData: string[], layerIndex: number) => (
                    <div key={layerIndex} className="justify-self-center w-full">
                        <KeyboardLayout
                            layerData={layerData}
                            layoutIndex={layerIndex}
                            layoutTitle={layerNames[layerIndex] ?? `レイヤー ${layerIndex}`}
                            cols={cols}
                            fixedWidth={fixedWidth}
                            showKeyLabels={true}
                            lastInvalidKeyCode={null}
                            lastInvalidLayerIndex={null}
                            activePractice={null}
                            practice={''}
                            currentFunctionKeyMap={{}}
                            training={training}
                            // LayoutViewではエラーハイライトは不要なので、常にfalseを返すダミー関数を渡す
                            isInvalidInputTarget={(_targetPressCode, _currentLayoutIndex, _currentKeyIndex) => false}
                        />
                    </div>
                ))}
            </div>
        </>
    );
};

export default LayoutView;