import { ChallengeResult } from "../hooks/usePracticeCommons";

// --- ランクメッセージ判定関数 (英字チャレンジの基準を共通化) ---
const getRankMessage = (score: number, accuracy: number): string => {
    // accuracy は 0-1 の値で渡される想定
    const accuracyPercentage = accuracy * 100;

    if (score >= 5000 && accuracyPercentage >= 98) return "Typing Master! 驚異的！\nランク：★★★★★";
    if (score >= 4000 && accuracyPercentage >= 95) return "Excellent! 素晴らしい！\nランク：★★★★☆";
    if (score >= 3000 && accuracyPercentage >= 90) return "Great! とても良い調子！\nランク：★★★☆☆";
    if (score >= 2000 && accuracyPercentage >= 85) return "Good! 良いですね！\nランク：★★☆☆☆";
    if (score >= 1000) return "Nice! その調子！\nランク：★☆☆☆☆";
    return "Keep Trying! 練習あるのみ！";
};

// --- スコア計算関数 (英字チャレンジの計算式を共通化) ---
export const calculateChallengeScoreAndRank = (results: ChallengeResult): { score: number; rankMessage: string } => {
    // ChallengeResult には以下の値が含まれていることを想定:
    // totalQuestions: 達成課題数 (チャレンジの種類による)
    // correctCharsCount: 正しく入力完了した文字数 (かな、短文、英数短文)
    // totalCharsTyped: 総打鍵数 (キーを押した回数)
    // correctCount: 正解タイプ数 (チャレンジの種類による)
    // missCount: ミスタイプ数
    // accuracy: 正答率 (0-1)
    // cpm: Characters Per Minute (入力速度)
    // maxConsecutiveCorrectChars: 最大連続正解文字数

    // 英字チャレンジの計算式をベースにする
    // P_base: 正解文字数 * 10
    // M_acc: 精度^2
    // P_speed: max(0, cpm - 50) * 5
    // P_combo: log(1 + maxConsecutiveCorrectChars) * 100
    // P_task: 達成課題数 * 50 (英字チャレンジでは正解文字数だったが、ここでは totalQuestions を使用)

    const P_base = (results.correctCharsCount ?? results.correctCount) * 10; // 正解文字数または正解タイプ数を使用
    const M_acc = Math.max(0, Math.pow(results.accuracy, 2));
    const P_speed = Math.max(0, results.cpm - 50) * 5;
    const P_combo = Math.log(1 + (results.maxConsecutiveCorrectChars ?? 0)) * 100;
    const P_task = (results.totalQuestions ?? (results.correctCharsCount ?? results.correctCount)) * 50; // 達成課題数または正解数を使用

    const totalScore = Math.round((P_base + P_speed + P_combo + P_task) * M_acc);

    // 入力が全くなかった場合はランクメッセージを空にする
    const rankMessage = (results.totalCharsTyped ?? 0) > 0 ? getRankMessage(totalScore, results.accuracy) : '';

    return {
        score: totalScore,
        rankMessage: rankMessage,
    };
};