import React from 'react';
import { PracticeMode } from '../hooks/usePracticeCommons';

interface GlobalControlsProps {
    training: boolean;
    showTrainingButton: boolean;
    // showKeyLabels: boolean; // This prop is no longer directly used for the button text
    isRandomMode: boolean;
    isRandomModeAvailable: boolean;
    practice: PracticeMode | '';
    onToggleTraining: () => void;
    onToggleKeyLabels: () => void;
    actualShowKeyLabelsState: boolean; // New prop to reflect the true current state
    onToggleRandomMode: () => void;
    onStartAlphaNumericPractice: () => void;
    buttonStyle: React.CSSProperties;
}

const GlobalControls: React.FC<GlobalControlsProps> = ({
    training,
    showTrainingButton,
    // showKeyLabels, // Destructured but not directly used for button text
    isRandomMode,
    isRandomModeAvailable,
    practice,
    onToggleTraining,
    onToggleKeyLabels,
    actualShowKeyLabelsState, // Use this for button text
    onToggleRandomMode,
    onStartAlphaNumericPractice,
    buttonStyle,
}) => {
    if (!showTrainingButton) {
        return null;
    }

    return (
        <div className="absolute top-4 right-4 flex flex-col space-y-2 items-end z-50">
            <button
                className={`px-4 py-1 rounded shadow text-white ${training ? 'bg-gray-600' : 'bg-green-600'}`}
                onClick={onToggleTraining}
                style={buttonStyle}
            >
                {training ? 'レイアウト表示に戻る' : 'かな練習を始める'}
            </button>
            {!training && (
                <button
                    className={`px-4 py-1 rounded shadow text-white bg-blue-500 hover:bg-blue-600`}
                    onClick={onStartAlphaNumericPractice}
                    style={buttonStyle}
                >
                    英数練習を始める
                </button>
            )}
            {training && (
                <>
                    <button
                        className="px-4 py-1 rounded shadow text-white bg-blue-600 hover:bg-blue-700"
                        onClick={onToggleKeyLabels}
                        style={buttonStyle}
                    >
                        {actualShowKeyLabelsState ? 'キー表示 OFF' : 'キー表示 ON'}
                    </button>
                    {isRandomModeAvailable && (
                        <button
                            className={`px-4 py-1 rounded shadow text-white bg-purple-600 hover:bg-purple-700`}
                            onClick={onToggleRandomMode}
                            style={buttonStyle}
                        >
                            ランダム {isRandomMode ? 'OFF' : 'ON'}
                        </button>
                    )}
                </>
            )}
        </div>
    );
};

export default GlobalControls;
