// /home/coffee/my-keymap-viewer/src/hooks/useKigoPractice3SymbolMenu.ts
import { useMemo, useState, useEffect, useCallback, useRef } from 'react';
import { kigoPractice3Data, functionKeyMaps, kigoMapping3, EQUAL_KIGO_KEY_NAME } from '../data/keymapData'; // EQUAL_KIGO_KEY_NAME をインポート
import {
    KeyboardSide,
    KeyboardModel,
    PracticeHookProps,
    PracticeInputInfo,
    PracticeInputResult,
    PracticeHookResult,
    hid2GyouHRight_Kana,
    HeadingCharDisplayInfo, // HeadingCharDisplayInfo をインポート
    hid2GyouHLeft_Kana,
    hid2GyouVRight_Kana,
    hid2GyouVLeft_Kana,
    CharInfoKigo3,
    allKigo3CharInfos,
    PracticeHighlightResult,
    PracticeStatus,
    toHalfWidth, // toHalfWidth をインポート
    gyouList, // gyouList をインポート
} from './usePracticeCommons';

type KigoPractice3Stage = 'kigoInput' | 'waitingForSecondKigo' | 'gyouInput'; // 'kigoInputWait' を 'waitingForSecondKigo' に変更

const useKigoPractice3SymbolMenu = ({ gIdx, dIdx, isActive, side, kb, isRandomMode, layers, practice }: PracticeHookProps): PracticeHookResult => {
    const [status, setStatus] = useState<PracticeStatus>('idle'); // status state を追加
    const [stage, setStage] = useState<KigoPractice3Stage>('kigoInput');
    const [showHighlightAfterWait, setShowHighlightAfterWait] = useState(true);
    const isWaitingForSecondKigo = useRef(false);
    const highlightTimeoutRef = useRef<number | null>(null);
    const prevGIdxRef = useRef(gIdx);
    const prevDIdxRef = useRef(dIdx);
    const isInitialMount = useRef(true);
    const prevIsActiveRef = useRef(isActive); // isActive の前回の値を保持
    const prevIsRandomModeRef = useRef(isRandomMode);

    const [randomTarget, setRandomTarget] = useState<CharInfoKigo3 | null>(null);

    const currentGroup = useMemo(() => {
        if (isRandomMode || !isActive || gIdx < 0 || gIdx >= kigoPractice3Data.length) return null;
        return kigoPractice3Data[gIdx];
    }, [isActive, isRandomMode, gIdx]);

    const currentInputDef = useMemo(() => {
        if (isRandomMode || !currentGroup?.inputs || dIdx < 0 || dIdx >= currentGroup.inputs.length) {
            return null;
        }
        return currentGroup.inputs[dIdx];
    }, [isRandomMode, currentGroup, dIdx]);

    const isTargetEqualSign = useMemo(() => { // currentInputDef を使って「＝」かどうかを判定
        if (isRandomMode) {
            return randomTarget?.char === '＝';
        }
        return !!currentInputDef && !currentInputDef.gyouKey;
    }, [isRandomMode, randomTarget, currentInputDef]);

    const currentTargetChar = useMemo(() => {
        if (isRandomMode) return randomTarget?.char ?? null; // ランダムモード時はランダムターゲットの文字
        return currentInputDef?.char ?? null; // 通常モード時は現在の入力定義の文字
    }, [isRandomMode, randomTarget, currentInputDef]); // currentGroup, dIdx を currentInputDef に変更

    const expectedGyouKey = useMemo(() => {
        if (isTargetEqualSign) return null;
        // ランダムモード時はランダムターゲットの行キー、通常モード時は現在の入力定義の行キー
        if (isRandomMode) return randomTarget?.gyouKey ?? null;
        return currentInputDef?.gyouKey ?? null;
    }, [isRandomMode, randomTarget, currentInputDef, isTargetEqualSign]);

    const headingChars = useMemo((): HeadingCharDisplayInfo[] => {
        if (!isActive) return [];

        if (practice === '記号の基本練習３_英数') {
            // Get the current group's characters based on gIdx (from props, managed by usePracticeManagement)
            const currentKigoGroup = kigoPractice3Data[gIdx];
            if (!currentKigoGroup || !currentKigoGroup.chars) return [];

            const groupCharsToDisplay = currentKigoGroup.chars; // e.g., ['`', '?', '^', '。', '、'] or ['!', '$', '…', '#', '~']
            // currentTargetChar は kigoPractice3Data からの実際のターゲット記号 (例: '。', '`')
            const actualInternalTargetChar = isRandomMode ? randomTarget?.char : currentTargetChar;

            const charsToDisplayObjects = groupCharsToDisplay.map(internalCharInGroup => {
                let displayCharInHeader = internalCharInGroup;
                if (internalCharInGroup === '。') displayCharInHeader = ',';
                else if (internalCharInGroup === '、') displayCharInHeader = '.';

                // isPracticeTarget should be true if the internalCharInGroup matches the actualInternalTargetChar
                const isTarget = internalCharInGroup === actualInternalTargetChar;

                return {
                    char: displayCharInHeader,
                    isPracticeTarget: isTarget
                };
            });

            if (charsToDisplayObjects.length <= 1) return charsToDisplayObjects;

            return charsToDisplayObjects.flatMap((item, index) =>
                index < charsToDisplayObjects.length - 1 ? [item, { char: '\u00A0', isPracticeTarget: false }] : [item]
            );
        } else { // かなモードの '記号の基本練習３' または practice が未定義の場合のフォールバック
            if (isRandomMode) {
                if (!randomTarget) return [];
                // かなモードでは kigoPractice3Data の文字をそのまま表示
                return [{ char: randomTarget.char, isPracticeTarget: true }];
            } else {
                if (!currentGroup?.chars) return [];
                const groupChars = currentGroup.chars; // kigoPractice3Data から

                const charsToDisplayObjects = groupChars.map((internalChar, index) => {
                    return {
                        char: internalChar, // kigoPractice3Data の文字をそのまま表示
                        isPracticeTarget: index === dIdx
                    };
                });

                if (charsToDisplayObjects.length <= 1) return charsToDisplayObjects;
                return charsToDisplayObjects.flatMap((item, index) =>
                    index < charsToDisplayObjects.length - 1 ? [item, { char: '\u00A0', isPracticeTarget: false }] : [item]
                );
            }
        }
    }, [isActive, practice, isRandomMode, randomTarget, currentGroup, dIdx, currentTargetChar, gIdx]);


    const selectNextRandomTarget = useCallback(() => {
        if (allKigo3CharInfos.length > 0) {
            const randomIndex = Math.floor(Math.random() * allKigo3CharInfos.length);
            const nextTarget = allKigo3CharInfos[randomIndex];
            setRandomTarget(nextTarget);
            setStage('kigoInput');
            isWaitingForSecondKigo.current = false;
            setShowHighlightAfterWait(true);
        } else {
            setRandomTarget(null);
        }
    }, [setRandomTarget, setStage]);

    const reset = useCallback(() => { // reset 関数
        setStage('kigoInput');
        isWaitingForSecondKigo.current = false;
        setShowHighlightAfterWait(true);
        if (highlightTimeoutRef.current !== null) {
            clearTimeout(highlightTimeoutRef.current);
            highlightTimeoutRef.current = null;
        }
        setRandomTarget(null);
        prevGIdxRef.current = -1;
        prevDIdxRef.current = -1;
        isInitialMount.current = true;
        prevIsRandomModeRef.current = false;
        setStatus('idle'); // reset 時は idle に
    }, [setStage, setRandomTarget]);

    useEffect(() => {
        if (!isActive && prevIsActiveRef.current) {
            reset();
        }

        if (isActive) {
            if (isActive && !prevIsActiveRef.current) {
                isInitialMount.current = true;
            }

            const indicesChanged = gIdx !== prevGIdxRef.current || dIdx !== prevDIdxRef.current;
            const randomModeChangedToTrue = isRandomMode && !prevIsRandomModeRef.current;
            const randomModeChangedToFalse = !isRandomMode && prevIsRandomModeRef.current;

            if (randomModeChangedToFalse || (!isRandomMode && (isInitialMount.current || indicesChanged))) {
                setStatus('waiting'); // 通常モード開始時は waiting に
                setStage('kigoInput');
                isWaitingForSecondKigo.current = false;
                setShowHighlightAfterWait(true);
                if (highlightTimeoutRef.current !== null) {
                    clearTimeout(highlightTimeoutRef.current);
                    highlightTimeoutRef.current = null;
                }
                setRandomTarget(null);
                prevGIdxRef.current = gIdx; // gIdx, dIdx の前回値を更新
                prevDIdxRef.current = dIdx;
                isInitialMount.current = false;
            } else if (isRandomMode && (randomModeChangedToTrue || isInitialMount.current || !randomTarget)) {
                 setStatus('waiting');
                 selectNextRandomTarget();
                 isInitialMount.current = false;
                 prevGIdxRef.current = gIdx;
                 prevDIdxRef.current = dIdx;
            }
        }
        prevIsActiveRef.current = isActive;
        prevIsRandomModeRef.current = isRandomMode;
        if (isActive) {
            prevGIdxRef.current = gIdx;
            prevDIdxRef.current = dIdx;
        }

        return () => {
            if (highlightTimeoutRef.current !== null) {
                clearTimeout(highlightTimeoutRef.current);
                highlightTimeoutRef.current = null;
            }
        };
    }, [isActive, isRandomMode, gIdx, dIdx, randomTarget, reset, selectNextRandomTarget]);

    const handleInput = useCallback((inputInfo: PracticeInputInfo): PracticeInputResult => {
        if (inputInfo.type !== 'release') return { isExpected: true, shouldGoToNext: undefined };

        const { pressCode, layerIndex } = inputInfo;
        console.log(`[Kigo3SM handleInput] Received release input: pressCode=${pressCode}, layerIndex=${layerIndex}, stage: ${stage}`);

        let isExpected = false;
        let shouldGoToNext_final: boolean | undefined = undefined;
        let nextStage: KigoPractice3Stage = stage;
        const layer8 = layers[8] ?? [];

        const expectedEqualKigoPressCode = layer8.findIndex(key => key.trim() === EQUAL_KIGO_KEY_NAME.trim());

        if (stage === 'kigoInput') {
            if (layerIndex === 8 && pressCode === expectedEqualKigoPressCode && expectedEqualKigoPressCode !== -1) {
                isExpected = true;
                if (isTargetEqualSign) {
                    nextStage = 'waitingForSecondKigo';
                    isWaitingForSecondKigo.current = true;
                    if (highlightTimeoutRef.current !== null) clearTimeout(highlightTimeoutRef.current);
                    setShowHighlightAfterWait(false); // 1打目入力後、ハイライトを一旦非表示にする
                    highlightTimeoutRef.current = window.setTimeout(() => { setShowHighlightAfterWait(true); highlightTimeoutRef.current = null; }, 300);
                    shouldGoToNext_final = undefined;
                } else {
                    nextStage = 'gyouInput';
                    isWaitingForSecondKigo.current = false;
                    setShowHighlightAfterWait(false); // 遅延ハイライトは不要
                    setStatus('waiting');
                    shouldGoToNext_final = undefined;
                }
            } else {
                setStatus('incorrect'); isExpected = false; nextStage = 'kigoInput'; shouldGoToNext_final = false;
            }
        } else if (stage === 'gyouInput') {
            if (layerIndex === 8) {
                const originalInputKeyLabel = layer8[pressCode]?.trim();
            const normalizedExpectedGyouKey = toHalfWidth(expectedGyouKey ?? '').trim(); // e.g., "は行"
            const normalizedCurrentTargetChar = toHalfWidth(currentTargetChar ?? '').trim(); // e.g., "!"
                let isInputCorrect = false;

                if (originalInputKeyLabel) {
                const isKanaGyouExpected = gyouList.includes(normalizedExpectedGyouKey);
                const normalizedOriginalInputKey = toHalfWidth(originalInputKeyLabel).trim(); // e.g., "!"

                if (isKanaGyouExpected) {
                    // If expectedGyouKey is a Kana Gyou (e.g., "は行" for targetChar "!"),
                    // then the input is correct if the pressed key (normalized) matches the targetChar (normalized).
                    if (normalizedCurrentTargetChar === '…' && normalizedOriginalInputKey === '...') {
                        isInputCorrect = true;
                    } else {
                        isInputCorrect = normalizedOriginalInputKey === normalizedCurrentTargetChar;
                    }
                    } else {
                    // Otherwise, it's a direct symbol gyouKey (like '`', '?', '^', ',', '.')
                    if (normalizedExpectedGyouKey === ',') {
                        isInputCorrect = originalInputKeyLabel === '。'; // Compare original for these specific cases
                    } else if (normalizedExpectedGyouKey === '.') {
                        isInputCorrect = originalInputKeyLabel === '、'; // Compare original
                    } else {
                        isInputCorrect = normalizedOriginalInputKey === normalizedExpectedGyouKey;
                    }
                    }
                }
            console.log(`[Kigo3SM handleInput] Stage gyouInput. originalInputKeyLabel: "${originalInputKeyLabel}", expectedGyouKey: "${expectedGyouKey}", normalizedExpectedGyouKey: "${normalizedExpectedGyouKey}", currentTargetChar: "${currentTargetChar}", isInputCorrect: ${isInputCorrect}`);
            if (isInputCorrect) { // Use the updated isInputCorrect
                    setStatus('correct'); isExpected = true; nextStage = 'kigoInput';
                    if (isRandomMode) {
                        selectNextRandomTarget();
                        shouldGoToNext_final = undefined; // Hook manages its own state
                    } else {
                        const currentGroupChars = currentGroup?.chars ?? [];
                        shouldGoToNext_final = (dIdx >= currentGroupChars.length - 1); // true if last char in group
                    }
                    isWaitingForSecondKigo.current = false; setShowHighlightAfterWait(false);
                } else {
                    setStatus('incorrect'); isExpected = false; nextStage = 'kigoInput'; shouldGoToNext_final = false;
                    isWaitingForSecondKigo.current = false; setShowHighlightAfterWait(false);
                }
            } else {
                setStatus('incorrect'); isExpected = false; nextStage = 'kigoInput'; shouldGoToNext_final = false;
                isWaitingForSecondKigo.current = false; setShowHighlightAfterWait(false);
            }
        } else if (stage === 'waitingForSecondKigo') {
            if (layerIndex === 8 && pressCode === expectedEqualKigoPressCode && expectedEqualKigoPressCode !== -1) {
                setStatus('correct'); isExpected = true; nextStage = 'kigoInput';
                isWaitingForSecondKigo.current = false;
                setShowHighlightAfterWait(false); // 遅延ハイライトを解除
                if (isRandomMode) {
                    selectNextRandomTarget();
                    shouldGoToNext_final = undefined; // Hook manages its own state
                } else {
                    const currentGroupChars = currentGroup?.chars ?? [];
                    shouldGoToNext_final = (dIdx >= currentGroupChars.length - 1); // true if last char in group
                }
            } else {
                setStatus('incorrect'); isExpected = false; nextStage = 'kigoInput';
                isWaitingForSecondKigo.current = false; setShowHighlightAfterWait(false); shouldGoToNext_final = false;
            }
        }

        if (nextStage !== stage) {
            setStage(nextStage);
            console.log(`[Kigo3SM handleInput] Stage changed from ${stage} to ${nextStage}`);
        }

        console.log(`[Kigo3SM handleInput] Returning: isExpected=${isExpected}, shouldGoToNext=${shouldGoToNext_final}`);
        return { isExpected, shouldGoToNext: shouldGoToNext_final, errorLayer: isExpected ? null : 8};

    }, [
        stage, layers, isTargetEqualSign, expectedGyouKey, currentTargetChar, isRandomMode,
        selectNextRandomTarget, setStage, setShowHighlightAfterWait, gIdx, dIdx, setStatus, // setStatus を追加
        isWaitingForSecondKigo, highlightTimeoutRef, currentGroup // isWaitingForSecondKigo, highlightTimeoutRef, currentGroup を追加
    ]);

    const getHighlightClassName = useCallback((keyName: string, layoutIndex: number, keyIndex: number): PracticeHighlightResult => {
        const currentPracticeStatus = status;
        const originalKey = (keyName ?? '').trim();
        console.log(`[Kigo3SM getHighlightClassName] key: "${originalKey}", layoutIndex: ${layoutIndex}, keyIndex: ${keyIndex}, stage: ${stage}, status: ${currentPracticeStatus}, isWaitingForSecondKigo: ${isWaitingForSecondKigo.current}, showHighlightAfterWait: ${showHighlightAfterWait}`);
        const layer8 = layers[8] ?? [];

        if (currentPracticeStatus === 'correct') {
            return { className: null, overrideKey: originalKey };
        }

        // ステージとターゲットに基づいて基本のハイライトクラスを決定
        let baseClassName: string | null = null;
        const expectedEqualKigoKeyIndex = layer8.findIndex(k => k.trim() === EQUAL_KIGO_KEY_NAME.trim());
        const normalizedKeyName = toHalfWidth(originalKey).trim(); // キー名を正規化

        if (layoutIndex === 8) { // レイヤー8のみ練習ハイライトの対象
            if (stage === 'kigoInput') {
            if (layoutIndex === 8 && keyIndex === expectedEqualKigoKeyIndex && expectedEqualKigoKeyIndex !== -1) {
                console.log(`[Kigo3SM getHighlightClassName] Stage kigoInput. Highlighting ${originalKey} (keyIndex ${keyIndex})`);
                    baseClassName = 'bg-blue-100 dark:bg-blue-800';
            }
        } else if (stage === 'gyouInput') { // このステージのハイライトにも fontSizeClass を適用
            // Normalize the key label from the layer before comparing with the normalized expectedGyouKey
        const keyIndexForTargetGyouCorrected = expectedGyouKey ? layer8.findIndex(originalLayerKey => { // expectedGyouKey は currentTarget.gyouKey
                const trimmedOriginalKey = (originalLayerKey ?? '').trim();
            const normalizedExpectedGyou = toHalfWidth(expectedGyouKey).trim(); // 例: "は行" or "`"

            // Check if expectedGyouKey is a Kana Gyou (e.g., "あ行", "か行", etc.)
            const isKanaGyouExpected = gyouList.includes(normalizedExpectedGyou);

            if (isKanaGyouExpected) {
                // If expectedGyouKey is a Kana Gyou (e.g., "は行" for targetChar "!"),
                // then we should highlight the actual targetChar (e.g., "!") on layer 8.
                const normalizedTargetChar = toHalfWidth(currentTargetChar ?? '').trim(); // currentTargetChar is from activePractice.targetChar
                const normalizedLayerKey = toHalfWidth(trimmedOriginalKey).trim();
                if (normalizedTargetChar === '…' && normalizedLayerKey === '...') { // Special case for ellipsis
                    return true;
                }
                return normalizedLayerKey === normalizedTargetChar;
                } else {
                // Otherwise, it's a direct symbol gyouKey (like '`', '?', '^', ',', '.')
                if (normalizedExpectedGyou === ',') {
                    return trimmedOriginalKey === '。';
                } else if (normalizedExpectedGyou === '.') {
                    return trimmedOriginalKey === '、';
                } else {
                    return toHalfWidth(trimmedOriginalKey).trim() === normalizedExpectedGyou;
                }
                }
            }) : -1;

            console.log(`[Kigo3SM getHighlightClassName] Stage gyouInput. targetGyouKey: "${expectedGyouKey}"`);
            console.log(`[Kigo3SM getHighlightClassName]   Normalized findIndex result: ${keyIndexForTargetGyouCorrected}`);
            if (expectedGyouKey) {
                console.log(`[Kigo3SM getHighlightClassName]   Comparing normalized layer keys with normalized expectedGyouKey: "${toHalfWidth(expectedGyouKey ?? '').trim()}"`);
            }
            if (layoutIndex === 8 && keyIndex === keyIndexForTargetGyouCorrected && keyIndexForTargetGyouCorrected !== -1) {
                baseClassName = 'bg-blue-100 dark:bg-blue-800';
            }
        } else if (stage === 'waitingForSecondKigo') {
            if (layoutIndex === 8 && keyIndex === expectedEqualKigoKeyIndex && expectedEqualKigoKeyIndex !== -1) {
                console.log(`[Kigo3SM getHighlightClassName] Stage waitingForSecondKigo. Highlighting ${originalKey} (keyIndex ${keyIndex}) with delay.`);
                // 遅延ハイライトの場合も背景色を使用
                baseClassName = showHighlightAfterWait ? 'bg-blue-100 dark:bg-blue-800' : null;
            }
        }

        // Default highlight for layer 8 keys that are not the target
        // Add a check to ensure we are not highlighting the error key if status is incorrect
        if (layoutIndex === 8 && status !== 'incorrect') {
             // 現在のステージのターゲットキーでない場合にデフォルトのテキスト色を適用
             const expectedEqualKigoKeyIndex = layer8.findIndex(k => k.trim() === EQUAL_KIGO_KEY_NAME.trim());
             const keyIndexForTargetGyou = expectedGyouKey ? layer8.findIndex(k => k.trim() === expectedGyouKey.trim()) : -1;
             const isCurrentTargetKey = (stage === 'kigoInput' || stage === 'waitingForSecondKigo') && keyIndex === expectedEqualKigoKeyIndex;
             const isGyouTargetKey = stage === 'gyouInput' && keyIndex === keyIndexForTargetGyou;
             // baseClassName が設定されていない（つまり特定のターゲットハイライトに一致しない）場合のみデフォルトテキスト色を適用
             if (baseClassName === null && !isCurrentTargetKey && !isGyouTargetKey) {
                 baseClassName = 'text-gray-700 dark:text-gray-300';
             }
        }
    }

        return { className: baseClassName ? baseClassName.trim() : null, overrideKey: originalKey };
    }, [
        status, stage, layers, currentTargetChar, expectedGyouKey,
        isWaitingForSecondKigo, showHighlightAfterWait, gIdx, dIdx
    ]);

    const isInvalidInputTarget = useCallback((pressCode: number, layoutIndex: number, keyIndex: number): boolean => {
        if (!isActive || status !== 'incorrect') return false;
        return layoutIndex === 8 && keyIndex === pressCode;
    }, [isActive, status]);

    const clearErrorState = useCallback(() => {
        setStatus('waiting');
    }, [setStatus]);

    // 表示レイヤーのカスタマイズ
    const customizedDisplayLayers = useMemo(() => {
        // 英数記号練習モードの場合のみ、レイヤー8の表示をカスタマイズ
        if (practice === '記号の基本練習３_英数') {
            if (!layers || layers.length <= 8) return layers;
            return layers.map((layer, index) => {
                if (index === 8) { // レイヤー8のみ対象
                    return layer.map(key => {
                        const trimmedKey = key.trim();
                        if (trimmedKey === '。') return ','; // 「。」を「,」に変換して表示
                        if (trimmedKey === '、') return '.'; // 「、」を「.」に変換して表示
                        return key;
                    });
                }
                return layer;
            });
        }
        // かな記号練習モード、または practice が未定義の場合は元のレイヤーをそのまま使用
        return layers;
    }, [layers, practice]);

    return {
        headingChars,
        handleInput,
        getHighlightClassName,
        reset,
        isInvalidInputTarget,
        status,
        targetChar: currentTargetChar ?? undefined, // App.tsx に渡す現在のターゲット文字
        currentTarget: currentInputDef ? { type: 'kigo3', char: currentInputDef.char, isEqualSign: !currentInputDef.gyouKey, gyouKey: currentInputDef.gyouKey } : undefined,
        displayLayers: customizedDisplayLayers, // Use customized layers
        targetLayerIndices: [8], // 記号の基本練習3はレイヤー8のみ表示
        targetLayerIndex: 8, // targetLayerIndex も追加
        clearErrorState,
        stage, // stage を返す
    };
};

export default useKigoPractice3SymbolMenu;
