// /home/coffee/my-keymap-viewer/src/hooks/useYouonKakuchoPractice.ts
import { useState, useEffect, useCallback, useMemo, useRef } from 'react';
import {
    PracticeHookProps, PracticeHookResult, PracticeInputInfo, PracticeInputResult,
    getHidKeyCodes, hid2Youon,
    PracticeHighlightResult,
    PracticeStage,
    PracticeStatus, // Import PracticeStatus
    hid2GyouHRight_Kana,
    hid2GyouHLeft_Kana,
    hid2DanHRight_Kana,
    hid2DanHLeft_Kana,
    hid2GyouVRight_Kana,
    hid2GyouVLeft_Kana,
    hid2DanVRight_Kana,
    hid2DanVLeft_Kana,
} from './usePracticeCommons';
import {
    youonGyouList,
    youonKakuchoChars,
    youonKakuchoPracticeData,
    YouonKakuchoInputDef,
    functionKeyMaps,
    YouonKakuchoPracticeGroup, // YouonKakuchoPracticeGroup をインポート
} from '../data/keymapData';

export interface YouonKakuchoHeadingCharInfo {
    char: string;
    isPracticeTarget: boolean;
}

const useYouonKakuchoPractice = ({
    gIdx, dIdx, isActive, side, layers, kb, isRandomMode, onAdvance
}: PracticeHookProps): PracticeHookResult => {
    const [stage, setStage] = useState<PracticeStage>('line');
    const [randomTarget, setRandomTarget] = useState<{ g: number; d: number; char: string } | null>(null); // Added char to randomTarget
    const prevGIdxRef = useRef(gIdx);
    const prevDIdxRef = useRef(dIdx);
    const isInitialMount = useRef(true);
    const prevIsActiveRef = useRef(isActive);
    const prevIsRandomModeRef = useRef(isRandomMode);
    // const [isSpecialRowHighlightActive, setIsSpecialRowHighlightActive] = useState(false); // 削除
    // const specialHighlightTimerRef = useRef<NodeJS.Timeout | null>(null); // 削除
    const [currentErrorHighlightLayer, setCurrentErrorHighlightLayer] = useState<number | null>(null);

    const hid2GyouMap = useMemo(() => {
        if (kb === 'tw-20v') {
            return side === 'left' ? hid2GyouVLeft_Kana : hid2GyouVRight_Kana;
        } else { // TW-20H
            return side === 'left' ? hid2GyouHLeft_Kana : hid2GyouHRight_Kana;
        }
    }, [kb, side]);

    const hid2DanMap = useMemo(() => {
        if (kb === 'tw-20v') {
            return side === 'left' ? hid2DanVLeft_Kana : hid2DanVRight_Kana;
        } else { // TW-20H
            return side === 'left' ? hid2DanHLeft_Kana : hid2DanHRight_Kana;
        }
    }, [kb, side]);

    const currentGIdx = isRandomMode ? (randomTarget?.g ?? 0) : gIdx;
    const currentDIdx = isRandomMode ? (randomTarget?.d ?? 0) : dIdx;

    const currentGyouKey = useMemo(() => {
        const practiceGroup = youonKakuchoPracticeData[currentGIdx];
        if (practiceGroup) {
            // Fallback to finding a valid input definition's gyouKey
            const validDef = practiceGroup.inputs.find(def => def !== null);
            if (validDef) {
                return validDef.gyouKey;
            }
        }
        // Further fallback if no gyouKey can be determined from the group or its inputs
        return youonGyouList[currentGIdx] ?? youonGyouList[0];
    }, [currentGIdx]);


    const headingChars = useMemo((): YouonKakuchoHeadingCharInfo[] => {
        if (!isActive) return [];
        console.log(`[YouonKakucho headingChars CALC START] isActive: ${isActive}, isRandomMode: ${isRandomMode}, gIdx(prop): ${gIdx}, dIdx(prop): ${dIdx}, randomTarget: ${JSON.stringify(randomTarget)}`);

        const currentGIdxForHeading = isRandomMode ? (randomTarget?.g ?? 0) : gIdx;
        console.log(`  [YouonKakucho headingChars CALC] currentGIdxForHeading: ${currentGIdxForHeading}`);

        if (isRandomMode) {
            if (!randomTarget) {
                console.log("  [YouonKakucho headingChars CALC Random] randomTarget is null, returning empty.");
                return [];
            }
            const randomPracticeGroup = youonKakuchoPracticeData[currentGIdxForHeading];
            if (!randomPracticeGroup) {
                console.warn(`  [YouonKakucho headingChars CALC Random] randomPracticeGroup is undefined for gIdx: ${currentGIdxForHeading}`);
                return [];
            }

            // Get gyouKey from the randomly selected input definition within the group
            const randomInputDef = randomPracticeGroup.inputs[randomTarget.d];
            if (!randomInputDef) {
                console.warn(`  [YouonKakucho headingChars CALC Random] randomInputDef is null for gIdx: ${currentGIdxForHeading}, dIdx: ${randomTarget.d}`);
                return [];
            }
            const displayGyouKey = randomInputDef.gyouKey;
            console.log(`  [YouonKakucho headingChars CALC Random] displayGyouKey from inputDef: "${displayGyouKey}"`);
            
            const displayStringArray = (displayGyouKey && youonKakuchoChars[displayGyouKey]) ? youonKakuchoChars[displayGyouKey] : undefined;
            if (!displayStringArray || displayStringArray.length === 0) {
                console.warn(`  [YouonKakucho headingChars CALC Random] No displayStringArray for displayGyouKey: "${displayGyouKey}"`);
                return []; 
            }

            const displayString = displayStringArray[0] ?? "";
            if (displayString === "") {
                console.warn(`  [YouonKakucho headingChars CALC Random] displayString is empty for displayGyouKey: "${displayGyouKey}"`);
                return [];
            }
            console.log(`  [YouonKakucho headingChars CALC Random] displayString: "${displayString}"`);

            const displayableChars = displayString.split('');
            const targetCharForHighlight = randomTarget?.char; // Use char from randomTarget state

            const result = displayableChars.map((char: string, index: number) => {
                let isTarget = false;
                if (targetCharForHighlight && targetCharForHighlight.length === 2) {
                    if (index % 2 === 0 && index + 1 < displayableChars.length) { // ペアの最初の文字
                        if (char === targetCharForHighlight[0] && displayableChars[index + 1] === targetCharForHighlight[1]) {
                            isTarget = true;
                        }
                    } else if (index % 2 !== 0 && index > 0) { // ペアの2番目の文字
                        if (char === targetCharForHighlight[1] && displayableChars[index - 1] === targetCharForHighlight[0]) {
                            isTarget = true;
                        }
                    }
                }
                return {
                    char: char,
                    isPracticeTarget: isTarget,
                };
            });
            console.log("  [YouonKakucho headingChars CALC Random] Result (target chars):", JSON.stringify(result.filter(item => item.isPracticeTarget).map(item => item.char)));
            return result;
        }
        // 通常モード
        const currentGroupData = youonKakuchoPracticeData[currentGIdxForHeading];
        if (!currentGroupData) {
            return [];
        }

        // Derive gyouKey from inputs, then fallback
        const gyouKeyFromInputs = currentGroupData.inputs.find(def => def !== null)?.gyouKey;
        const gyouKeyForHeading = gyouKeyFromInputs || youonGyouList[currentGIdxForHeading] || youonGyouList[0];
        
        const charsArrayForHeading = youonKakuchoChars[gyouKeyForHeading]; 
        if (!charsArrayForHeading || charsArrayForHeading.length === 0) {
            return [];
        }
        
        const fullDisplayString = charsArrayForHeading[0]; 
        const displayableChars = fullDisplayString.split(''); 
        const conceptualInputs = currentGroupData.inputs;

        const result = displayableChars.map((char: string, index: number) => {
            const conceptualInputIndex = Math.floor(index / 2);
            // Highlight based on the dIdx prop for normal mode
            const isTarget = conceptualInputs?.[conceptualInputIndex] !== null && conceptualInputs?.[conceptualInputIndex] !== undefined && conceptualInputIndex === dIdx;
            return {
                char: char,
                isPracticeTarget: isTarget,
            };
        });
        return result;
    }, [isActive, isRandomMode, gIdx, dIdx, randomTarget]);

    const currentInputDef = useMemo((): YouonKakuchoInputDef | null => {
        if (isRandomMode) {
            if (randomTarget) {
                return youonKakuchoPracticeData[randomTarget.g]?.inputs[randomTarget.d] ?? null;
            }
            return null;
        }
        // Ensure gIdx and dIdx are within bounds for normal mode
        if (currentGIdx < 0 || currentGIdx >= youonKakuchoPracticeData.length || currentDIdx < 0) return null;
        const group = youonKakuchoPracticeData[currentGIdx];
        if (!group || currentDIdx >= group.inputs.length) return null;
        return youonKakuchoPracticeData[currentGIdx]?.inputs[currentDIdx] ?? null;
    }, [isRandomMode, randomTarget, currentGIdx, currentDIdx]);
    
    const targetChar = useMemo(() => {
        return currentInputDef?.char ?? '';
    }, [currentInputDef]);

    const targetDan = useMemo(() => {
        return currentInputDef?.dan ?? '';
    }, [currentInputDef]);

    const generateRandomTarget = useCallback(() => {
        const randomG = Math.floor(Math.random() * youonKakuchoPracticeData.length);
        const inputsInGroup = youonKakuchoPracticeData[randomG]?.inputs;
        if (!inputsInGroup || inputsInGroup.length === 0) {
            setRandomTarget(null);
            return;
        }
        const validPracticeIndices: number[] = [];
        inputsInGroup.forEach((inputDef, index) => {
            if (inputDef !== null) {
                validPracticeIndices.push(index);
            }
        });

        if (validPracticeIndices.length === 0) {
            setRandomTarget(null);
            return;
        }
        const randomTargetD = validPracticeIndices[Math.floor(Math.random() * validPracticeIndices.length)];
        const targetChar = youonKakuchoPracticeData[randomG]?.inputs[randomTargetD]?.char;
        if (!targetChar) {
            console.error(`[YouonKakucho generateRandomTarget] Could not find char for g:${randomG}, d:${randomTargetD}`);
            setRandomTarget(null); return;
        }
        setRandomTarget({ g: randomG, d: randomTargetD, char: targetChar });
        setStage('line');
        setCurrentErrorHighlightLayer(null);
        // setIsSpecialRowHighlightActive(false); // 削除済みのはずが残っていた箇所
    }, []);

    const selectNextRandomTarget = useCallback(() => {
        generateRandomTarget();
    }, [generateRandomTarget]);

    const currentFunctionKeyMap = useMemo(() => functionKeyMaps[kb]?.[side] ?? {}, [kb, side]);

    const reset = useCallback(() => {
        setStage('line');
        setRandomTarget(null);
        prevGIdxRef.current = -1;
        prevDIdxRef.current = -1;
        isInitialMount.current = true;
        prevIsRandomModeRef.current = false;
        setCurrentErrorHighlightLayer(null);
        // setIsSpecialRowHighlightActive(false); // 削除
        // if (specialHighlightTimerRef.current) {
        //     clearTimeout(specialHighlightTimerRef.current);
        //     specialHighlightTimerRef.current = null;
        // }
    }, []);

    useEffect(() => {
        if (!isActive && prevIsActiveRef.current) {
            reset();
        }
        if (isActive) {
            const justActivated = isActive && !prevIsActiveRef.current;
            const indicesChanged = gIdx !== prevGIdxRef.current || dIdx !== prevDIdxRef.current;
            const randomModeChanged = isRandomMode !== prevIsRandomModeRef.current;

            if (justActivated) {
                isInitialMount.current = true;
            }

            if (randomModeChanged) {
                console.log(`[YouonKakucho useEffect] Random mode changed from ${prevIsRandomModeRef.current} to ${isRandomMode}. Calling reset.`);
                reset(); // This will clear randomTarget and set stage to 'line'
                if (isRandomMode) {
                    console.log(`[YouonKakucho useEffect] Switched to random mode. Selecting new random target.`);
                    selectNextRandomTarget();
                } else {
                    // Switched to normal mode. currentInputDef will be re-calculated by its useMemo.
                    // Ensure stage and error states are reset for normal mode.
                    setStage('line');
                    setCurrentErrorHighlightLayer(null);
                    // setIsSpecialRowHighlightActive(false); // 削除
                }
            } else if (justActivated || (!isRandomMode && indicesChanged)) {
                // Normal mode: just activated or indices changed
                setStage('line');
                setCurrentErrorHighlightLayer(null);
                // setIsSpecialRowHighlightActive(false); // 削除
            } else if (isRandomMode && (isInitialMount.current || !randomTarget)) {
                 // Random mode: just activated or no target yet
                 selectNextRandomTarget(); 
                 setCurrentErrorHighlightLayer(null);
                 // setIsSpecialRowHighlightActive(false); // 削除
            }
            isInitialMount.current = false; // Clear initial mount flag after first active processing
        }
        prevIsActiveRef.current = isActive;
        prevIsRandomModeRef.current = isRandomMode;
        prevGIdxRef.current = gIdx;
        prevDIdxRef.current = dIdx;
        // return () => { // 削除済みのはずが残っていた箇所
        //     if (specialHighlightTimerRef.current) {
        //         clearTimeout(specialHighlightTimerRef.current);
        //     }
        // };
    }, [isActive, isRandomMode, gIdx, dIdx, randomTarget, reset, selectNextRandomTarget]);

    const handleInput = useCallback((info: PracticeInputInfo): PracticeInputResult => {
        if (info.type !== 'release' || !isActive || !currentInputDef) {
            return { isExpected: false, shouldGoToNext: undefined, errorLayer: currentErrorHighlightLayer };
        }

        const pressCode = info.pressCode;
        let isExpected = false;
        let shouldGoToNext_final: boolean | undefined = undefined;
        let nextStageInternal: PracticeStage = stage;
        let errorLayer: number | null = null;

        const youonKeyCodeEntry = Object.entries(currentFunctionKeyMap).find(([_, name]) => name === 'ゃゅょ');
        const expectedYouonKeyCode = youonKeyCodeEntry ? parseInt(youonKeyCodeEntry[0]) + 1 : -1;

        if (expectedYouonKeyCode === -1) {
            console.error("useYouonKakuchoPractice: Could not find key code for 'ゃゅょ'.");
            return { isExpected: false, shouldGoToNext: undefined, errorLayer: null };
        }

        switch (stage) {
            case 'line':
                const expectedGyouKeyCodes = Object.entries(hid2GyouMap)
                    .filter(([_, name]) => name === currentInputDef.gyouKey)
                    .map(([codeStr, _]) => parseInt(codeStr));
                if (expectedGyouKeyCodes.includes(pressCode + 1)) {
                    isExpected = true;
                    nextStageInternal = 'youon';
                    errorLayer = null;
                } else {
                    errorLayer = 2;
                }
                break;
            case 'youon':
                 if ((pressCode + 1) === expectedYouonKeyCode) {
                    isExpected = true;
                    nextStageInternal = 'dan';
                    errorLayer = null;
                } else {
                    nextStageInternal = 'line';
                    errorLayer = 2;
                }
                break;
            case 'dan':
                const expectedDanKeyCodes = Object.entries(hid2DanMap)
                    .filter(([_, name]) => name === currentInputDef.dan)
                    .map(([codeStr, _]) => parseInt(codeStr));
                nextStageInternal = 'line';
                if (expectedDanKeyCodes.includes(pressCode + 1)) {
                    isExpected = true;
                    errorLayer = null;
                    if (isRandomMode) {
                        selectNextRandomTarget();
                        shouldGoToNext_final = false;
                    } else {
                        if (currentDIdx === 3) {
                            shouldGoToNext_final = true;
                        } else if (currentDIdx === 1) {
                            onAdvance?.();
                            shouldGoToNext_final = undefined;
                        } else {
                            shouldGoToNext_final = true;
                        }
                        if (currentDIdx === 1 || currentDIdx === 3) {
                            // setIsSpecialRowHighlightActive(true); // 削除
                            // if (specialHighlightTimerRef.current) clearTimeout(specialHighlightTimerRef.current); // 削除
                            // specialHighlightTimerRef.current = setTimeout(() => { // 削除
                            //     setIsSpecialRowHighlightActive(false); // 削除
                            //     specialHighlightTimerRef.current = null; // 削除
                            // }, 500); // 削除
                        }
                    }
                } else {
                    errorLayer = 3;
                }
                break;
        }
        setCurrentErrorHighlightLayer(errorLayer);
        if (nextStageInternal !== stage) {
            setStage(nextStageInternal);
        } else if (!isExpected && stage !== 'line') {
             setStage('line');
        }
        return { isExpected, shouldGoToNext: shouldGoToNext_final, errorLayer };
    }, [
        isActive, stage, currentInputDef, hid2GyouMap, hid2DanMap, currentFunctionKeyMap,
        isRandomMode, setStage, selectNextRandomTarget, currentDIdx, onAdvance, currentErrorHighlightLayer // setIsSpecialRowHighlightActive を削除
    ]);

    const getHighlightClassName = useCallback((keyName: string, layoutIndex: number): PracticeHighlightResult => {
        const noHighlight: PracticeHighlightResult = { className: null, overrideKey: null };
        if (!isActive || (currentErrorHighlightLayer === layoutIndex)) {
            return noHighlight;
        }

        // Determine the dIdx to use for highlighting based on the mode
        const dIdxForHighlight = isRandomMode ? (randomTarget?.d ?? -1) : currentDIdx;

        // In normal mode, only highlight if dIdxForHighlight is 1 or 3 (for the specific keys in the sequence)
        // In random mode, this condition (dIdxForHighlight !== 1 && dIdxForHighlight !== 3) might also prevent highlighting if not handled carefully.
        if (!isRandomMode && (dIdxForHighlight !== 1 && dIdxForHighlight !== 3)) {
            // // For normal mode, if dIdx is 0 or 2 (which are null inputs), don't highlight anything specific for the sequence. // 削除
            // // However, the `isSpecialRowHighlightActive` might still apply. // 削除
            //  if (isSpecialRowHighlightActive && (layoutIndex === 2 || layoutIndex === 3)) { // 削除
            //     return { className: 'bg-yellow-200 dark:bg-yellow-700', overrideKey: null }; // 削除
            // } // 削除
            return noHighlight;
        }
        if (!currentInputDef) {
            //  if (isSpecialRowHighlightActive && (layoutIndex === 2 || layoutIndex === 3)) { // 削除
            //     return { className: 'bg-yellow-200 dark:bg-yellow-700', overrideKey: null }; // 削除
            // } // 削除
            return noHighlight;
        }

        const currentStageForHighlight = stage;
        let expectedKeyName: string | null = null;
        let targetLayoutIndex: number | null = null;
        const youonKeyEntry = Object.entries(currentFunctionKeyMap).find(([_, name]) => name === 'ゃゅょ');
        const youonDisplayName = youonKeyEntry ? currentFunctionKeyMap[parseInt(youonKeyEntry[0])] : 'ゃゅょ';

        switch (currentStageForHighlight) {
            case 'line':
                expectedKeyName = currentInputDef.gyouKey;
                targetLayoutIndex = 2;
                break;
            case 'youon':
                expectedKeyName = youonDisplayName;
                targetLayoutIndex = 2;
                break;
            case 'dan':
                expectedKeyName = currentInputDef.dan;
                targetLayoutIndex = 3;
                break;
        }
        if (expectedKeyName !== null && layoutIndex === targetLayoutIndex && keyName.trim() === expectedKeyName.trim()) {
            return { className: 'bg-blue-100', overrideKey: null };
        }
        // // Apply special row highlight if active, even if no specific key matches // 削除
        // if (isSpecialRowHighlightActive && (layoutIndex === 2 || layoutIndex === 3)) { // 削除
        //     return { className: 'bg-yellow-200 dark:bg-yellow-700', overrideKey: null }; // 削除
        // } // 削除
        return noHighlight;
    }, [
        isActive, stage, currentInputDef, currentFunctionKeyMap, isRandomMode, currentErrorHighlightLayer, currentDIdx, randomTarget // isSpecialRowHighlightActive を削除
    ]);

    const isInvalidInputTarget = useCallback((pressCode: number, layoutIndex: number, keyIndex: number): boolean => {
        return isActive && currentErrorHighlightLayer === layoutIndex && keyIndex === pressCode;
    }, [isActive, currentErrorHighlightLayer]);

    const clearErrorState = useCallback(() => {
        setCurrentErrorHighlightLayer(null);
    }, []);

    const currentTarget = useMemo(() => {
        if (!currentInputDef) return undefined;
        return {
            type: 'youonKakucho' as const,
            char: currentInputDef.char,
            inputDef: currentInputDef
        };
    }, [currentInputDef]);

    const status = useMemo((): PracticeStatus => {
        if (!isActive) return 'idle';
        // Based on the internal stage of the hook
        if (stage === 'line') return 'waiting';
        // if (stage === 'youon' || stage === 'dan') return 'running'; // Or more specific if needed
        return 'running'; // Default to running if active and not in 'line' stage
    }, [isActive, stage]);

    return {
        headingChars,
        handleInput,
            getHighlightClassName: (keyName, layoutIndex, keyIndex) => getHighlightClassName(keyName, layoutIndex), // Adapt signature
        reset,
        isInvalidInputTarget,
        currentTarget,
        errorHighlightLayer: currentErrorHighlightLayer,
        status, // Add status
        targetChar, // Add targetChar (already calculated)
        clearErrorState,
        targetLayerIndices: useMemo(() => {
            const layers = new Set([2, 3]);
            if (currentErrorHighlightLayer !== null) {
                layers.add(currentErrorHighlightLayer);
            }
            return Array.from(layers);
        }, [currentErrorHighlightLayer]),
        stage,
        // isSpecialRowHighlightActive, // 削除
    };
};

export default useYouonKakuchoPractice;
