  export const isSpecialSymbolLayer10 = (keycode: string): boolean => {
    // Note: The keymap data uses '"' and "'" directly, not HTML entities.
    const targetSymbols = [':', '"', "'", ';'];
    const trimmedKeycode = keycode.trim();
    const result = targetSymbols.includes(trimmedKeycode);
    console.log(`[styleUtils isSpecialSymbolLayer10] input keycode: "${keycode}", trimmedKeycode: "${trimmedKeycode}", targetSymbols: [${targetSymbols.join(', ')}], result: ${result}`);
    return result;
  };